import React from 'react'
import qrimage from '../../assets/png/image.png'
import './HomePage.css'
import { useNavigate } from 'react-router-dom'

const Traceablity = () => {
    const navigate = useNavigate();
  return (
    <div>
       <div className="content-container">
        <p className="detail-heading">Please Scan the part QR Code to start</p>
        <div className="barcode">
          <img src={qrimage} alt="qrscanner" width={160} />
        </div>
        <div className="sub-headings">
          <p>Or</p>
          <p className="detail-heading2">Enter Code printed on QR Code</p>
        </div>
        <input type="text" placeholder="" className="inputbar"/>
        <div className="buttons">
          <button>Clear All</button>
          <button  onClick={() => navigate("/product-details")}>Confirm</button>
        </div>
      </div>
    </div>
  )
}

export default Traceablity
