import React from "react";
import Modal from "../../../components/modal";
import Input from "../../../components/input";
import { Stack } from "@mui/material";
import Button from "../../../components/button";
import SchemaExtractor from "../../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../../helpers/constants'
import { Validate } from "../../../helpers/utils";
import crossIcon from "../../../assets/svg/cross-icon.svg";
import { t } from "i18next";
const OrderDetailsModal = ({
  open,
  onClose,
  data ={},
  setData,
  onSubmit,
  handleAdd2,
  loading,
  tab,
}) => {
  const capitalizeFirstLetter = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1);
  };

  const filteredData = Object.entries(data).filter(
    ([key, value]) => key !== "id" && value != null
  ); // Filter out 'id' and invalid values

  console.log("order details data", data);

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 py-4"
      width="81vw"
      hideCloseIcon={crossIcon}
      crossIconClasses={`w-2.5vw w-5vw--mob h-2.5vw h-5vw--mob`}
    >
      <div className="orderDetailsContainer">
        <div className="leftPart">
          <ul className="orderDetailKeyList">
            {filteredData.map(([key]) => (
              <li key={key}>{capitalizeFirstLetter(key)}</li>
            ))}
          </ul>
        </div>
        <div className="rightPart">
          <ul className="orderDetailValueList">
            {filteredData.map(([key, value]) => (
              <li key={key}>{value}</li>
            ))}
          </ul>
        </div>
      </div>
    </Modal>
  );
};

export default OrderDetailsModal;

