import React, { useRef, useState, useEffect } from "react";
import { t } from "i18next";
import Button from "../../../components/button"; // Adjust the import path as needed

const SupervisorLoginModal = ({ noStepFn, nextStepFn }) => {
  const [formData, setFormData] = useState({ username: "", password: "" });
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    nextStepFn(formData.username, formData.password);
  };
  return (
    <div className="action_modal__container">
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <div>
          <p>Supervisor Login is required to proceed further.</p>
          <p style={{ display: "flex", justifyContent: "center" }}>
            <strong>Please Login.</strong>
          </p>
        </div>
        <form
          onSubmit={handleSubmit}
          style={{
            gap: "2.5rem",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <div>
            <label htmlFor="username" style={{ marginRight: "0.5rem" }}>
              {t('username')}:
            </label>
            <input
              type="text"
              id="username"
              name="username"
              value={formData.username}
              onChange={handleChange}
              required
            />
          </div>
          <div>
            <label htmlFor="password" style={{ marginRight: "0.5rem" }}>
              {t('password')}:
            </label>
            <input
              type="password"
              id="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              required
            />
          </div>
          <div
            style={{
              gap: "2.5rem",
              display: "flex",
            }}
          >
            <Button
              onClick={noStepFn}
              white={true}
              width="8rem"
              height="2.5rem"
              bordered={true}
              customStyle={{
                border: "1px solid #3E3E3E",
                backgroundColor: "#FFFFFF",
              }}
            >
              {t('cancel')}
            </Button>
            <Button
              onClick={handleSubmit}
              color="green"
              width="8rem"
              height="2.5rem"
              disabled={!formData.username || !formData.password}
            >
              {t('submit')}
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default SupervisorLoginModal;
