
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import { styled, useTheme } from '@mui/material/styles';
import ReactApexChart from 'react-apexcharts';
import { Box, Typography,Stack } from '@mui/material';

// import { fNumber } from 'src/utils/format-number';

export default function PieChart({ title, chartColors, chart, ...other }) {

  const { series, options } = chart;

  const chartSeries = series.map((i) => i.value);

  const pieOptions = {
    chart: {
      width: 150,
      type: 'pie',
      animations: {
        enabled: false // Disable animation
      }
    },
    dataLabels: {
      enabled: false, // Disable data labels
    },
    legend: {
      show: false, // Disable legend
    },
    tooltip: {
      enabled: false, // Disable tooltip on hover
    },
    colors: chartColors, 
    // labels: ['Team A', 'Team B', 'Team C', 'Team D', 'Team E'],
    responsive: [{
      breakpoint: 480,
      options: {
        chart: {
          // width: 200
        },
        legend: {
          position: 'bottom'
        }
      }
    }]
  }
  
  return (
      <Stack sx={{}} dir="ltr">
        <ReactApexChart type="pie" series={chartSeries} options={pieOptions} />
      </Stack>
  );
}
