import React, { useEffect, useState,useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../../components/table";
// import useDivision from "../../store/division";
import Button from "../../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete, Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../../components/UI/Category";
import { COLORS } from "../../../helpers/constants";
import AllMatScan from "./AllMatScan"
import MachineData from "./MachineData";
import Downtime from "./Downtime"
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import JsonFormatter from "../../../components/UI/JsonFormatter";
import { t } from "i18next";
import useJob from "../../../store/operatorPortal/job";

const MaterialScan = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const locationData = location.state
  const{OrderMaterialList,create,updateOrderStatus,getMachineStatus} = useJob()
  const [tableData, setTableData] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });

  const [payload , setPayload] = useState()
  const [machineId, setMachineId] = useState(localStorage.getItem('machineId') || "");

  const[runningStatus, setRunningStatus] = useState(false)


  // const { list, create, update, remove } = useDivision();

  useEffect(() => {
    console.log("workOrderTask--scan---",locationData)
    fetchMaterials();
  }, [page]);

    // useEffect(() => {
    //   if (scannedData) {
    //     handleBarcodeScannerModalSubmit(scannedData);
    //   }
    // }, [scannedData]);

  const fetchMaterials = async () => {
    setLoading("fetch");
    const [_list, _count] = await OrderMaterialList(locationData.id,page + 1);
    console.log("workOrderTask----_list-",_list)
    setTableData(_list?.items);
    // setTotal(_count);
    setLoading(false);
  };

  const SubmitScanned = async () => {
    setModalOpen('detail')
    // setLoading("fetch");
    let res =await create(locationData.id,payload);
    
    if(res && res.message==='OK'){
      let req = {"status":"RUNNING"}
      let res = await updateOrderStatus(locationData.id,req)
      console.log("updateOrderStatus respose----",res)
      setRunningStatus(true)
      setTimeout(() => {
        navigate("/machine-data")
        
      }, 2000);
      
    }
    // setModalOpen(false);
    // await fetchMaterials();
    // setLoading(false);
    // setEdit(false)
  };


  const handleAdd = async () => {
    const payload = { ...data }
    setLoading("fetch");
    let res =await create(payload, locationData.id);
    console.log("handle add respose----",res)
    setModalOpen(false);
    // await fetchMaterials();
    setLoading(false);
    // setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    // await remove(id);
    await fetchMaterials();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    // await update(id, payload);
    await fetchMaterials();
    setLoading(false);
  };

  const LABELS = [
    {
      id: 1,
      title: "Material Code",
      render: (rowData) => rowData?.material_name,
    },
    // {
    //   id: 2,
    //   title: "Material Type",
    //   render: (rowData) => rowData?.material_type ||'-',
    // },
    {
      id: 3,
      title: "Description",
      render: (rowData) => rowData?.material_description || '-'
    },
    // {
    //   id: 4,
    //   title: "Qty",
    //   render: (rowData) => rowData?.total_quantity || '0'
    // },
    // {
    //   id: 4,
    //   title: "Scanned Qty",
    //   render: (rowData) => rowData?.total_quantity || '0'
    // },
    // {
    //   id: 5,
    //   title: "Actions",
    //   render: (rowData) => 
    // //   <Button color={'light_green'} onClick={()=>navigate('/job-scan',{state:rowData})}>
    // //   <Typography color={'black'}>
    // //     Scan
    // //   </Typography>  
    // // </Button>
    //   <Button color={'LG_PINK'} onClick={()=>setModalOpen("start")}>
    //     <Typography color={'black'}>
    //       Report Issue
    //     </Typography>  
    //   </Button>
    // },

    {
      id: 6,
      title: "Status",
      render: (rowData) =>  {
        if (!payload || !payload[rowData.material_name]) {
          return "-"; // Default status if payload is not yet available
        }
    
        const totalScanned = payload[rowData.material_name].reduce(
          (sum, item) => sum + (item.quantity || 0),
          0
        );
    
        return totalScanned === rowData.quantity ? <CheckCircleOutlineIcon fontSize="large" sx={{color:COLORS.LIGHT_GREEN}}/> : "-";
      }
    }
  ];

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      handleScan(e.target.value);
      e.target.value = ""; // Clear the input for the next scan
    }
  };
  const handleScan = (data) => {
    handleBarcodeScannerModalSubmit(data);
  };

  const handleBarcodeScannerModalSubmit = (inputData) => {
    const [material_name, quantityString, unit, grnNumber, grnDate] = inputData.split("#");
    // console.log("scan item", material_name, quantityString, unit, grnNumber, grnDate);
  
    const quantity = parseFloat(quantityString);
    if (isNaN(quantity)) {
      console.log("Invalid quantity");
      return;
    }
  
    const currentMaterial = tableData.find(
      (item) => item.material_name === material_name
    );
    // console.log("Material found in table data");
    if (!currentMaterial) {
      toast.warn("Material not found in data");
      return;
    }
  
    setPayload((prevPayload) => {
      const updatedPayload = { ...prevPayload };
      // Calculate the total existing quantity in payload for this material
      const existingQuantity = updatedPayload[material_name]?.reduce(
        (sum, item) => sum + item.quantity,
        0
      ) || 0;

      // Check if adding the new quantity exceeds the allowed quantity
      // if (quantity > currentMaterial.quantity) {
      //   toast.warn(`Cannot add.${material_name} Quantity is more then require.`);
      //   return prevPayload; // Do not update if limit is exceeded
      // }
      if (existingQuantity + quantity > currentMaterial.quantity) {
        toast.warn(`Cannot add more. Max quantity for ${material_name} reached.`);
        return prevPayload; // Do not update if limit is exceeded
      }
  
      if (!updatedPayload[material_name]) {
        updatedPayload[material_name] = [];
      }
  
      updatedPayload[material_name].push({
        quantity: quantity,
        grn_number: grnNumber || "DUMMY"
      });
  
      // console.log("Updated Payload Inside setPayload:", updatedPayload);
      return updatedPayload;
    });
  };
  
  useEffect(() => {
    console.log("Updated Payload in useEffect:", payload);
  }, [payload]);

  const inputRef = useRef(null);

useEffect(() => {
  if (inputRef.current) {
    inputRef.current.focus();
  }
}, []); 
  


  return (
    <div className="mx-3 my-2">
      <div className=" items-center mb-2">
        <h1 className="mt-4">{locationData.circuit_name} &gt; {locationData.work_order_order_id}</h1>
        <Stack sx={{flexDirection:'row',justifyContent:'space-between',alignItems:'center'}}>
        <h1 className="fw-24 mt-1">Please scan the material and confirm</h1>
        <input ref={inputRef} style={{height:'1.5rem',width:'26rem'}} type="text" onKeyPress={handleKeyPress}/>
        </Stack>
      </div>
      <Table
      // headColor={COLORS.TEXT_GRAY}
      // headLabelColor={COLORS.WHITE}
        columns={LABELS}
        rows={tableData}
        // rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        // paginator
        serialize
        total={total}
      />
      <AllMatScan
        open={modalOpen === "detail"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        // data={machineData}
        edit={edit}
        // setData={setData}
        // onSubmit={handleAdd}
      />
      {/* <Downtime
        open={modalOpen === "start"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        // data={data}
        // edit={edit}
        // setData={setData}
        // onSubmit={handleAdd}
        id={machineId}
      /> */}
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
        // sx={{px:6}}
      >
        <Button bordered gray >
          Cancel
        </Button>
        <Button color={'gray'} onClick={()=>SubmitScanned()}>
          <Typography color={COLORS.TEXT_GRAY}>
          Confirm
          </Typography>  
        </Button>
      </Stack>
    </div>
  );
};

export default MaterialScan;
