import React, { useEffect, useState } from "react";
import {useDispatch} from 'react-redux'
import Table from "../../components/table";
import useUserTenant from "../../store/userTenant";
import { toast } from "react-toastify";
import { IconButton, Stack ,Tab, Tabs,Typography} from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import Category from "../../components/UI/Category";
import Button from "../../components/button";
import { useLocation, useNavigate } from "react-router-dom";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import Tooltip from '@mui/material/Tooltip';
import AddUser from "./AddUser";
import { t } from "i18next";
import {setUsr} from "../../store/users/user.js"
import ManagePermission from "./ManagePermission.jsx";
import MachinesPermission from "./MachinePermission.jsx";
import PrecisionManufacturingIcon from '@mui/icons-material/PrecisionManufacturing';

const UserTenantPage = () => {
  const location = useLocation()
  const navigate = useNavigate()
    const dispatch = useDispatch()
  const loginUserId = localStorage.getItem("userId")
  const tenantData = location?.state
  const { list,opratorList,create,createOperator,update} = useUserTenant();
  const [tab , setTab] = useState("Users")
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [data, setData] = useState({
  });

  useEffect(() => {
    fetchUsers();
  }, [page, tab]);

  const fetchUsers = async () => {
    setLoading("fetch");
    const [_list, _count] =  await list(page + 1,tenantData.id) 
    const Usr = _list.filter(item => !item.is_operator);
    setUsers(Usr);
    setTotal(_count);
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: "Email",
      render: (rowData) => rowData.email || '-',
    },
    {
      id: 2,
      title: "User Name",
      render: (rowData) => rowData.username || '-',
    },
    {
      id: 3,
      title: "Full Name",
      render: (rowData) => rowData.fullname || '-',
    },
    // {
    //   id: 4,
    //   title: "Employee Id",
    //   render: (rowData) => rowData.employee_id,
    // },
    {
      id: 5,
      title: "Role",
      render: (rowData) => <Category title={rowData.is_admin?"admin":"Na"}/>,
    },
    {
      id: 6,
      title: "Action",
      render: (rowData) => 
        <>
      <Tooltip title={rowData.is_admin?"Remove Admin":"Make Admin"}>
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleUpdate(rowData?.id,rowData?.is_admin)}
        >
         <AdminPanelSettingsIcon color={rowData.is_admin?"success":"error"} fontSize="small" />
        </IconButton>
      </Tooltip>
        {rowData?.id !== loginUserId.replace(/-/g, '') ? (
          <>
         <Tooltip title="Manage Permission">
          <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={()=>{dispatch(setUsr(rowData));setModalOpen('manage')}}
        >
          <MenuIcon color="success" fontSize="small" />
        </IconButton>
        </Tooltip>
         <Tooltip title="Machine Permission">
          <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={()=>{dispatch(setUsr(rowData));setModalOpen('machine')}}
        >
          <PrecisionManufacturingIcon color="success" fontSize="small" />
        </IconButton>
        </Tooltip>
        </>
        ):null}
        </>
    }
  ];

  const handleAdd = async () => { 
    const payload = {...data} 
    setLoading("fetch")
    tab === "Users" ? await create(payload,tenantData.id): await createOperator(payload,tenantData.id)
    setData({});
    setModalOpen(false);
    await fetchUsers();
    setLoading(false);
  };

  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    } 
    await update(tenantData.id,id,payload)
    await fetchUsers();
    setLoading(false);
  };

  return (
    <div className="mx-3 my-2">
      {/* <Stack sx={{flexDirection:'row',gap:1}}>
      <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer',alignSelf:'center'}} onClick={()=>navigate(-1)}/>
      <Tabs value={tab} onChange={(_e, v) => {setTab(v);setPage(0)}} className="mb-2">
          {["Users", "Operators"].map((label, i) => (
            <Tab label={label} value={label} />
          ))}
        </Tabs>
        </Stack> */}
      <div className="flex items-center justify-between">
        <Stack sx={{flexDirection:'row',gap:1}}>
      <IconButton onClick={()=>navigate(-1)}>
          <ArrowBackIcon/>
        </IconButton>
         <Typography className="fw-20-500-36">
          {tenantData.name} {tab}
          {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </Typography>
        </Stack>
        <Button color={'gray'} onClick={() => {setModalOpen(true)}}>
          <Typography className="fw-20-500-24">Create {tab}</Typography>
          </Button>
      </div>
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
       <AddUser
        open={modalOpen}
        onClose={() => setModalOpen(false)}
        data={data}
        setData={setData}
        onSubmit={handleAdd}
        loading={loading === "add"}
        tab={tab}
      />
       <ManagePermission
       onClose={() => setModalOpen(false)}
       open={modalOpen === "manage"}
       modalOpen={modalOpen}
      />
      <MachinesPermission
       onClose={() => setModalOpen(false)}
       open={modalOpen === "machine"}
       modalOpen={modalOpen}
      />
    </div>
  );
};

export default UserTenantPage;
