import React,{useEffect, useState} from "react";
import { useSelector } from "react-redux";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import crossIcon from '../../assets/svg/cross-icon.svg'
import useUser from "../../store/users";
import { useTranslation } from 'react-i18next';

const EditUser = ({ open, onClose, data, setData, onSubmit,handleAdd2, loading ,tab,edit,fetchUsers}) => {
  const { t, i18n } = useTranslation();
  const {updateProfile,userProfile} = useUser()
  const userData = useSelector((state) =>state?.USER_ITEM?.data)
  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
 

  const [editData,setEditData] = useState({
    username:'',
    email:'',
    fullname:'',
    aadhar_number:'',
    phone_number:'',
    employee_id:'',
    employee_type:'',
    date_of_joining:'',
    aadhar_no:''

  })
 
  useEffect(()=>{
    if(userData !== undefined){
      console.log("user data----------",tab)
      if(tab ===1){
        setEditData({...editData,
          "username":userData.username || userData.user.username ,
          "email":userData.email ,
          "fullname":userData.fullname || userData.user.fullname ,
          "aadhar_number":userData.aadhar_number ||'',
          "phone_number": userData.phone_number || '',
          // "employee_id":'',
          // "employee_type":'',
          "date_of_joining":userData.date_of_joining || '',
          // "aadhar_number":userData.aadhar_number ||'',
        })
      }else{
        setEditData({...editData,
         "aadhar_number":userData.aadhar_number ||'',
          "phone_number": userData.phone_number || '',
          "date_of_joining":userData.date_of_joining || '',
          // "aadhar_number":'',
          // "phone_number":'',
          // "employee_id":'',
          // "employee_type":'',
          // "date_of_joining":'',
          // "aadhar_no":'',
        })
      }
     
    }
  },[edit === 'edit'])

  // console.log("useSelector----------------->",userData)
 
  const USER_SCHEMA = [
    // {
    //   title: t('user_name'),
    //   placeholder: "Enter User Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "username",
    //   validator: VALIDATORS.NAME,
    // },
    // {
    //   title: t('email'),
    //   placeholder: "Enter Email",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "email",
    //   validator: VALIDATORS.EMAIL,
    // },  
    // {
    //   title:t('full_name'),
    //   placeholder: "Enter Full Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "fullname"
    // },
    {
      title: "Aadhar Number",
      placeholder: "Aadhar Number",
      type: SCHEMA_TYPES.INPUT,
      name: "aadhar_number",
      inputType:"number", 
      validator: VALIDATORS.AADHAR, 
    },
    {
      title: "Phone Number",
      placeholder: "Phone Number",
      type: SCHEMA_TYPES.INPUT,
      name: "phone_number",
      inputType:"number", 
      validator: VALIDATORS.PHONE, 
    },
    // {
    //   title: "Employee Id",
    //   placeholder: "employee_id",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "employee_id",
    //   // validator: VALIDATORS.REQUIRED, 
    // },
    // {
    //   title: "Employee Type",
    //   placeholder: "Employee Type",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "employee_type",
    //   // validator: VALIDATORS.REQUIRED, 
    // },
    {
      title: "Date of Joining",
      placeholder: "date_of_joining",
      type: SCHEMA_TYPES.DATE,
      name: "date_of_joining",
      // validator: VALIDATORS.REQUIRED, 
    },
   
  ];

  const operatorSchema =[
    {
      title: t('aadhaarNumber'),
      placeholder: "Aadhar Number",
      type: SCHEMA_TYPES.INPUT,
      name: "aadhar_number",
      inputType:"number", 
      validator: VALIDATORS.REQUIRED && VALIDATORS.AADHAR,
    },
    //  {
    //   title: t('full_name'),
    //   placeholder: "Enter Full Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "fullname"
    // },
    {
      title: "Phone Number",
      placeholder: "Phone Number",
      type: SCHEMA_TYPES.INPUT,
      name: "phone_number",
      inputType:"number", 
      validator: VALIDATORS.PHONE, 
    },
    // {
    //   title: "Employee Id",
    //   placeholder: "employee_id",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "employee_id",
    //   // validator: VALIDATORS.REQUIRED, 
    // },
    // {
    //   title: "Employee Type",
    //   placeholder: "Employee Type",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "employee_type",
    //   // validator: VALIDATORS.REQUIRED, 
    // },
    {
      title: t('date_of_joining'),
      placeholder: "date_of_joining",
      type: SCHEMA_TYPES.DATE,
      name: "date_of_joining",
      // validator: VALIDATORS.REQUIRED, 
    },
  ]


  async function updateFunction(){
    let userPayload = {
      "username":editData.username,
      "email":editData.email,
      "fullname":editData.fullname
    }
    let operatorPayload = {
      "aadhar_number":editData.aadhar_number,
      "fullname":editData.fullname,
      "phone_number":editData.phone_number,
      "date_of_joining":editData.date_of_joining
    }
      
    let profileId = await userProfile(tab===0?userData.id:userData.user.id)
    await updateProfile(profileId.id,tab===0? userPayload:operatorPayload)
    await onClose()
    await fetchUsers()
  }
  
  const handleNext = async () => {
    if (step === 0 ) {
      const _errors = Validate(tab === 0?USER_SCHEMA:operatorSchema, editData);
      console.log("_errors",_errors)
      setErrors(_errors);
      if (_errors.size) return;
      await updateFunction()
      // setStep((prev) => prev + 1);
      return;
    }
    // if (step === 1) {
    //   const _errors = Validate(form2, data);
    //   setErrors(_errors);
    //   if (_errors.size) return;
    //   await handleAdd2()
    //   setStep(0);
    //   return;
    // } 
  };
  
  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setEditData((prev) => ({ ...prev, [name]: value }));
  };
  
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{tab===0?t('edit_user'):t('edit_operator')}</h3>
      <SchemaExtractor
          data={editData}
          errors={errors}
          onChange={handleChange}
          schema={tab === 0?USER_SCHEMA:operatorSchema}
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">
          {t('next')}
          </Typography>
          
        </Button>
      </Stack>
    </Modal>
  );
};

export default EditUser;
