import { React, useState, useEffect } from "react";
import { t } from "i18next";
// components
import Button from "../../../components/buttonn";
// Assets
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";

import { useOrder } from "../../../store/materialPlanner";

const FulfilledOrderActionModal = ({
  setLocalUI,
  data = {},
  noStepFn = () => {},
  nextStepFn = () => {},
}) => {
  const {
    fetchOrdersById,
    error: orderError,
    loading: ordersLoading,
  } = useOrder();
  console.log("FulfilledOrderActionModal data", data);
  const [selectedOrderDetails, setSelectedOrderDetails] = useState(null);
  useEffect(() => {
    if (data && data.orderId) {
      fetchOrdersById(data.orderId).then((fetchedData) => {
        console.log("setSelectedOrderDetails data", fetchedData);
        setSelectedOrderDetails(fetchedData);
      });
    }
  }, [data]);

  return (
    <div className="action_modal__container">
      <div>
        <CheckCircleOutlineIcon sx={{ fontSize: "6rem", color: "#86D56A" }} />
      </div>
      <p>
        Material Planning for Order <strong>{selectedOrderDetails?.name}</strong> is
        successfully done.
      </p>
      <p>
        Order <strong>{selectedOrderDetails?.order_id}</strong> is in Processing Stage now.
      </p>
      <p>
        Do you want to move it to <em>Running Stage</em>?
      </p>
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
        }}
      >
        <Button
          onClick={noStepFn}
          white={true}
          width="8rem"
          height="2.5rem"
          bordered={true}
          customStyle={{
            border: "1px solid #3E3E3E",
            backgroundColor: "#FFFFFF",
          }}
        >
          {t('no')}
        </Button>
        <Button onClick={nextStepFn} color="green" width="8rem" height="2.5rem">
        {t('yes')}
        </Button>
      </div>
    </div>
  );
};

export default FulfilledOrderActionModal;
