import React from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const AddTenant = ({ open, onClose, data, setData, onSubmit, loading,edit }) => {
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit?"Edit":'Create new'} Tenant</h3>
      <Input
        title="Tenant name"
        placeholder="Enter tenant name"
        width="99%"
        name="name"
        onChange={setData}
        value={data.name}
      />
      {/* <Input
        title="Tenant ID"
        placeholder="Enter tenant id"
        width="99%"
        name="id"
        onChange={setData}
        value={data.id}
      /> */}
      <Input
        title="Secret Key"
        placeholder="Enter secret key"
        width="99%"
        name="secret_key"
        onChange={setData}
        value={data.secret_key}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={onSubmit} loading={loading}>
          <Typography className="fw-20-500-24">{t('create')}</Typography>
        </Button>
      
      </Stack>
    </Modal>
  );
};

export default AddTenant;
