import React, { useEffect, useState } from "react";
import "./Card.css";
import Chart from "react-apexcharts";
import LinearWithValueLabel from "./Progressbar";
import { Stack, Typography } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import RefreshIcon from "../../assets/svg/refresh.svg"
import polygonIcon from "../../assets/svg/polygon.svg"
import { t } from "i18next";


const CardComponent = ({ data, expanded, onToggle,clickMore }) => {
  
  const total = 100;
  return (
    <div
      className={`card-container ${expanded ? "expanded" : ""} ${
        data.status === "online" ? "running" : "Downtime"
      } ${data.status === "downtime" ? "maintenance" : "Downtime"} ${
        data.status === "offline" ? "disconnected" : "Downtime"
      }`}
      style={{
        zIndex: expanded ? 10 : 1,
        position: expanded ? "absolute" : "relative",
        width: "430px"
      }}
    >
      {/* <div className="refresh-icon">
      <img  src={RefreshIcon} style={{display:'none'}}/>
      </div> */}

      <div className="heading" style={{marginTop:25}}>
        <h1>{data.asset_name}</h1>
        <p>{t('status')} |<span className="status-text">{data.status}</span></p>
      </div>

      <div className="pie" pointer>
        <Chart
          type="donut"
          width="100%"
          height="100%"
          series={[data.oee, total - data.oee]}
          options={{
            chart: { id: "donut-chart" },
            colors: [
              `${data.completed > 50 ? "#27AE60" : "#FF0000"}`,
              "#ffffff",
            ],
            labels: ["Used", "Remaining"],
            plotOptions: {
              pie: {
                startAngle: -90,
                endAngle: 90,
                donut: { size: "65%" },
                expandOnClick: false,
              },
            },
            stroke: {
              colors: ["#D9D9D9"],
            },
            legend: { show: false },
            dataLabels: { enabled: false },
            tooltip: { enabled: false },
          }}
        />
        <div className={`chart-percentage`}>
          <p className={`chart-percentage-text`}>
            {`${data.oee}%`}
          </p>
          </div>
      </div>

      <Stack sx={{py:1,backgroundColor:COLORS.RADIAL_LG_GRAY,mt:-7,mb:1,width:'430px'}}>
        <LinearWithValueLabel
          type="Availability"
          progress={data.availability}
        />
        <LinearWithValueLabel
          type="Performance"
          progress={data.utilization}
        />
        <LinearWithValueLabel
          type="Quality"
          progress={data.quality}
        />
      </Stack>
      {expanded && (
        <div className="additional-info" style={{width:'100%'}}>
          <div className="product-metrics-section">
            <h3 className="LinearProgress-title-h">{t('productMetrics')}</h3>
            <div className="metrics">
              <div className="metric-row">
                <span className="card-metric-label">{t('total')}</span>
                <span className="card-metric-value">
                  {data.total}
                </span>
              </div>
              <div className="metric-row">
                <span className="card-metric-label">{t('good')}</span>
                <span className="card-metric-value">
                  {data.total_good}
                </span>
              </div>
              <div className="metric-row">
                <span className="card-metric-label">{t('ng')}</span>
                <span className="card-metric-value">
                  {data.total_ng}
                </span>
              </div>
              <div className="metric-row">
                <span className="card-metric-label">{t('pass')}</span>
                <span className="card-metric-value">
                  {data.total_pass}
                </span>
              </div>
              <div className="metric-row">
                <span className="card-metric-label">{t('fail')}</span>
                <span className="card-metric-value">
                  {data.total_fail}
                </span>
              </div>
            </div>
          </div>
          <div className="cycle-info-section">
            <h3 className="LinearProgress-title-h">{t('cyclePerSec')}</h3>
            <div className="metrics">
              <div className="metric-row">
                <span className="card-metric-label">{t('best')}</span>
                <span className="card-metric-value">
                  {data.best_cycle_time}
                </span>
              </div>
              <div className="metric-row">
                <span className="card-metric-label">{t('worst')}</span>
                <span className="card-metric-value">
                  {data.worst_cycle_time}
                </span>
              </div>
              <div className="metric-row">
                <span className="card-metric-label">{t('average')}</span>
                <span className="card-metric-value">
                  {data.avg_cycle_time}
                </span>
              </div>
            </div>
            {clickMore &&(
            <div className="more-button">
              <button onClick={()=>clickMore()}>{t('more')}</button>
            </div>
            )}
          </div>
        </div>
      )}
      <div style={{display:'flex',width:'100%',justifyContent:'space-around'}}>
      <div
        className={`drop-icon ${expanded ? "rotated" : ""}`}
        onClick={onToggle}
      >
        <img  src={polygonIcon}/>
      </div>
      </div>
    </div>
  );
};

export default CardComponent;