import React, { useEffect, useState, useLayoutEffect } from "react";
import { IconButton, Stack, Tab, Tabs, Grid, Typography } from "@mui/material";
import CardComponent from "./CardComponent";
import "./Card.css";
import Table from "../../components/table";
import { useTranslation } from 'react-i18next';
import { COLORS } from "../../helpers/constants";
import useDowntime from "../../store/downtimeMaster";
import { useLocation ,useNavigate} from "react-router-dom";
import useMachineMonitoring from '../../store/machineMonitoring'
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import HorizontalStackedBarChart from './timeline-bar'
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import PendingActionsIcon from '@mui/icons-material/PendingActions';
import AddDowntimeReason from './AddDowntimeReason'
import { t } from "i18next";


const ChildMachine = () => {
  const { t, i18n } = useTranslation();
  const location = useLocation()
    const navigate = useNavigate()

  const childMachineData = location?.state
  console.log("state-----------------------data",childMachineData)
  const { assetSummary,timelineSummary,getFilters } = useMachineMonitoring()
  const { list:Reasonlist,post_reason,reason_list} = useDowntime();
  const [expandedCard, setExpandedCard] = useState(null);
  const [tab, setTab] = useState('Kanban')
  const [page, setPage] = useState(0)
    const [total, setTotal]= useState(0)
  const [loading, setLoading] = useState();
  const [machineData, setMachineData] = useState([])
  const [lineData, setLineData] = useState([])

  const [errors, setErrors] = useState(new Map());
  const [data, setData] = useState({
    id:'',
    date: childMachineData.date,
    shift:'',
    state:'',
    quality:'',
    barcode:''
  });
  const [totalPage ,setTotalPage] = useState([])
  const [filterDrop,setFilterDrop] = useState({shifts:[],states:[],quality:[]})
  const [modalOpen,setModalOpen] = useState(false)
  const [downtimeList,setDowntimeList] = useState([])

  const handleCardToggle = (index) => {
    setExpandedCard(expandedCard === index ? null : index);
  };

  useEffect(()=>{
    Filters()
    lineSummary()
    // fetchReason()
  },[])

  useEffect(()=>{
   
    summary()
      // Set up interval to call allSumary() every 5 seconds
  const interval = setInterval(() => {
    // lineSummary()
    summary()
  }, 15000);

  // Cleanup function to clear interval when dependencies change or component unmounts
  return () => clearInterval(interval);
  },[page,data.date ||'',data.shift||'',data.state||'',data.quality||'',data.barcode])

  const COLUMNS = [
    {
      id: 1,
      title: t('shift'),
      render: (rowData) => rowData.shift?.shift_name,
    },
    {
      id: 2,
      title: t('state'),
      render: (rowData) => rowData.event_type,
    },
    {
      id: 3,
      title: t('barcode'),
      render: (rowData) =><Typography color={"primary"} sx={{cursor:'pointer'}}
       onClick={()=>navigate('/packet-detail-page',{state:rowData}) }
       >{rowData.barcode}</Typography> ,
    },
    {
      id: 4,
      title: t('startTime'),
      render: (rowData) => rowData.start_time,
    },
    {
      id: 5,
      title: t('endTime'),
      render: (rowData) => rowData.end_time,
    },
    {
      id: 6,
      title: t('model'),
      render: (rowData) => rowData.employee_id,
    },
    {
      id: 7,
      title: t('duration'),
      render: (rowData) => rowData.duration,
    },
    {
      id: 8,
      title: t('serialNo'),
      render: (rowData) => rowData.employee_id,
    },
    {
      id: 8,
      title: t('quantity'),
      render: (rowData) => rowData.parts_quantity,
    },
    {
      id: 8,
      title: t('quality'),
      render: (rowData) => rowData.parts_quality,
    },
    {
      id: 8,
      title: t('qc'),
      render: (rowData) => rowData.employee_id|| '-',
    },
    {
      id: 8,
      title: t('operator'),
      render: (rowData) => rowData.employee_id|| '-',
    },
    // {
    //   id: 8,
    //   title: 'Planner Job',
    //   render: (rowData) => rowData.employee_id|| '-',
    // },
    // {
    //   id: 8,
    //   title: 'Uploaded Job',
    //   render: (rowData) => rowData.employee_id|| '-',
    // },
    {
      id: 8,
      title: t('downtimeReason'),
      render: (rowData) =>rowData.event_type==='Downtime'&&( 
        <Stack sx={{flexDirection:'row',alignItems:'center'}}>
          {rowData.downtime_reason?(
          <Typography>{rowData.downtime_reason}</Typography>
          ):(
          <PendingActionsIcon sx={{cursor:'pointer'}} onClick={()=>fetchReason(rowData)} color={'error'}/>
          )}
        </Stack>
    ),
    },
    // {
    //   id: 8,
    //   title: 'Updated By',
    //   render: (rowData) => rowData.employee_id|| '-',
    // },
    // {
    //   id: 8,
    //   title: 'Updated At',
    //   render: (rowData) => rowData.employee_id|| '-',
    // },
    // {
    //   id: 8,
    //   title: 'Edit',
    //   render: (rowData) => rowData.employee_id|| '-',
    // },
  ];

  const fetchReason = async (rowData) => {
    setDowntimeList([])
    setData({ ...data, 'p_id': rowData.id,'rowData': rowData});
    setModalOpen('submitReason')
    // setLoading("fetch");
    const [_list] = await reason_list(rowData.id);
    console.log("reason_list linst", _list)
    if(_list?.reasons?.length>0){
    const newUsers = _list.reasons.map(item => ({
      value: item.reason_name,
      key: item.id
    }));
    setDowntimeList(prevUsers => [...prevUsers, ...newUsers]);
  }
  };

  const fetchReasonm = async () => {
    const [_list] = await Reasonlist();
    const newUsers = _list.results.map(item => ({
      value: item.reason_name,
      key: item.id
    }));
    setDowntimeList(prevUsers => [...prevUsers,...newUsers]);

  };

  async function summary() {
    let [res] = await assetSummary(page + 1,childMachineData.data.asset_id,data.date,data.shift ||'',data.state||'',data.quality ||'',data.barcode ||'')
    setMachineData(res.results)
    setTotalPage(res.count)
    setLoading(false);
  }

  async function lineSummary() {
    let [timeLineData] = await timelineSummary(childMachineData.data.asset_id,data.date,data.shift ||'',data.state ||'',data.quality ||'')
    setLineData(timeLineData.data_set)
    setLoading(false);
  }
  async function Filters() {
    let [timeLineData] = await getFilters(childMachineData.data.asset_id)
    // console.log("filter res-----",timeLineData)
    setFilterDrop(timeLineData)
    setLoading(false);
  }

  const handleChange = (e) => {
    setPage(0)
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const FilterData = [
    {
      // title: t('test'),
      placeholder: t('date'),
      type: SCHEMA_TYPES.DATE,
      name: 'date',
      validator: VALIDATORS.REQUIRED,
      width: '150px',
      // keyValues:testList,
    },
    {
      // title: t('test'),
      placeholder: t('shift'),
      type: SCHEMA_TYPES.SELECT,
      name: 'shift',
      validator: VALIDATORS.REQUIRED,
      width: '150px',
      keyValues:[{ key: '', value: 'All' },...filterDrop.shifts],
    },
    {
      // title: t('test'),
      placeholder: t('state'),
      type: SCHEMA_TYPES.SELECT,
      name: 'state',
      validator: VALIDATORS.REQUIRED,
      width: '150px',
      keyValues:[{ key: '', value: 'All' },...filterDrop.states],
    },
    {
      // title: t('test'),
      placeholder: t("quality"),
      type: SCHEMA_TYPES.SELECT,
      name: 'quality',
      validator: VALIDATORS.REQUIRED,
      width: '150px',
      // keyValues:filterDrop.quality,
      keyValues:[{ key: '', value: 'All' }, ...filterDrop.quality],
    },
    {
      // title: t('test'),
      placeholder: t("searchBarcode"),
      type: SCHEMA_TYPES.INPUT,
      name: 'barcode',
      // validator: VALIDATORS.REQUIRED,
      width: '150px',
      // keyValues:filterDrop.quality,
      // keyValues:[{ key: '', value: 'All' }, ...filterDrop.quality],
    }
  ]

  const handleAdd = async () => {
    let packetId = data.p_id
    const req = {
      reason_id: data.reason,
    }
    setLoading("fetch");
    let res = await post_reason(packetId, req);
    setData({ id:'',
      date: new Date().toISOString().split('T')[0],
      shift:'',
      state:'',
      quality:'',
      barcode:''});
      setPage(0)
    setModalOpen(false);
    await summary();
    console.log("creat res----", res)
  };

  return (
    <div className="mx-3 my-2">
      <Stack sx={{justifyContent:'space-between',flexDirection:'row'}}>
      <Stack sx={{ flexDirection: 'row',gap:1 }}>
      <IconButton onClick={()=>navigate(-1)}>
          <ArrowBackIcon/>
        </IconButton>
        <Typography className="fw-20-500-36">
          {childMachineData?.data?.asset_name || 'name not found'}
        </Typography>
        </Stack>
        <Stack sx={{ flexDirection: 'row', gap: 2 }}>
          <SchemaExtractor
            data={data}
            errors={errors}
            onChange={handleChange}
            schema={FilterData}
          />
        </Stack>
      </Stack>
       
      <>
        <HorizontalStackedBarChart timeLineData={lineData} />
        <Table
          columns={COLUMNS}
          rows={machineData}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          paginator
          serialize
          total={totalPage}
          tableBodyColor={COLORS.WHITE}
        />
        <AddDowntimeReason
          open={modalOpen === "submitReason"}
          onClose={() =>{ setModalOpen(false)}}
          data={data}
          setData={setData}
          onSubmit={handleAdd}
          rsn={downtimeList}

        />
      </>
    </div>
  );
};

export default ChildMachine;