import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import useUser from "../../store/users";
import { toast } from "react-toastify";
import { IconButton, Stack, Tab, Tabs, Typography } from "@mui/material";
import { Delete } from "@mui/icons-material";
import EditIcon from '@mui/icons-material/Edit';
import Category from "../../components/UI/Category";
import useResult from "../../store/result";
import Input from "../../components/input";

import { COLORS } from "../../helpers/constants";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";
import { useNavigate } from "react-router-dom";
import moment from "moment/moment";

const TestUserPage = () => {
  const navigate = useNavigate()
  const [tab, setTab] = useState("Users")
  const [users, setUsers] = useState([]);
  const [usersF, setUsersF] = useState([]);
  const [operators, setOperators] = useState([]);
  const [operatorsF, setOperatorsF] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [csvData, setCsvData] = useState([])
  const [page, setPage] = useState(0);
  const [file, setFile] = useState(null);
  const [data, setData] = useState({
  });

  const [searchInput, setSearchInput] = useState('')
  const { list, listByUserName, listByAadhar} = useResult();
 
  useEffect(() => {
    setUsers([])
    fetchUsers();
  }, [page, tab]);

  const fetchUsers = async () => {
    setLoading("fetch");
    try {
      const res = await list(page + 1,'');
      console.log("list result user",res[0].data)
      setUsers(res[0].data);
      setTotal(res[0].count);
    } catch (error) {
      console.error("Error fetching users:", error);
    } finally {
      setLoading(false);
    }
  };

  const fetchByName = async (name) => {
    let tempUsers = usersF
    setLoading("fetch")
    if (name.length !== 0) {
      const [_list] = await listByUserName(name);
      if (_list !== undefined) {
        setUsers([_list]);
      }else{
        setUsers([])
      }
    }
    else {
      setUsers(tempUsers)
    }
    setLoading(false);
  }

  const fetchByAadhar = async (aadhar) => {
    let tempOperator = operatorsF
    setLoading("fetch")
    if (aadhar !== '') {
      const [_list, _count] = await listByAadhar(aadhar);
      if (_list !== undefined) {
        setOperators([_list])
      }else{
        setOperators([])
      }
    } else {
      setOperators(tempOperator)
    }
    setLoading(false)
  }


  const COLUMNS = [
    {
      id: 1,
      title: t('aadhar'),
      render: (rowData) =><Typography sx={{color:'blue',cursor:'pointer'}} onClick={()=>navigate('/result-page',{state:rowData.user_id})}>{rowData.aadhar}</Typography>,
    },
    {
      id: 2,
      title: t('user_name'),
      render: (rowData) => rowData.username,
    },
    {
      id: 3,
      title: t('date_of_joining'),
      render: (rowData) => moment(rowData.date_of_joining).format('ll'),
    },
    // {
    //   id: 4,
    //   title: "Employee Id",
    //   render: (rowData) => rowData.employee_id,
    // },
    {
      id: 5,
      title: t('testCount'),
      render: (rowData) => <Category title={rowData.tests} />,
    },
  ];

  const fetchByNO = async (p,val) => {
    setLoading("fetch");
    try {
      const res = await list(p,val);
      console.log("list result user",res[0].data)
      setUsers(res[0].data);
      setTotal(res[0].count);
    } catch (error) {
      console.error("Error fetching users:", error);
    } finally {
      setLoading(false);
    }
  };


 const handleChange=(e)=> {
    const value = e.target.value;
    console.log("e.target.value",e.target.value.length)
    fetchByNO(1,value)
    setSearchInput(value)
  }

  return (
    <div className="mx-3 my-2 fw-500">
      <div className="flex items-center justify-between">
        <Typography className="fw-20-500-36">{t('usersTestCompletionList')}</Typography>
      
        <Stack mb={1}>
          <Input
            height="37px"
            value={searchInput}
            placeholder={'Search by Aadhar Number'}
            onChange={(e) => handleChange(e)}
          />
        </Stack>
       
      </div>
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
        tableBodyColor={COLORS.WHITE}
      />
    </div>
  );
};

export default TestUserPage;
