import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import Table from "../../components/table";
import Button from "../../components/button";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Tooltip from '@mui/material/Tooltip';
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import useUser from "../../store/users";
import useQuestion from "../../store/questionBank";
import { Delete } from "@mui/icons-material";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants';
import { setTestRes } from "../../store/questionBank/testItem";
import { Validate } from "../../helpers/utils";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";


const TestPortal = () => {
  const location = useLocation()
  const row = useSelector((state) => state)
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState()
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
  });
  const { test_list, assignExam } = useQuestion();
  const { list: userList, listByAadhar2,listByAadharFortestPortal } = useUser();
  const [file, setFile] = useState(null);
  const [errors, setErrors] = useState(new Map());
  const [skills, setSkills] = useState([])
  const [testList, setTestList] = useState([])
  // const [testId, setTestId] = useState('')

  async function fetchByAd(val){
    let res = await listByAadharFortestPortal(val||'')
    return res
  }

  const TEST = [
    {
      title: t('operator'),
      placeholder: t('selectOperator'),
      type: SCHEMA_TYPES.MASTER,
      name: "operator",
      validator: VALIDATORS.REQUIRED,
      fetch: [fetchByAd],
      noSearch: true
    },
    {
      title: t('test'),
      placeholder: t('selectTest'),
      type: SCHEMA_TYPES.SELECT,
      name: 'test',
      validator: VALIDATORS.REQUIRED,
      keyValues: testList,
    }
  ];

  useEffect(() => {
    localStorage.setItem('draweWidth', 190)
    fetchTestList()
    fetchUsers()
  }, [])

  const fetchTestList = async () => {
    const [_list, _count] = await test_list();
    console.log("testport----",_list)
    const newList = _list.results.map(item => ({
      value: `${item.test_name}_${item.skill?item.skill:item.test_type}_${item.skill_level?item.skill_level:''}`,
      key: item.id
    }));
    setTestList(newList);
  };

  const fetchUsers = async () => {
    const [_list, _count] = await userList(page + 1)
    const newOptr = _list.results.filter(item => item.is_operator);
    const newUsers = newOptr.map(item => ({
      value: item.username,
      key: item.id
    }));
    setUsers(prevUsers => [...prevUsers, ...newUsers]);
  };

  const handleAssign = async () => {
    // console.log("data---op",data)
    // return
    const _errors = Validate(TEST, data);
    setErrors(_errors);
    if (_errors.size) return;
    let res = await assignExam(data)
    console.log("response----", res)
    if (res) {
      await startNewTab(res)
    }
    return;
  }

  async function startNewTab(res) {
    localStorage.setItem('draweWidth', 0)
    const data = { key1: res.id };
    const params = new URLSearchParams(data).toString();
    const url = `/test?${params}`;
    const newTab = window.open(url, '_blank', 'rel=noopener noreferrer');
  }

  const handleChange = async (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    if(name==='operator'){
     await fetchByAd(value)
    }
    setData((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Stack sx={{ flexDirection: 'row', alignItems: 'center' }}>
          {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
          <Typography className="fw-20-500-36">
            {t('testPortal')}
            {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
          </Typography>
        </Stack>

      </div>
      {/* <Grid display={'grid'} gap={1} mb={1} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}> */}
      <Stack sx={{ alignItems: 'flex-start', pl: 3 }}>
        <Stack>
        <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={TEST}
        />
          <Button color={'gray'} Button  onClick={handleAssign}>
            <Typography className="fw-20-500-24">
            {t('start')}
              </Typography>
              </Button>
        </Stack>

      </Stack>
      {/* </Grid> */}


    </div>
  );
};

export default TestPortal
