import React, { useState } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import qr_scan from '../../assets/svg/qr_scan.svg'
import { useNavigate } from "react-router-dom";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const StartJob = ({ open, onClose }) => {
  const navigate = useNavigate()
  const [errors, setErrors] = useState(new Map());
  const [start, setStart] = useState(true)

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <Typography className="fw-24" sx={{ mt: 4 }}>As we are going to start the process, Please check the machine and confirm</Typography>
      {/* <Typography className="fw-24">Detail</Typography> */}
      <Stack>
        <Typography className="fw-24">
          1. Instruction 1
        </Typography>
        <Typography className="fw-24">
          2. Instruction 2
        </Typography>
        <Typography className="fw-24">
          3. Instruction 3
        </Typography>
      </Stack>
      <Typography className="fw-24" sx={{ mt: 4 }}>
        Quantity is distributed in 5 batches, 5 barcodes will be printed, Please apply on each batch
      </Typography>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button color={'light_green'} onClick={()=>setStart(true)}>
          <Typography color={'black'}>
          Start
          </Typography>  
        </Button>
      </Stack>
   
    </Modal>
  );
};

export default StartJob;
