import React from 'react';
import {  TextField, Button,IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import Table from "../../components/tablee";
import './PlanningMaterial.css';
import { useState,useEffect } from 'react';
import { useOrder } from '../../store/machiningOrders';
import useMaterialPlanning from '../../store/planningMaterial'
import CapsuleComponent from '../../components/capsule'
import actionIcon from "../../assets/svg/action-icon.svg";
import RefreshIcon from "@mui/icons-material/Refresh";
import { t } from "i18next";
const orders = [
  { id: 1, orderID: '100', name: '100_SZXB0601', qty: 25, code: 'SZXB0601', description: 'ACC', orderType: 'ACC', status: 'Issuing' },
  { id: 2, orderID: '101', name: '200_SZXB0601', qty: 25, code: 'SZXB0601', description: 'Desc', orderType: 'LPC', status: 'PLAN' },
  { id: 3, orderID: '102', name: 'SZXB0601', qty: 25, code: 'SZXB0601', description: '', orderType: 'ASSEMBLY', status: 'RUNNING' },
  { id: 4, orderID: '140', name: 'HERO887677889', qty: 50, code: 'HERO887677889', description: '', orderType: 'ASSEMBLY', status: 'PLAN' },
  { id: 5, orderID: '140', name: 'HERO887677889', qty: 50, code: 'HERO887677889', description: '', orderType: 'ASSEMBLY', status: 'PLAN' },
  { id: 6, orderID: '140', name: 'HERO887677889', qty: 50, code: 'HERO887677889', description: '', orderType: 'ASSEMBLY', status: 'PLAN' },
];

const ITEMS_PER_PAGE = 5;

const PlanningMaterial = () => {
  const navigate = useNavigate();
  const {
    // fetchOrders,
    fetchOrdersMatrixView,
    fetchOrdersById,
    getOrderHoldQuantity,
    submitBOM,
    error: orderError,
    loading: ordersLoading,
  } = useOrder();

  const {matrixCount,orderList} = useMaterialPlanning()
  const [currentPage, setCurrentPage] = useState(1);
  const [inputPage, setInputPage] = useState('1');
  const [fgCodeFilter, setFgCodeFilter] = useState('');
  const [orderTypeFilter, setOrderTypeFilter] = useState('');
  const [statusFilter, setStatusFilter] = useState('');

   const [isOrderDetailsModalOpen, setIsOrderDetailsModalOpen] = useState(false);
    const [selectedOrderDetailsData, setSelectedOrderDetailsData] = useState({}); // state to display order details on order details modal
    const [page, setPage] = useState(1);
    const [ordersCount, setOrdersCount] = useState(null);
    const [pagesCount, setPagesCount] = useState(null);
    const [resultsPerPage, setResultsPerPage] = useState(10);
    const [isPrevPageAvailable, SetIsPrevPageAvailable] = useState(null);
    const [isNextPageAvailable, SetIsNextPageAvailable] = useState(null);

    const [lastRefreshTime, setLastRefreshTime] = useState(null);
    const [isSortedByCode, setIsSortedByCode] = useState(null); // null means not sorted, true means ascending, false means descending
    const [isSortedByOrderType, setIsSortedByOrderType] = useState(null); // null means not sorted, true means ascending, false means descending
    const [isSortedByStatus, setIsSortedByStatus] = useState(null); // null means not sorted, true means ascending, false means descending
    const [isSortedByOrderId, setIsSortedByOrderId] = useState(true); // null means not sorted, true means ascending, false means descending
    const [isSortedByName, setIsSortedByName] = useState(null); // null means not sorted, true means ascending, false means descending
    const [isSortedByQuantity, setIsSortedByQuantity] = useState(null); // null means not sorted, true means ascending, false means descending
    const [sortingOrder, setSortingOrder] = useState(["order_id"]); // sort by order id by default
    const [orderIdFilterValue, setOrderIdFilterValue] = useState(null);
    const [fgCodeFilterValue, setFgCodeFilterValue] = useState(null);
    const [orderTypeFilterValue, setOrderTypeFilterValue] = useState(null);
    const [orderStatusFilterValue, setOrderStatusFilterValue] = useState(null);
    const [ordersData, setOrdersData] = useState([]);
     const [pageinputValue, setPageinputValue] = useState(page);

     const [matrixData,setMatrixData] = useState({})

     useState(()=>{
      fetchMatrix()
      fetchOrders()
     },[])

    async function fetchMatrix(){
      let res = await matrixCount()
      console.log("fetchMatrix ---",res)
      setMatrixData(res[0])
     }

  const filteredOrders = orders.filter(order => {
    return (
      (fgCodeFilter === '' || order.code.toLowerCase().includes(fgCodeFilter.toLowerCase())) &&
      (orderTypeFilter === '' || order.orderType.toLowerCase().includes(orderTypeFilter.toLowerCase())) &&
      (statusFilter === '' || order.status.toLowerCase().includes(statusFilter.toLowerCase()))
    );
  });

  const totalPages = Math.ceil(filteredOrders.length / ITEMS_PER_PAGE);

  const handleNext = () => {
    if (currentPage < totalPages) {
      const nextPage = currentPage + 1;
      setCurrentPage(nextPage);
      setInputPage(nextPage.toString());
    }
  };

  const handlePrev = () => {
    if (currentPage > 1) {
      const prevPage = currentPage - 1;
      setCurrentPage(prevPage);
      setInputPage(prevPage.toString());
    }
  };

  const handlePageChange = (e) => {
    const value = e.target.value;
    setInputPage(value);

    const page = parseInt(value, 10);
    if (!isNaN(page) && page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  const handleInputBlur = () => {
    if (inputPage === '' || isNaN(parseInt(inputPage, 10)) || parseInt(inputPage, 10) < 1 || parseInt(inputPage, 10) > totalPages) {
      setInputPage(currentPage.toString());
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleInputBlur();
    }
  };

  const startIndex = (currentPage - 1) * ITEMS_PER_PAGE;
  const currentOrders = filteredOrders.slice(startIndex, startIndex + ITEMS_PER_PAGE);

  React.useEffect(() => {
    setCurrentPage(1);
    setInputPage('1');
  }, [fgCodeFilter, orderTypeFilter, statusFilter]);
  
  // useEffect(() => {
  //   fetchOrders(
  //     page,
  //     isSortedByCode,
  //     isSortedByOrderType,
  //     isSortedByStatus,
  //     isSortedByOrderId,
  //     isSortedByName,
  //     isSortedByQuantity,
  //     sortingOrder,
  //     orderIdFilterValue,
  //     fgCodeFilterValue,
  //     orderTypeFilterValue,
  //     orderStatusFilterValue
  //   ).then((data) => {
  //     setOrdersData(data.results);
  //     setOrdersCount(data.count);
  //     const totalPages = Math.ceil(data.count / resultsPerPage);
  //     setPagesCount(totalPages);

  //     setPageinputValue(page);

  //     if (data.previous) {
  //       SetIsPrevPageAvailable(true);
  //     } else {
  //       SetIsPrevPageAvailable(false);
  //     }
  //     if (data.next) {
  //       SetIsNextPageAvailable(true);
  //     } else {
  //       SetIsNextPageAvailable(false);
  //     }
  //   });
  // }, [
  //   page,
  //   isSortedByCode,
  //   isSortedByOrderType,
  //   isSortedByStatus,
  //   isSortedByOrderId,
  //   isSortedByName,
  //   isSortedByQuantity,
  //   orderIdFilterValue,
  //   fgCodeFilterValue,
  //   orderTypeFilterValue,
  //   orderStatusFilterValue,
  // ]);

  async function fetchOrders(){
    let [res] = await orderList()
    console.log("all order -------",res)
    setOrdersData(res.results);
        setOrdersCount(res.count);
        const totalPages = Math.ceil(res.count / resultsPerPage);
        setPagesCount(totalPages);
  
        setPageinputValue(page);
  
        if (res.previous) {
          SetIsPrevPageAvailable(true);
        } else {
          SetIsPrevPageAvailable(false);
        }
        if (res.next) {
          SetIsNextPageAvailable(true);
        } else {
          SetIsNextPageAvailable(false);
        }
  }
  // useEffect(() => {
  //   fetchOrders(
  //     page,
  //     isSortedByCode,
  //     isSortedByOrderType,
  //     isSortedByStatus,
  //     isSortedByOrderId,
  //     isSortedByName,
  //     isSortedByQuantity,
  //     sortingOrder,
  //     orderIdFilterValue,
  //     fgCodeFilterValue,
  //     orderTypeFilterValue,
  //     orderStatusFilterValue
  //   ).then((data) => {
  //    
  //   });
  // }, [
  //   page,
  //   isSortedByCode,
  //   isSortedByOrderType,
  //   isSortedByStatus,
  //   isSortedByOrderId,
  //   isSortedByName,
  //   isSortedByQuantity,
  //   orderIdFilterValue,
  //   fgCodeFilterValue,
  //   orderTypeFilterValue,
  //   orderStatusFilterValue,
  // ]);
  
  const COLUMNS = (navigate) => [
    {
      // id: "order_id",
      title: `Order ID ${
        isSortedByOrderId === true
          ? ""
          : isSortedByOrderId === false
          ? ""
          : ""
      }`,
      render: (rowData) => (
        <span
          // onClick={() => handleOrderClick(rowData.id)}
          style={{ cursor: "pointer"}}
        >
          {rowData.order_id}
        </span>
      ),
    },
    {
      id: "name",
      title: `Name ${
        isSortedByName === true ? "" : isSortedByName === false ? "" : ""
      }`,
      render: (rowData) => rowData.name,
    },
    {
      id: "quantity",
      title: "Qty",
      title: `Qty ${
        isSortedByQuantity === true
          ? ""
          : isSortedByQuantity === false
          ? ""
          : ""
      }`,
      render: (rowData) => rowData.quantity,
    },
    {
      // id: "fg_code",
      title: `Code ${
        isSortedByCode === true ? "" : isSortedByCode === false ? "" : ""
      }`,
      render: (rowData) => rowData.fg_code,
    },
    {
      id: 5,
      title: "Description",
      render: (rowData) => rowData.fg_description,
    },
    {
      // id: "order_type",
      title: `Order type ${
        isSortedByOrderType === true
          ? ""
          : isSortedByOrderType === false
          ? ""
          : ""
      }`,
      render: (rowData) => rowData.order_type,
    },
    {
      // id: "status",
      title: `Status ${
        isSortedByStatus === true
          ? ""
          : isSortedByStatus === false
          ? ""
          : ""
      }`,
      render: (rowData) => <CapsuleComponent capsuleText={rowData.running_status} />,
    },
    {
      id: 9,
      title: "Action",
      render: (orderData) => (
        <img
          src={actionIcon}
          style={{
            width: "2.78vw",
            height: "2.78vw",
            cursor: "pointer",
          }}
          alt="Action Icon"
          onClick={() =>{
             if(orderData.order_type==='ACC'){navigate("/planning-material-plan",{state:orderData})}
            }}
        />
      ),
    },
  ];
  return (
    <div>
      <div style={{marginBottom:10}} className="planning-overview-heading">
        Planning
      </div>
      <div className="planning-overview-metrics-container">
      <div className="planning-metrics-box4">
          <p>Planned</p>
          <p><strong>{matrixData.planned}</strong></p>
        </div>
        <div className="planning-metrics-box1">
          <p>Plan</p>
          <p><strong>{matrixData.plan}</strong></p>
        </div>  
        <div className="planning-metrics-box2">
          <p>Running</p>
          <p><strong>{matrixData.running}</strong></p>
        </div>
        <div className="planning-metrics-box3">
          <p>Completed</p>
          <p><strong>{matrixData.completed}</strong></p>
        </div>
        {/* <div className="planning-last-refresh">
          <div className="planning-left-part">
            <p>Last Refresh</p>
            <p>12/29/2024, 4:31:53 PM</p>
          </div>
          <div className="planning-right-part">
            
            <IconButton >
                <RefreshIcon />
              </IconButton>
          
          </div>
        </div> */}
      </div>
   
      <div style={{padding:10}}>
       <Table
          // edit={edit}
          columns={COLUMNS(navigate)}
          // onTableHeadCellClick={(title) => {
          //   handleColumnHeadClick(title);
          // }}
          rows={ordersData}
          rowsPerPage={resultsPerPage}
          loading={ordersLoading}
          titleCellClasses="c-gray fw-700"
          page={page - 1}
          // paginator
          serialize
          total={ordersCount}
          // handleOrderIdFilterInput={(value) => {
          //   handleOrderIdFilterInput(value);
          // }}
          // handleFgCodeFilterInput={(value) => {
          //   handleFgCodeFilterInput(value);
          // }}
          // handleOrderTypeFilterInput={(value) => {
          //   handleOrderTypeFilterInput(value);
          // }}
          // handleOrderStatusFilterInput={(value) => {
          //   handleOrderStatusFilterInput(value);
          // }}
        />
        </div>
      <div className='planning-footer-container'>
        <button onClick={handlePrev} disabled={currentPage === 1}>
          Prev
        </button>
        <div className='planning-overview-page-details'>
          <h1>Page</h1>
          <input
            type="number"
            className="planning-page-number-input"
            value={inputPage}
            onChange={handlePageChange}
            onBlur={handleInputBlur}
          
            min="1"
            max={totalPages}
          />
          <h1>Of</h1>
          <p>{totalPages}</p>
        </div>
        <button onClick={handleNext} disabled={currentPage === totalPages}>
          Next
        </button>
      </div>
    </div>  
  );
};

export default PlanningMaterial;