import "./style.scss";
import { useNavigate, useSearchParams } from "react-router-dom";
import RefreshIcon from "@mui/icons-material/Refresh";
import React, { useEffect, useState } from "react";
import Table from "../../../components/tablee";
// import useTools from "../../../store//tools";
import { Chip, IconButton, Stack, Tab, Tabs } from "@mui/material";
// import useMaterials from "../../../store//materials";
// import useMaster from "../../../store//master";
import CapsuleComponent from "../../../components/capsule";
import Button from "../../../components/buttonn";
import actionIcon from "../../../assets/svg/action-icon.svg";
import OrderDetailsModal from "./OrderDetailsModal";
import { t } from "i18next";

// components
import Modal from "../../../components/modal";
import Spin from "../../../components/spin";

// Assets
import crossIcon from "../../../assets/svg/cross-icon.svg";
// constants
import { USER_ACTIONS, PLANNER_STAGES } from "../../../helpers/constants";

// immediate
import FulfilledOrderActionModal from "./FulfilledOrderActionModal";
import PartialFulfilledOrderActionModal from "./PartialFulfilledOrderActionModal";
// import REQUEST from "../../../store/api";
import { useOrder, useScanning } from "../../../store/materialPlanner";
import moment from "moment";

const metricsBoxesInitialState = [
  { color: "var(--primary-color)", title: "Plan", value: 38 },
  { color: "var(--secondary-color)", title: "Issuing", value: 14 },
  { color: "var(--tertiary-color)", title: "Running", value: 2 },
];
// const apiRequest = new REQUEST();

function PlannerOverview() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [loading, setLoading] = useState(true);
  const [localUI, setLocalUI] = useState({
    pageLoading: true,
    isFulfilledOrderActionModalOpen: false, // when user is navigated back to overview page after scanning materials
    isPartialFulfilledOrderActionModalOpen: false, // when user clicks on action btn for Processing type orders
    selectedOrderId: null, // selected order's ID (used as API call parameter)
    selectedOrderOrderId: null, // selected order's Order ID (used only for display purposes)
    selectedOrderName: null, // selected order's Name (used only for display purposes)
    selectedOrderStatus: null,
    userActionStatus: null,
    selectedOrderType: null,
    selectedHoldingOrderMaterialsQty: null,
    isPartialFulfilledOrderActionModalEditBtnEnabled: null, // for ASSEMBLY type orders, Run btn is disabled. User has to go to edit page first to be checked for order completion i.e. whether all items are collected or not. When the user is redirected back to the overview page from the scanning page, FulfilledOrderActionModal is opened which has an option to Run the order. This is done as checks are not done while scanning from mobile app.
  });
  const [dataList, setDataList] = useState([]); // state to display orders list in Frontend
  const [ordersData, setOrdersData] = useState([]); // to capture all the orders
  const {
    fetchOrders,
    fetchOrdersMatrixView,
    fetchOrdersById,
    getOrderHoldQuantity,
    submitBOM,
    error: orderError,
    loading: ordersLoading,
  } = useOrder();

  const { updateOrderState } = useScanning();

  const [total, setTotal] = useState(0);
  const [isOrderDetailsModalOpen, setIsOrderDetailsModalOpen] = useState(false);
  const [selectedOrderDetailsData, setSelectedOrderDetailsData] = useState({}); // state to display order details on order details modal
  const [page, setPage] = useState(1);
  const [ordersCount, setOrdersCount] = useState(null);
  const [pagesCount, setPagesCount] = useState(null);
  const [resultsPerPage, setResultsPerPage] = useState(5);
  const [isPrevPageAvailable, SetIsPrevPageAvailable] = useState(null);
  const [isNextPageAvailable, SetIsNextPageAvailable] = useState(null);
  const [edit, setEdit] = useState(false);
  const [data, setData] = useState({
    specification: {},
    location: {},
  });

  const [metricsBoxesState, setMetricsBoxesState] = useState(
    metricsBoxesInitialState
  );
  const [lastRefreshTime, setLastRefreshTime] = useState(null);
  const [isSortedByCode, setIsSortedByCode] = useState(null); // null means not sorted, true means ascending, false means descending
  const [isSortedByOrderType, setIsSortedByOrderType] = useState(null); // null means not sorted, true means ascending, false means descending
  const [isSortedByStatus, setIsSortedByStatus] = useState(null); // null means not sorted, true means ascending, false means descending
  const [isSortedByOrderId, setIsSortedByOrderId] = useState(true); // null means not sorted, true means ascending, false means descending
  const [isSortedByName, setIsSortedByName] = useState(null); // null means not sorted, true means ascending, false means descending
  const [isSortedByQuantity, setIsSortedByQuantity] = useState(null); // null means not sorted, true means ascending, false means descending
  const [sortingOrder, setSortingOrder] = useState(["order_id"]); // sort by order id by default
  const [orderIdFilterValue, setOrderIdFilterValue] = useState(null);
  const [fgCodeFilterValue, setFgCodeFilterValue] = useState(null);
  const [orderTypeFilterValue, setOrderTypeFilterValue] = useState(null);
  const [orderStatusFilterValue, setOrderStatusFilterValue] = useState(null);

  useEffect(() => {
    fetchOrders(
      page,
      isSortedByCode,
      isSortedByOrderType,
      isSortedByStatus,
      isSortedByOrderId,
      isSortedByName,
      isSortedByQuantity,
      sortingOrder,
      orderIdFilterValue,
      fgCodeFilterValue,
      orderTypeFilterValue,
      orderStatusFilterValue
    ).then((data) => {
      setOrdersData(data.results);
      setOrdersCount(data.count);
      const totalPages = Math.ceil(data.count / resultsPerPage);
      setPagesCount(totalPages);

      setPageinputValue(page);

      if (data.previous) {
        SetIsPrevPageAvailable(true);
      } else {
        SetIsPrevPageAvailable(false);
      }
      if (data.next) {
        SetIsNextPageAvailable(true);
      } else {
        SetIsNextPageAvailable(false);
      }
    });
  }, [
    page,
    isSortedByCode,
    isSortedByOrderType,
    isSortedByStatus,
    isSortedByOrderId,
    isSortedByName,
    isSortedByQuantity,
    orderIdFilterValue,
    fgCodeFilterValue,
    orderTypeFilterValue,
    orderStatusFilterValue,
  ]);

  // async function fetchOrderDetails(id) {}

  const onActionClick = (orderData) => {
    switch (orderData.status) {
      case PLANNER_STAGES.PLAN:
        navigate(
          `/material-planner-plan?orderId=${orderData.id}&orderStatus=${orderData.status}&orderType=${orderData.order_type}&orderOrderId=${orderData.order_id}`
        );
        break;
      case PLANNER_STAGES.ISSUING:
        console.log("orderData.order_type", orderData.order_type);
        // Update local UI state
        setLocalUI((prevLocalUI) => ({
          ...prevLocalUI,
          isPartialFulfilledOrderActionModalOpen: true,
          selectedOrderOrderId: orderData.order_id,
          selectedOrderStatus: orderData.status,
          selectedOrderId: orderData.id,
          selectedOrderType: orderData.order_type,
          isPartialFulfilledOrderActionModalEditBtnEnabled:
            orderData.order_type === "ASSEMBLY" ? false : true,
          // for ASSEMBLY type orders, Run btn is disabled. User has to go to edit page first to be checked for order completion i.e. whether all items are collected or not. When the user is redirected back to the overview page from the scanning page, FulfilledOrderActionModal is opened which has an option to Run the order. This is done as checks are not done while scanning from mobile app.
        }));
        break;
      case PLANNER_STAGES.RUNNING:
        // Update local UI state
        setLocalUI((prevLocalUI) => {
          return {
            ...prevLocalUI,
            // isPartialFulfilledOrderActionModalOpen: true,
            selectedOrderOrderId: orderData.order_id,
            selectedOrderStatus: orderData.status,
            selectedOrderId: orderData.id,
            selectedOrderType: orderData.order_type,
            isPartialFulfilledOrderActionModalEditBtnEnabled: false,
            // for ASSEMBLY type orders, Run btn is disabled. User has to go to edit page first to be checked for order completion i.e. whether all items are collected or not. When the user is redirected back to the overview page from the scanning page, FulfilledOrderActionModal is opened which has an option to Run the order. This is done as checks are not done while scanning from mobile app.
          };
        });
        viewRunningOrderDetails(
          orderData.id,
          orderData.status,
          orderData.order_type,
          orderData.order_id
        );
        break;
      default:
        break;
    }
  };

  const handleOrderClick = async (id) => {
    setIsOrderDetailsModalOpen(true);
    try {
      const order = await fetchOrdersById(id);
      setSelectedOrderDetailsData(order);
    } catch (error) {
      console.error("Error in fetchOrderDetails:", error);
    }
  };

  const COLUMNS = (navigate) => [
    {
      id: 5,
      title: "Date",
      render: (rowData) => moment(rowData.date_created).format('ll'),
    },
    {
      id: "order_id",
      title: `Order ID ${isSortedByOrderId === true
          ? ""
          : isSortedByOrderId === false
            ? ""
            : ""
        }`,
      render: (rowData) => (
        <span
          onClick={() => handleOrderClick(rowData.id)}
          style={{ cursor: "pointer", color: "blue" }}
        >
          {rowData.order_id}
        </span>
      ),
    },
    {
      id: "name",
      title: `Name ${isSortedByName === true ? "" : isSortedByName === false ? "" : ""
        }`,
      render: (rowData) => rowData.name,
    },
    {
      id: "quantity",
      title: "Qty",
      title: `Qty ${isSortedByQuantity === true
          ? ""
          : isSortedByQuantity === false
            ? ""
            : ""
        }`,
      render: (rowData) => rowData.quantity,
    },
    {
      id: "fg_code",
      title: `Code ${isSortedByCode === true ? "" : isSortedByCode === false ? "" : ""
        }`,
      render: (rowData) => rowData.fg_code,
    },

    {
      id: "order_type",
      title: `Order type ${isSortedByOrderType === true
          ? ""
          : isSortedByOrderType === false
            ? ""
            : ""
        }`,
      render: (rowData) => rowData.order_type,
    },
    {
      id: "status",
      title: `Status ${isSortedByStatus === true
          ? ""
          : isSortedByStatus === false
            ? ""
            : ""
        }`,
      render: (rowData) => <CapsuleComponent capsuleText={rowData.status} />,
    },
    {
      id: 9,
      title: "Action",
      render: (orderData) => (
        <img
          src={actionIcon}
          style={{
            width: "2.78vw",
            height: "2.78vw",
            cursor: "pointer",
          }}
          alt="Action Icon"
          onClick={() => onActionClick(orderData)}
        />
      ),
    },
  ];
  // looking for orderId and status in the url
  const orderId = searchParams.get("orderId");
  const paramStatus = searchParams.get("status");
  const orderStatus = searchParams.get("orderStatus");
  const refreshOverviewMatrix = () => {
    fetchOrdersMatrixView().then((data) => {
      const updatedMetrics = metricsBoxesState.map((box) => ({
        ...box,
        value: data[box.title.toLowerCase()],
      }));
      setMetricsBoxesState(updatedMetrics);
      setLastRefreshTime(new Date().toLocaleString());
    });
  };
  // handling the action modal
  useEffect(() => {
    refreshOverviewMatrix();
    // looking for orderId and status in the url
    const orderId = searchParams.get("orderId");
    const paramStatus = searchParams.get("status");
    const orderStatus = searchParams.get("orderStatus");
    if (orderId && paramStatus && orderStatus) {
      setLocalUI((prevLocalUI) => {
        let updatedUI = { ...prevLocalUI };

        if (paramStatus.toLowerCase() === USER_ACTIONS.SCANNED) {
          console.log("paramStatus.toLowerCase() === USER_ACTIONS.SCANNED");

          updatedUI.isFulfilledOrderActionModalOpen = true;
          updatedUI.selectedOrderId = orderId; //TODO - set selectedOrderOrderId
          updatedUI.selectedOrderStatus = orderStatus;
          updatedUI.userActionStatus = paramStatus;
        }

        updatedUI.pageLoading = false;
        return updatedUI;
      });
      fetchOrdersById(orderId).then((data) => {
        console.log("fetchOrdersById", localUI.selectedOrderId, "data", data);
        if (data) {
          const newOrderName = data.name || "";
          const newOrderOrderId = data.order_id || "";
          setLocalUI((prevState) => {
            return {
              ...prevState,
              selectedOrderOrderId: newOrderOrderId,
              selectedOrderName: newOrderName,
            };
          });
        }
      });
    } else {
      setLocalUI((prevLocalUI) => ({
        ...prevLocalUI,
        pageLoading: false,
      }));
    }
  }, [orderId, paramStatus, orderStatus]);

  // polling - 1 hour change for fetch all orders - call every 30 mins
  // const moveOrderToRunningStage = async () => {
  //   const route = `/orders/update-state/${data.order_id}/`;
  //   const token = localStorage.getItem("t_token");
  //   const headers = {
  //     Authorization: `Bearer ${token}`,
  //   };
  //   const body = { state: "running" };

  //   try {
  //     const response = await apiRequest.get(
  //       route,
  //       body,
  //       headers,
  //       (response) => {
  //         console.log("Success:", response);
  //       },
  //       (error) => {
  //         console.error("Error:", error);
  //       }
  //     );

  //     // Do something with the response, if needed
  //   } catch (error) {
  //     console.error("Error in fetchOrderDetails:", error);
  //   }
  // };
  // handling the action modal submit
  const moveFulfilledOrderToRunningState = () => {
    const orderId = searchParams.get("orderId");
    // get on hold order state from API call
    // call submit BOM API
    getOrderHoldQuantity(localUI.selectedOrderId).then((data) => {
      setLocalUI((prevLocalUI) => ({
        ...prevLocalUI,
        selectedHoldingOrderMaterialsQty: data,
      }));
      console.log("getOrderHoldQuantity data", data);
      console.log(
        "selectedHoldingOrderMaterialsQty",
        localUI.selectedHoldingOrderMaterialsQty
      );
      submitBOM(localUI.selectedOrderId, { material_issue: data }).then(
        (data) => {
          console.log("submit BOM data", data);
          updateOrderState(orderId, PLANNER_STAGES.RUNNING).then((data) => {
            refreshOverviewMatrix();
          });
          const newOrdersData = ordersData.map((item) => ({ ...item }));

          // find index of the orderId in the dataList and update the status
          const index = ordersData.findIndex((item) => item.id === orderId);
          if (index !== -1) {
            newOrdersData[index].status = PLANNER_STAGES.RUNNING;
          }

          setOrdersData(newOrdersData);

          setLocalUI({ ...localUI, isFulfilledOrderActionModalOpen: false });
          // clear search params so that next action btn click does not open fulfilled orders Action modal
          navigate(`/material-planner/`);
        }
      );
    });
  };
  // handling the action modal denial
  const handleFulfilledOrderActionModalDenial = () => {
    const newDataList = [...dataList];
    // find index of the orderId in the dataList and update the status
    const index = dataList.findIndex(
      (item) => item.order_id === localUI.selectedOrderId
    );
    if (index !== -1) {
      if (localUI.userActionStatus === USER_ACTIONS.SCANNED) {
        newDataList[index].status = PLANNER_STAGES.ISSUING;
      }
    }
    setDataList(newDataList);

    setLocalUI({ ...localUI, isFulfilledOrderActionModalOpen: false });
    // clear search params so that next action btn click does not open fulfilled orders Action modal
    navigate(`/material-planner/`);
  };
  // edit partially filled order
  const editPartiallyFilledOrder = () => {
    setLocalUI({ ...localUI, isPartialFulfilledOrderActionModalOpen: false });
    navigate(`/material-planner/`);
    navigate(
      `/material-planner-plan/scanning?orderId=${localUI.selectedOrderId}&orderStatus=${localUI.selectedOrderStatus}&orderType=${localUI.selectedOrderType}&orderOrderId=${localUI.selectedOrderOrderId}`
    );
  };
  // move partially filled order to running state
  const movePartiallyFulfilledOrderToRunningState = () => {
    const orderId = localUI.selectedOrderId;
    // get order on hold quantity
    getOrderHoldQuantity(localUI.selectedOrderId).then((data) => {
      setLocalUI((prevLocalUI) => ({
        ...prevLocalUI,
        selectedHoldingOrderMaterialsQty: data,
      }));
      console.log("getOrderHoldQuantity data", data);
      console.log(
        "selectedHoldingOrderMaterialsQty",
        localUI.selectedHoldingOrderMaterialsQty
      );
      submitBOM(localUI.selectedOrderId, { material_issue: data }).then(
        (data) => {
          console.log("submit BOM data", data);
          updateOrderState(orderId, PLANNER_STAGES.RUNNING).then((data) => {
            refreshOverviewMatrix();
          });
          const newOrdersData = ordersData.map((item) => ({ ...item }));

          // find index of the orderId in the dataList and update the status
          const index = ordersData.findIndex((item) => item.id === orderId);
          if (index !== -1) {
            newOrdersData[index].status = PLANNER_STAGES.RUNNING;
          }

          setOrdersData(newOrdersData);

          setLocalUI({
            ...localUI,
            isPartialFulfilledOrderActionModalOpen: false,
          });
          refreshOverviewMatrix();
        }
      );
    });
    // submit BOM
  };
  const viewRunningOrderDetails = (
    orderId,
    orderStatus,
    orderType,
    orderOrderId
  ) => {
    // setLocalUI({ ...localUI, isPartialFulfilledOrderActionModalOpen: false });
    // navigate(`/material-planner/`);
    navigate(
      `/material-planner-plan/scanning?orderId=${orderId}&orderStatus=${orderStatus}&orderType=${orderType}&orderOrderId=${orderOrderId}`
    );
  };
  const [pageinputValue, setPageinputValue] = useState(page); // for values stored in page number input at the bottom of page
  const handlePageInputChange = (e) => {
    const value = e.target.value;
    if (/^\d*$/.test(value)) {
      // Allow only digits
      setPageinputValue(value);
    }
  };
  const handlePageInputBlur = () => {
    const newPage = Math.ceil(pageinputValue);
    if (!isNaN(newPage) && newPage > 0 && newPage <= pagesCount) {
      setPage(newPage);
    } else {
      setPageinputValue(page); // Revert to current page if input is invalid
    }
  };
  const handleColumnHeadClick = (columnId) => {
    switch (columnId) {
      case "fg_code":
        setIsSortedByCode((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              // add fg_code string to sortingOrder state array as isSortedbyCode is being set true from previous null value
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              // no change in sortingOrder array as sorting by fg_code is still present, just the order is reversed to descending.
              return false;
            case false:
              console.log("a is false");
              // remove fg_code string from sortingOrder state array as isSortedbyCode is being set to null value
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("code case");
        break;
      case "order_type":
        setIsSortedByOrderType((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("order type case");
        break;
      case "status":
        setIsSortedByStatus((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("status case");
        break;
      case "order_id":
        setIsSortedByOrderId((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("order id case");
        break;
      case "name":
        setIsSortedByName((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("order id case");
        break;
      case "quantity":
        setIsSortedByQuantity((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("order id case");
        break;
      default:
        console.log(columnId, "clicked");
    }
  };
  const handleOrderIdFilterInput = (orderIdFilterValue) => {
    console.log("handleOrderIdFilterInput", orderIdFilterValue);
    setOrderIdFilterValue(orderIdFilterValue);
  };
  const handleFgCodeFilterInput = (fgCodeFilterValue) => {
    console.log("handleFgCodeFilterInput", fgCodeFilterValue);
    setFgCodeFilterValue(fgCodeFilterValue);
  };
  const handleOrderTypeFilterInput = (orderTypeFilterValue) => {
    console.log("handleOrderTypeFilterInput", orderTypeFilterValue);
    setOrderTypeFilterValue(orderTypeFilterValue);
  };
  const handleOrderStatusFilterInput = (orderStatusFilterValue) => {
    console.log("handleOrderStatusFilterInput", orderStatusFilterValue);
    setOrderStatusFilterValue(orderStatusFilterValue);
  };
  if (localUI.pageLoading) {
    return (
      <div
        className="mx-3 my-2"
        style={{ display: "grid", placeItems: "center" }}
      >
        <Spin green />
      </div>
    );
  }
  return (
    <div className="mx-3 my-2 material-planner-layout">
      {/* Header   */}
      <div className="material-planner-layout-header">
        <div className="overview-heading">{t('overview')}</div>
        <div className="overview-metrics-container">
          {metricsBoxesState.map((box) => (
            <MetricsBox color={box.color} title={box.title} value={box.value} />
          ))}
          {/* Last refresh time */}
          <div className="overview-last-refresh">
            <div className="leftPart">
              <div>{t('lastRefresh')}</div>
              <div>{lastRefreshTime}</div>
            </div>
            <div className="rightPart">
              {/* MUI icon */}
              <IconButton onClick={refreshOverviewMatrix}>
                <RefreshIcon />
              </IconButton>
            </div>
          </div>
        </div>
      </div>
      {/* Body   */}
      <div className="material-planner-layout-body">
        <Table
          edit={edit}
          columns={COLUMNS(navigate)}
          onTableHeadCellClick={(title) => {
            handleColumnHeadClick(title);
          }}
          rows={ordersData}
          rowsPerPage={resultsPerPage}
          loading={ordersLoading}
          titleCellClasses="c-gray fw-700"
          page={page - 1}
          // paginator
          serialize
          total={ordersCount}
          handleOrderIdFilterInput={(value) => {
            handleOrderIdFilterInput(value);
          }}
          handleFgCodeFilterInput={(value) => {
            handleFgCodeFilterInput(value);
          }}
          handleOrderTypeFilterInput={(value) => {
            handleOrderTypeFilterInput(value);
          }}
          handleOrderStatusFilterInput={(value) => {
            handleOrderStatusFilterInput(value);
          }}
        />
        {/* pagination controls */}
        <div className="pagination-controls">
          <Button
            onClick={() => {
              setPage(page - 1);
              console.log("prev page");
            }}
            white={true}
            width="auto"
            height="auto"
            bordered={true}
            disabled={!isPrevPageAvailable}
            customStyle={{
              border: "1px solid #3E3E3E",
              backgroundColor: !isPrevPageAvailable ? "#CCCCCC" : "#FFFFFF",
              color: !isPrevPageAvailable ? "#999999" : "#000000",
              cursor: !isPrevPageAvailable ? "not-allowed" : "pointer",
            }}
          >
            {t('prev')}
          </Button>
          <div className="page-details">
            <div>{t('page')}</div>{" "}
            <input
              type="number"
              value={pageinputValue}
              onChange={handlePageInputChange}
              onBlur={handlePageInputBlur}
              className="page-number-input"
            />
            <div>of </div>
            <div>
              <strong>{pagesCount}</strong>
            </div>
          </div>
          <Button
            onClick={() => {
              setPage(page + 1);
              console.log("next page");
            }}
            color="green"
            width="auto"
            height="auto"
            disabled={!isNextPageAvailable}
            customStyle={{
              backgroundColor: !isNextPageAvailable ? "#CCCCCC" : "#27ae60",
              color: !isNextPageAvailable ? "#999999" : "#FFFFFF",
              cursor: !isNextPageAvailable ? "not-allowed" : "pointer",
            }}
          >
            {t('next')}
          </Button>
        </div>
      </div>
      {/* Order Details Modal */}
      {isOrderDetailsModalOpen && (
        <OrderDetailsModal
          open={isOrderDetailsModalOpen}
          onClose={() => setIsOrderDetailsModalOpen(false)}
          data={selectedOrderDetailsData}
        />
      )}
      {/* Fully Filled Order Action Modal */}
      {localUI.isFulfilledOrderActionModalOpen && (
        <Modal
          open={localUI.isFulfilledOrderActionModalOpen}
          onClose={() => setLocalUI({ isFulfilledOrderActionModalOpen: false })}
          width="80vw"
          top="10vh"
          hideCloseIcon={crossIcon}
          wrapperClasses="px-5 py-4"
          crossIconClasses={`w-2.5vw w-5vw--mob h-2.5vw h-5vw--mob`}
        >
          <FulfilledOrderActionModal
            data={{
              orderId: localUI.selectedOrderId,
              status: localUI.selectedOrderStatus,
            }}
            noStepFn={handleFulfilledOrderActionModalDenial}
            nextStepFn={moveFulfilledOrderToRunningState}
          />
        </Modal>
      )}
      {/* Partially Filled Order Action Modal */}
      {localUI.isPartialFulfilledOrderActionModalOpen && (
        <Modal
          open={localUI.isPartialFulfilledOrderActionModalOpen}
          onClose={() =>
            setLocalUI({ isPartialFulfilledOrderActionModalOpen: false })
          }
          width="50vw"
          top="20vh"
          hideCloseIcon={crossIcon}
          wrapperClasses="px-5 py-4"
          crossIconClasses={`w-2.5vw w-5vw--mob h-2.5vw h-5vw--mob`}
        >
          <PartialFulfilledOrderActionModal
            data={{
              orderId: localUI.selectedOrderId,
              status: localUI.selectedOrderStatus,
            }}
            noStepFn={editPartiallyFilledOrder}
            nextStepFn={movePartiallyFulfilledOrderToRunningState}
            isPartialFulfilledOrderActionModalEditBtnEnabled={
              localUI.isPartialFulfilledOrderActionModalEditBtnEnabled
            }
          />
        </Modal>
      )}
    </div>
  );
}
const MetricsBox = ({ color, title, value }) => {
  return (
    <div className="overview-metrics-box" style={{ backgroundColor: color }}>
      <p>{title}</p>
      <p>{value}</p>
    </div>
  );
};
export default PlannerOverview;
