import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useUser = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const listUsers = async (page = 1,tab) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.USERS}/?page=${page}&page_size=${10}&is_operator=${tab}`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.USERS}/`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const listByUserName = async (name) => {
    const payload = {'username':name}
    const client = new REQUEST();
    const res = await client.post(
      `search-by-username/?is_operator=0`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    if (res?.data) {
      // console.log("res  ",res.data)
      return res?.data;
    }
    return [[]];
  };

  const listByAadhar = async (name) => {
    const payload = {'aadhar_number':name}
    const client = new REQUEST();
    const res = await client.post(
      `search-by-aadhar/?is_operator=1`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    if (res?.data) {
      // console.log("res  ",res.data)
      return res?.data;
    }
    return [[]];
  };


  const listByAadhar3 = async (name) => {
    const payload = {'aadhar_number':name}
    const client = new REQUEST();
    const res = await client.post(
      `search-by-aadhar/?is_operator=1`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    if (res?.data) {
      // console.log("res  ",res.data)
      const newOptr = res.data.results.filter(item => item.status);
      return newOptr.map(item => ({
      value: item.username,
      key: item.id
    }));
    }
    return [[]];
  };

  const listByAadharFortestPortal = async (name) => {
    const payload = {'aadhar_number':name}
    const client = new REQUEST();
    const res = await client.post(
      `search-by-aadhar/?is_operator=1`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    if (res?.data) {
      // console.log("res  ",res.data)
      const newOptr = res.data.filter(item => item.user.status);
      return newOptr.map(item => ({
      value: item.user.username,
      key: item.user.id
    }));
    }
    return [[]];
  };


  const listByAadhar2 = async (name) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.USERS}/?is_operator=1&page=1&page_size=1000`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      const newOptr = res.data.results.filter(item => item.status);
      return newOptr.map(item => ({
      value: item.username,
      key: item.id
    }));
    }
    return [[], 0];
  };

  const activeOperators = async (name) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.USERS}/?is_operator=1&page=1&page_size=1000`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      const newOptr = res.data.results.filter(item => item.status);
      return newOptr.map(item => ({
      value: item.username,
      key: item.id
    }));
    }
    return [[], 0];
  };

  const usersAll = async (name) => {
    const client = new REQUEST();
    const res = await client.get(
      `users/?page=1&page_size=1000&is_operator=0`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      // const newOptr = res.data.results.filter(item => item.is_operator);
      return res.data.results.map(item => ({
      value: item.username,
      key: item.id
    }));
    }
    return [[], 0];
  };
  const activeUsers = async (name) => {
    const client = new REQUEST();
    const res = await client.get(
      `users/?page=1&page_size=1000&is_operator=0`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      const newOptr = res.data.results.filter(item => item.status);
      return newOptr.map(item => ({
      value: item.username,
      key: item.id
    }));
    }
    return [[], 0];
  };


  const userProfile = async (id) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.USER}-${ENDPOINTS.PROFILE}/${id}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      // console.log("res  ",res.data)
      return res?.data;
    }
    return [[], 0];
  };

  const opratorList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.OPERATORS}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data?.results.length>0) {
      console.log("res  ",res.data)
      return [res?.data.results,res.data.count];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.USERS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User created successfully!")
    );
    return res?.data;
  };
  const createByCsv = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.USERS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null,'')
    );
    return res?.data;
  };

  const updateProfile = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.USER}-${ENDPOINTS.PROFILE}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User Updated successfully!")
    );
    return res?.data;
  };
  
  const updateProfileCSV = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.USER}-${ENDPOINTS.PROFILE}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const updateOpSkill = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.post(
      `training/update-skill/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Skill Updated successfully!")
    );
    return res?.data;
  };

  const createOperator = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.OPERATORS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User created successfully!")
    );
    return res?.data;
  };

  const update = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.USER}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User Updated successfully!")
    );
    return res?.data;
  };

  const updateOperator = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.OPERATOR}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Operator Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.USER}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "User deleted successfully!")
    );
    return res?.data;
  };

  const removeOperator = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.OPERATOR}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "Operator deleted successfully!")
    );
    return res?.data;
  };

  const getNotification = async (name) => {
    const client = new REQUEST();
    const res = await client.get(
      `notifications/user-settings/`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      return res.data
    }
    return [[], 0];
  };

  const postNotification = async (payload) => {
    console.log("notification data-----",payload)
    const client = new REQUEST();
    const res = await client.put(
      `notifications/user-settings/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  return {
    listUsers,
    list,
    usersAll,
    create,
    userProfile,
    updateProfile,
    update,
    remove,
    opratorList,
    createOperator,
    updateOperator,
    removeOperator,
    listByUserName,
    listByAadhar,
    listByAadhar2,
    listByAadhar3,
    listByAadharFortestPortal,
    updateOpSkill,
    createByCsv,
    updateProfileCSV,
    getNotification,
    postNotification,
    activeOperators,
    activeUsers
  };
};

export default useUser;
