import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useTraining from "../../store/training";
import Button from "../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete,Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import AddTraining from "./AddTraining";
import JsonFormatter from "../../components/UI/JsonFormatter";
import useUser from "../../store/users";
import useUserGroup from "../../store/group/user";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";
import moment from "moment/moment";


const TrainingPage = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const divId = location.state
  const [users, setUsers] = useState([]);
  const [plans,setPlans] = useState([])
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    feedback_required:'no',
    test_required:'no'
  });
  const { list:planList, create,update, remove } = useTraining();
  const {groupUser,allUser} = useUserGroup()
  const { list:userList} = useUser();

  useEffect(() => {
    fetchUsers();
    fetchTraining()
  }, [page]);

  const fetchTraining = async()=>{
    const [list] = await planList()
    setPlans(list)
  }

  const fetchUsers = async () => {
    const [_list, _count] =  await allUser(page + 1)
    // const newOptr = _list.filter(item => item.is_operator);
    const newUsers = _list.results.map(item => ({
      value: item.username,
      key: item.id
    }));
    setUsers(prevUsers => [...prevUsers,...newUsers]);
  };

  const handleAdd = async () => { 
    var userids =[]
    if(data.group?.length>0){
      const Profiles = await Promise.all(
        data.group.map(item => groupUser(item))
      );
       userids = Profiles.map((item) => {
        return item.users.map((itm) => {
          return itm.id;
        });
      }).flat()
    }
    const trainees = Array.isArray(data.trainees) ? data.trainees : [];
    const payload = {
      training_name:data.training_name,
      training_type:data.training_type,
      training_description:data.training_description,
      trainer:data.trainer,
      training_admin:data.training_admin,
      trainees:[...trainees, ...userids],
      start_date:data.start_date,
      end_date:data.end_date,
      start_time:data.start_time,
      end_time:data.end_time,
      feedback_link:data.feedback_link,
      test_required:data.test_required==='yes'?true:false,
      feedback_required:data.feedback_required==='yes'?true:false
    } 
    setLoading("fetch");
    let createRes = await create(payload,divId);
    console.log("planning createRes----",createRes)
    setData({});
    setModalOpen(false);
    data.test_required ==='yes'?navigate('/test-upload',{ state: {'id':createRes.id,training_admin:payload.training_admin,trainees:payload.trainees} }):await fetchTraining()
    setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    // await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    // await fetchCompany();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: t('trainingName'),
      render: (rowData) =><Typography
      // onClick={()=> navigate('/asset',{state:rowData.id})}
      >{rowData.training_name}</Typography>,
    },
    {
      id: 2,
      title: t('trainingType'),
      render: (rowData) => rowData.training_type,
    },
    {
      id: 22,
      title: t('trainer'),
      render: (rowData) => rowData.trainer || '-',
    },
    {
      id: 23,
      title: t('startDate'),
      render: (rowData) =>moment(rowData.start_date).format('ll'),
    },
    {
      id: 24,
      title: t('endDate'),
      render: (rowData) =>rowData.end_date? moment(rowData.end_date).format('ll'):'-',
    },
    // {
    //   id: 32,
    //   title: t('details'),
    //   render: (rowData) =>  <JsonFormatter
    //   icon={<Checklist fontSize="small" />}
    //   json={rowData?.metadata}
    // />,
    // },
    // {
    //   id: 5,
    //   title: "Actions",
    //   render: (rowData) => (
    //     <Stack flexDirection={'row'}>
    //     {/* {!rowData?.is_admin &&(
    //     <IconButton
    //       sx={{ height: 25, width: 25 }}
    //       onClick={() => handleUpdate(rowData?.id,rowData?.is_admin)}
    //     >
    //     <AdminPanelSettingsIcon color={rowData.is_admin?"error":"success"} fontSize="small" />
    //     </IconButton>
    //     )} */}
       
    //     <IconButton
    //     sx={{ height: 25, width: 25 }}
    //     onClick={()=>{setEdit(true);setModalOpen("add");setData(rowData)}}
    //   >
    //   <EditIcon color="primary" fontSize="small" />
    //   </IconButton>
    //   <IconButton
    //       sx={{ height: 25, width: 25 }}
    //       onClick={() => handleDelete(rowData?.id)}
    //     >
    //       <Delete color="error" fontSize="small" />
    //     </IconButton>
    //   </Stack>
    //   ),
    // },
  ];

  return (
    <div className="mx-4">
      <div className="flex items-center justify-between">
      <Stack sx={{flexDirection:'row',alignItems:'center',mb:1}}>
      {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
         <Typography className="fw-20-500-36">
       {t('planTraining')}
        </Typography>
        
        </Stack>
        {modalOpen !== "add"&&( 
           <Button color={'gray'} onClick={() => setModalOpen("add")}>
            <Typography className="fw-20-500-24">
            {t('addTraining')}
            </Typography>      
            </Button>)}
      </div>
     
     {modalOpen !== "add"?(
       <Table
        columns={COLUMNS}
        rows={plans}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
        trainings
        tableBodyColor={COLORS.WHITE}
      />
     ):(
      <AddTraining
        users={users}
        open={modalOpen === "add"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
     )}
    </div>
  );
};

export default TrainingPage;
