import React from "react";
import { resolveResponse } from "../../api/utils";
import REQUEST, { CONTENT_TYPES } from "../../api/index";
import { ENDPOINTS } from "../../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "../../";

const useJob = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const matrixCount = async (machineId) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/tasks/${machineId}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const taskList = async (machineId,page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/tasks/${machineId}/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const orderTasks = async (page = 1,orderid) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/order-tasks/${orderid}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  
  const orderAssetList = async (page = 1,orderid) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/asset-list/${orderid}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const machineAssign = async (mapId,payload) => {
    const client = new REQUEST();
    const res = await client.patch(
      `machining/machine-assignment/${mapId}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const workOrderTask = async (Id,page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/order-tasks/${Id}/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const OrderMaterialList = async (Id,page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/circuit-bom/${Id}/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const plants = async (page = 1,companyId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/company/${companyId}/plants/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const divisions = async (page = 1,plantId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/plant/${plantId}/divisions/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const getMachineStatus = async (machineId) => {
    const client = new REQUEST();
    const res = await client.get(
      `/machining/get-machines-latest-info/${machineId}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };



  const create = async (id,payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `machining/circuit-material-map/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const updateOrderStatus = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.patch(
      `/machining/update-running-status/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };
  const update = async (failid,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `orders/update-material-failure/${failid}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  return {
    matrixCount,
    taskList,
    orderTasks,
    orderAssetList,
    machineAssign,
    workOrderTask,
    OrderMaterialList,
    updateOrderStatus,
    getMachineStatus,
    // summary,
    // plants,
    // divisions,
    // lines,
    create,
    update
  };
};

export default useJob;
