import React from "react";
import Chart from "react-apexcharts";

const RadialBarChart = ({total,completed}) => {

  // let percentage = Math.min(100, Math.floor((completed / total) * 100));
  // const percentage = (completed / total) * 100;
  let percentage = Math.min(100, ((completed / total) * 100).toFixed(2));

  const options = {
    chart: {
      height: 350,
      type: "radialBar",
      offsetY: -10,
    },
    plotOptions: {
      radialBar: {
        startAngle: -135,
        endAngle: 135,
        dataLabels: {
          name: {
            fontSize: "16px",
            offsetY: 120,
          },
          value: {
            offsetY: 76,
            fontSize: "22px",
            formatter: function (val) {
              return val + "%";
            },
          },
        },
      },
    },
    fill: {
      type: "gradient",
      gradient: {
        shade: "dark",
        shadeIntensity: 0.15,
        inverseColors: false,
        opacityFrom: 1,
        opacityTo: 1,
        stops: [0, 50, 65, 91],
      },
    },
    stroke: {
      dashArray: 4,
    },
    labels: [""],
  };

  const series = [percentage];

  return (
    <div id="chart">
      <Chart options={options} series={series} type="radialBar" height={350} />
    </div>
  );
};

export default RadialBarChart;
