import React, { useEffect, useState } from "react";
import { IconButton, Stack, Tab, Tabs,Grid, Typography } from "@mui/material";
import CardComponent from "./CardComponent";
import "./Card.css";
import Table from "../../components/table";
import { useTranslation } from 'react-i18next';
import { COLORS } from "../../helpers/constants";
import { useNavigate } from "react-router-dom";
import useMachineMonitoring from '../../store/machineMonitoring'
import { styled } from '@mui/material/styles';
import Switch from '@mui/material/Switch';

import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { BorderColor } from "@mui/icons-material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { t } from "i18next";


const CardGrid = () => {
  const { t, i18n } = useTranslation();
  const navigate = useNavigate()
  const {summary,companies,plants,divisions,lines} = useMachineMonitoring()
  const [expandedCard, setExpandedCard] = useState(null);
  const [tab,setTab] = useState(false)
  const [page,setPage] = useState(0)
  const [total, setTotal]= useState(0)
  const [loading, setLoading] = useState();
  const [machineData,setMachineData] = useState([{
    email:'email@gmail.com',
    username:'user',
    fullname:'fullName',
    employee_id:'employee_id',
  }])
  
    const [errors, setErrors] = useState(new Map());
    const [data, setData] = useState({
    "company_id": "",
    "plant_id": "",
    "division_id": "",
    "line_id": "",
    "date": new Date().toISOString().split('T')[0],
    "shift_id": ""
    });
    const [company , setCompany] = useState([{
      value: 'All',
      key: ''}
    ])
    const [plant , setPlant] = useState([{
      value: 'All',
      key: ''}])
    const [division, setDivision] = useState([{
      value: 'All',
      key: ''}])
    const [line, setLine] = useState([{
      value: 'All',
      key: ''}])

    const [dailySummary, setDailySummary] = useState([])

    const Filters = [
      {
        // title: t('operator'),
        placeholder:t('company'),
        type: SCHEMA_TYPES.SELECT,
        name: 'company_id',
        // validator: VALIDATORS.REQUIRED,
        width:'150px',
        keyValues:company
     },
      {
        // title: t('test'),
        placeholder: t('plant'),
        type: SCHEMA_TYPES.SELECT,
        name: 'plant_id',
        // validator: VALIDATORS.REQUIRED,
        width:'150px',
        keyValues:plant,
      },
      {
        // title: t('test'),
        placeholder: t("division"),
        type: SCHEMA_TYPES.SELECT,
        name: 'division_id',
        validator: VALIDATORS.REQUIRED,
        width:'150px',
        keyValues:division,
      },
      {
        // title: t('test'),
        placeholder: t("line"),
        type: SCHEMA_TYPES.SELECT,
        name: 'line_id',
        validator: VALIDATORS.REQUIRED,
        width:'150px',
        keyValues:line,
      },
      {
        // title: t('test'),
        placeholder: t("status"),
        type: SCHEMA_TYPES.SELECT,
        name: 'state',
        // validator: VALIDATORS.REQUIRED,
        width:'150px',
        keyValues:[{key:'',value:'All'},{key:1,value:'Online'},{key:2,value:'Offline'},{key:3,value:'Downtime'}],
      },
      {
        // title: t('test'),
        placeholder: t("date"),
        type: SCHEMA_TYPES.DATE,
        name: 'date',
        // validator: VALIDATORS.REQUIRED,
        width:'150px',
        // keyValues:testList,
      },
     
    ];

    // useEffect(()=>{
    //   allSumary()
    // },[data.company_id,data.plant_id,data.division_id,data.line_id,data.date,data.shift_id,data.state])

 

useEffect(() => {
  // Call allSumary() immediately
  allSumary();

  // Set up interval to call allSumary() every 5 seconds
  const interval = setInterval(() => {
    allSumary();
  }, 10000);

  // Cleanup function to clear interval when dependencies change or component unmounts
  return () => clearInterval(interval);
}, [data.company_id,tab, data.plant_id, data.division_id, data.line_id, data.date, data.shift_id, data.state]);


  async function allSumary() {
      let [res] = await summary(page+1,tab===false?50:10, data.company_id ||'', data.plant_id ||'', data.division_id ||'', data.line_id ||'', data.date, data.shift_id || '',data.state ||'');

      console.log("machine res-------",res)
      // if(res.length>0){
        const addUniqueItems = (currentItems, newItems) => {
          const existingKeys = new Set(currentItems.map(item => item.key));
          return newItems.filter(item => !existingKeys.has(item.key));
        };
    if(res?.companies?.length>0){
      setCompany(prevUsers => [
        ...prevUsers,
        ...addUniqueItems(prevUsers, res?.companies.map(item => ({
          value: item.name,
          key: item.id,
        }))),
      ]);
    }
    if(res?.plants?.length>0){
      setPlant(prevUsers => [
        ...prevUsers,
        ...addUniqueItems(prevUsers, res?.plants.map(item => ({
          value: item.name,
          key: item.id,
        }))),
      ]);
    }
      if(res?.divisions?.length>0){
      setDivision(prevUsers => [
        ...prevUsers,
        ...addUniqueItems(prevUsers, res?.divisions.map(item => ({
          value: item.name,
          key: item.id,
        }))),
      ]);
    }
      if(res?.lines?.length>0){
      setLine(prevUsers => [
        ...prevUsers,
        ...addUniqueItems(prevUsers, res?.lines.map(item => ({
          value: item.name,
          key: item.id,
        }))),
      ]);
    }
    
      if (res?.summaries?.results?.length >= 0) {
        // const updatedResults = res.summaries.results.map(item => ({
        //   ...item,
        //   status: "Online", // Add the desired default status value
        // }));
        setDailySummary(res.summaries.results);
        setTotal(res.summaries.count)
      }
    
      setData(prev => ({ ...prev, company: company[0]?.key }));
  }

    

 async function allCompanies(){
   let [res] = await companies(page+1)
  //  console.log("res------------",res)
   const comp = res.map(item => ({
    value: item.name,
    key: item.id
  }));
  setCompany(prevUsers => [...prevUsers, ...comp]);
  if (comp.length > 0) {
    setData((prev) => ({ ...prev, company: comp[0].key }));
    await allPlants(comp[0].key)
  }
  }

 async function allPlants(companyId){
   let [res] = await plants(page+1,companyId)
   const comp = res.map(item => ({
    value: item.name,
    key: item.id
  }));
  // console.log("res-------comp[0].key-----",comp[0].key)
  setPlant(prevUsers => [...prevUsers, ...comp]);
  if (comp.length > 0) {
    setData((prev) => ({ ...prev, plants: comp[0].key }));
    await allDivisions(comp[0].key)
  }
  }  

 async function allDivisions(plantId){
   let [res] = await divisions(page+1,plantId)
   const comp = res.map(item => ({
    value: item.name,
    key: item.id
  }));
  console.log("res-------allDivisions-----",comp[0].key)
  setDivision(prevUsers => [...prevUsers, ...comp]);
  if (comp.length > 0) {
    setData((prev) => ({ ...prev, division: comp[0].key }));
    await allLines(comp[0].key )
  }
  }  
 async function allLines(divisionId){
   let [res] = await lines(page+1,divisionId)
   const comp = res.map(item => ({
    value: item.name,
    key: item.id
  }));
  console.log("res-------alllines-----",comp[0].key)
  setLine(prevUsers => [...prevUsers, ...comp]);
  if (comp.length > 0) {
    setData((prev) => ({ ...prev, line: comp[0].key }));
  }
  }  


  const handleCardToggle = (index) => {
    setExpandedCard(expandedCard === index ? null : index);
  };

  const COLUMNS = [
    {
      id: 1,
      title: t('machineId'),
      render: (rowData) => rowData.asset_name,
    },
    {
      id: 2,
      title: t('status'),
      render: (rowData) => <Typography sx={{textTransform:'capitalize'}} color={rowData.status==='offline'?'#FF0000':rowData.status==='online'?'#27AE60':'#FAA036'}>{rowData.status}</Typography>,
    },
    {
      id: 3,
      title: 'OEE',
      render: (rowData) => rowData.oee+ '%',
    },
    {
      id: 4,
      title: t('availability'),
      render: (rowData) => rowData.availability + '%',
    },
    {
      id: 5,
      title: t('performance'),
      render: (rowData) => rowData.utilization + '%',
    },
    {
      id: 6,
      title:t('qty'),
      render: (rowData) => rowData.quality,
    },
    {
      id: 7,
      title: t('action'),
      render: (rowData) => <Typography sx={{color:COLORS.LIGHT_BLUE,cursor:'pointer'}} onClick={()=>ChildFun(rowData)}>Details</Typography>,
    },
  ];

  
  function ChildFun(data){
    navigate('/machines',{state:{data:data,tab:tab}})
  }

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log("name value", name, value )
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value ==0?'':value}));
  };
  


     const AntSwitch = styled(Switch)(({ theme }) => ({
      width: 28,
      height: 16,
      padding: 0,
      display: 'flex',
      '&:active': {
        '& .MuiSwitch-thumb': {
          width: 15,
        },
        '& .MuiSwitch-switchBase.Mui-checked': {
          transform: 'translateX(9px)',
        },
      },
      '& .MuiSwitch-switchBase': {
        padding: 2,
        '&.Mui-checked': {
          transform: 'translateX(12px)',
          color: '#fff',
          '& + .MuiSwitch-track': {
            opacity: 1,
            backgroundColor: '#FAA036',
            // borderColor: '#0000',
            border:2,
            ...theme.applyStyles('dark', {
              backgroundColor: '#FAA036',
            }),
          },
        },
      },
      '& .MuiSwitch-thumb': {
        // boxShadow: '0 2px 4px 0 rgb(0 35 11 / 20%)',
        width: 12,
        height: 12,
        borderRadius: 6,
        // borderColor: '2px solid #0000',
        transition: theme.transitions.create(['width'], {
          duration: 200,
        }),
      },
      '& .MuiSwitch-track': {
        borderRadius: 16 / 2,
        opacity: 1,
        backgroundColor: 'rgba(0,0,0,.25)',
        boxSizing: 'border-box',
        ...theme.applyStyles('dark', {
          backgroundColor: 'rgba(255,255,255,.35)',
        }),
      },
    }));


    const tempCard = [{
      "id": "caf3e9d9-0ddf-4a0c-a8eb-9ee6c125714a",
      "asset_name": "MACHINE 1",
      "quality": 100,
      "oee": 3.6,
      "date": "2025-01-14",
      "availability": 0.1,
      "utilization": 100,
      "total_good": 300,
      "total_ng": 0,
      "total_pass": 300,
      "total_fail": 0,
      "best_cycle_time": 0.06,
      "worst_cycle_time": 0.06,
      "avg_cycle_time": 0.06,
      "total": 300
  },
  {
    "id": "caf3e9d9-0ddf-4a0c-a8eb-9ee6c125712a",
    "asset_name": "MACHINE 1",
    "quality": 100,
    "oee": 3.6,
    "date": "2025-01-14",
    "availability": 0.1,
    "utilization": 100,
    "total_good": 300,
    "total_ng": 0,
    "total_pass": 300,
    "total_fail": 0,
    "best_cycle_time": 0.06,
    "worst_cycle_time": 0.06,
    "avg_cycle_time": 0.06,
    "total": 300
},
{
  "id": "caf3e9d9-0ddf-4a0c-a8eb-9ee6c125734a",
  "asset_name": "MACHINE 1",
  "quality": 100,
  "oee": 3.6,
  "date": "2025-01-14",
  "availability": 0.1,
  "utilization": 100,
  "total_good": 300,
  "total_ng": 0,
  "total_pass": 300,
  "total_fail": 0,
  "best_cycle_time": 0.06,
  "worst_cycle_time": 0.06,
  "avg_cycle_time": 0.06,
  "total": 300
},
{
  "id": "caf3e9d9-0ddf-4a0c-b8eb-9ee6c125714a",
  "asset_name": "MACHINE 1",
  "quality": 100,
  "oee": 3.6,
  "date": "2025-01-14",
  "availability": 0.1,
  "utilization": 100,
  "total_good": 300,
  "total_ng": 0,
  "total_pass": 300,
  "total_fail": 0,
  "best_cycle_time": 0.06,
  "worst_cycle_time": 0.06,
  "avg_cycle_time": 0.06,
  "total": 300
}]
  return (
    <div className="mx-3 my-2">
      <Stack sx={{flexDirection:'row',justifyContent:'space-between'}}>
      <Typography className="fw-20-500-36">
      {t('machineMonitoring')}
      </Typography>
      <Stack sx={{flexDirection:'row',gap:1}}>
      <Stack direction="row" spacing={1} sx={{ alignItems: 'center' }}>
        <Typography className="fw-16-400-36">{t('kanbanView')}</Typography>
        <AntSwitch defaultChecked={tab} inputProps={{ 'aria-label': 'ant design' }} onChange={(e)=>setTab(e.target.checked)} />
        <Typography className="fw-16-400-36">{t('listView')}</Typography>
      </Stack>
      </Stack>
      </Stack>

      <Stack sx={{flexDirection:'row',gap:1,mb:1}}>
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={Filters}
        />
      </Stack>
      {/* <Tabs value={tab} onChange={(_e, v) => { setTab(v); setPage(0);  }} className="mb-2 fw-500">
        {['Kanban', 'Table'].map((label, index) => (
          <Tab label={label} className="fw-500" value={label} />
        ))}
      </Tabs> */}
      {tab===false?(
      // <Stack sx={{backgroundColor:'yellow',overflow:'scroll'}}>
      <Grid 
      // display={'grid'} 
      container
      spacing={1.5}
      sx={{
        // gridTemplateColumns: { sm: 'repeat(3, 1fr)', xs: 'repeat(3, 1fr)' },
        // gridAutoRows: 'auto', // Automatically adjust row height based on content
        // gap: 2, // Adds spacing between grid items
        // height: 'auto', // Allow the grid itself to grow dynamically
        mb:50
      }}
       >
        {dailySummary.length>0?dailySummary.map((data, index) => (
           <Grid item size={4}>
            <div key={index} style={{ position: "relative", height: 'auto',width: "430px" }}>
              <CardComponent
                data={data}
                expanded={expandedCard === index}
                onToggle={() => handleCardToggle(index)}
                clickMore={()=>ChildFun(data)}
              />
              </div>
            </Grid>
        )):(
          <Stack sx={{justifyContent:'center',alignItems:'center',mt:2,width:'100%'}}>

          <Typography>{t('machineNotFound')} !</Typography>
          </Stack>
        )
        }
      </Grid>
      // </Stack>  
      ):(
        <Table
          columns={COLUMNS}
          rows={dailySummary}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          paginator
          serialize
          total={total}
          tableBodyColor={COLORS.WHITE}
        />
      )}
    </div>
  );
};

export default CardGrid;