import { Box, Stack, Typography } from "@mui/material";
import React, { useState } from "react";
import Button from "../../components/button";
import Modal from "../../components/modal";
import { COLORS } from "../../helpers/constants";
import { AttachFile, CloudUpload, Upload } from "@mui/icons-material";
import Papa from 'papaparse';
import styled from "@emotion/styled";
import sampleCSV from "../../assets/SampleSheet.csv"
import csvUser from "../../assets/userCsv.csv"
import operatorCsv from "../../assets/operatorCsv.csv"
import mcq from "../../assets/mcq.csv"
import crossIcon from '../../assets/svg/cross-icon.svg'
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const VisuallyHiddenInput = styled("input")({
  clip: "rect(0 0 0 0)",
  clipPath: "inset(50%)",
  height: 1,
  overflow: "hidden",
  position: "absolute",
  bottom: 0,
  left: 0,
  whiteSpace: "nowrap",
  width: 1,
});

const UploadCsv = ({ file, setFile, loading, open, onClose, onSubmit,setCsvData,tab }) => {
  const [error, setError] = useState(false);

  const onFileChange = async(e) => {
    const _file = e.target.files[0];
    console.log("file-----",_file)
    if (!_file) return;
    if (!_file?.type?.toLowerCase()?.includes("csv")) {
      setError("The selected file is not a csv file!");
    } else {
      setError(false);
      Papa.parse(_file, {
        header: false ,
        complete: function(results) {
          console.log("csv res-----------",results?.data);
          setCsvData(results?.data)
        }
      });
      setFile(_file);
    }
  };

  function downloadSample(){
    window.open(tab===0?csvUser:tab===1?operatorCsv:tab==="question"?mcq:sampleCSV,'_blank')
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(500px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{t('uploadCsv')} </h3>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        my={3}
      >
        {/* <p className="c-gray">Need assistance?</p> */}
        <Button bordered color="orange" height="30px" onClick={()=>downloadSample()}>
         {t('downloadSampleCSV')}
        </Button>
      </Stack>
      <Box
        className="h-400 w-full rounded-10 flex flex-col items-center justify-center"
        sx={{
          border: `2px dotted ${COLORS.GRAY}`,
          // border: `2px dotted ${error ? COLORS.RED : COLORS.PRIMARY}`,
          // background: `${error ? COLORS.RED_OPAQUE : COLORS.BLUE_OPAQUE}`,
        }}
      >
        <CloudUpload fontSize="large" color="disabled" />
        <h3 className="opacity-80">{t('dropYourFileHereToUpload')}</h3>
        {!!file && (
          <p
            className={`${
              error ? "c-red" : "c-primary"
            } flex items-center`}
          >
            <AttachFile fontSize="small"  /> {file?.name}
          </p>
        )}
        <Button onClick={(e) => document.getElementById("file").click()}>
          <Upload /> {t('uploadCsvFile')}
        </Button>
        <VisuallyHiddenInput type="file" id="file" onChange={onFileChange} />
      </Box>
      {!!error && <p className="fw-500 c-red text-center">{error}</p>}
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        my={2}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} disabled={error || !file} onClick={onSubmit} loading={loading}>
         <Typography className="fw-20-500-24">
         {t('create')}
          </Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default UploadCsv;
