import { Box, Typography, useTheme ,Stack} from '@mui/material';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

const AnswerBox = ({ highlightAnswer, children, ...props }) => {
    const theme = useTheme();
    return (
      <Box
        {...props}
        sx={{
          width:'100%',
          fontSize: {
            xs: '18px',
            sm: 'calc(18px + 2vw)',
            md: 'calc(18px + 4vw)',
            lg: '16px'
          },
          color: theme.palette.text.secondary,
          fontWeight: highlightAnswer ? 400 : 300,
          border: `1px solid ${highlightAnswer ? theme.palette.primary.main : theme.palette.divider}`,
          backgroundColor: highlightAnswer ? theme.palette.background.selected : theme.palette.background.paper,
          borderRadius: '16px',
          marginTop: '8px',
          cursor: 'pointer',
          transition: highlightAnswer ? 'border 0.2s ease-in' : 'none',
          '& input': {
            visibility: 'hidden',
            margin: 0,
          }
        }}
      >
        {children}
      </Box>
    );
  };

const Answer = ({ onChange, index, choice, type,correct }) => {
  // Convert index to alphabet character to show ABCD before question
  const label = String.fromCharCode(65 + index)
  const [selectedAnswer,setSelectedAnswer] = useState('')

  const handleChangeAns =(e)=>{
    console.log("answer --",selectedAnswer ,choice)
    setSelectedAnswer('')
    setSelectedAnswer(e)
  }

  return (
    <Stack sx={{flexDirection:'row',alignItems:'center',gap:1}}>
    <Typography sx={{fontWeight:correct === true?'bold':''}}>{label}.</Typography>
    <AnswerBox key={index}
     highlightAnswer={correct === true?"21":''}
     >
      <Stack>
      <Typography sx={{p:1,display:'flex',fontWeight:correct === true?'bold':''}} onClick={()=>onChange(choice)}>
     
       {/* <input
          name={choice}
          // radio is for checked one option and checkbox is for checked multiple options
          type={'radio'}
        //   checked={selectedAnswer.includes(choice)}
          onChange={(e)=>handleChangeAns(e)}
        /> */}
        {choice}
      </Typography>
      </Stack>
    </AnswerBox>
    </Stack>
  )
}

export default Answer;
