import React from 'react'
import './DetailsCard.css'

const DetailsCard = () => {
    return (
      <div className="report-container">
        <div className="report-details-t">
          <div className="grid-container">
            <div className="font-medium">Part Name</div>
            <div className="font-semibold">SSKSKK2</div>
            
            <div className="font-medium">Customer</div>
            <div className="font-semibold">Mars Motors</div>
            
            <div className="font-medium">Date of Manufacture</div>
            <div className="font-semibold">12 Aug 2024</div>
            
            <div className="font-medium">Plant</div>
            <div className="font-semibold">Plant no-32</div>
          </div>
        </div>
  
        <div className="report-actions">
          <button className="action-button">Print Report</button>
          <button className="action-button">Share Report</button>
        </div>
      </div>
    );
  };
  

export default DetailsCard
