import React, { useState, useEffect, useLayoutEffect } from "react";
import { useSelector } from "react-redux";
import Modal from "../../components/modal";
import { Stack, Typography } from "@mui/material";
import CancelIcon from '@mui/icons-material/Cancel';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Button from "../../components/button";
import useMachineGroup from "../../store/group/machine";
import useOperatorGroup from "../../store/group/operator";
import useUserGroup from "../../store/group/user";
import { COLORS, SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { REDUCER_TYPES } from "../../store";
import { setToken } from "../../store/api/utils";
import { useTranslation } from 'react-i18next';
import useAuth from "../../store/auth";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { toast } from "react-toastify";
import { t } from "i18next";

import gifFile from '../../assets/gif/file.gif'

const Uploading = ({ open, onClose, data, setData, onSubmit, modalOpen, loading, edit, tab }) => {
  const { t, i18n } = useTranslation();
  const { getMenu, postMenu, getMachines, postMachine } = useAuth()
  const userData = useSelector((state) => state?.USER_ITEM?.data)
  const [userMenu, setUserMenu] = useState([])
  const [allMenu, setAllMenu] = useState([])

  return (
    <Modal
      open={open}
      onClose={onClose}
      none={'none'}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80" style={{ textTransform: 'capitalize' }}> File is uploading.</h3>
      <Stack sx={{ justifyContent: 'center', alignItems: 'center' }}>
        <Typography color={'error'} fontWeight={"bold"}>Please do not close or refresh the page. This window will close automatically once the upload is complete.</Typography>
        <img src={gifFile} alt="uploading" style={{ width: 300, height: 300, objectFit: 'contain' }} />
      </Stack>
    </Modal>
  );
};

export default Uploading;
