import React, { useRef, useState, useEffect } from "react";
import { t } from "i18next";
import Button from "../../../components/buttonn"; // Adjust the import path as needed

const StockTransferScannerModal = ({
  onClose,
  onScan,
  noStepFn,
  nextStepFn,
}) => {
  const [barcode, setBarcode] = useState(null);
  const inputRef = useRef(null);

  useEffect(() => {
    inputRef.current.focus(); // Automatically focus the input field when the component mounts
  }, []);

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      console.log("Scanned Barcode:", e.target.value);
      setBarcode(e.target.value);
      onScan(e.target.value);
      e.target.value = ""; // Clear the input for the next scan
    }
  };

  return (
    <div className="action_modal__container">
      <p style={{ fontWeight: "600", fontSize: "1.2rem" }}>Stock Transfer</p>
      <p style={{ fontWeight: "600", fontSize: "1.1rem" }}>
        Please scan the barcode of the substitute material to proceed.
      </p>
      <div>
        <input
          ref={inputRef}
          type="text"
          onKeyPress={handleKeyPress}
          style={{ width: "100%", padding: "10px", fontSize: "16px" }}
          placeholder="Scan or type available barcode here"
        />
      </div>
      {barcode && <p>Scanned: {barcode}</p>}
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
        }}
      >
        <Button
          onClick={noStepFn}
          white={true}
          width="8rem"
          height="2.5rem"
          bordered={true}
          customStyle={{
            border: "1px solid #3E3E3E",
            backgroundColor: "#FFFFFF",
          }}
        >
          {t('cancel')}
        </Button>
        <Button
          onClick={nextStepFn}
          color="green"
          width="8rem"
          height="2.5rem"
          disabled={barcode===null}
        >
          {t('submit')}
        </Button>
      </div>
    </div>
  );
};

export default StockTransferScannerModal;
