import React, { useRef, useState, useEffect } from "react";
import { t } from "i18next";
import Button from "../../../components/buttonn"; // Adjust the import path as needed
import { useScanning } from "../../../store/materialPlanner";

const FailureReasonSubmittedModal = ({
  onClose,
  onScan,
  noStepFn,
  nextStepFn,
}) => {
  return (
    <div className="action_modal__container">
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "1rem",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <p>Failure Reason has been submitted successfully!</p>
        <p>An email has also been sent to the supervisor regarding this.</p>
        <Button onClick={nextStepFn} color="green" width="8rem" height="2.5rem">
          {t('ok')}
        </Button>
      </div>
    </div>
  );
};

export default FailureReasonSubmittedModal;
