import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useQuestion = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const result = async (id) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/update-test-results/${id}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const induction_question = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/induction-questions/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const hr_question = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/miscellaneous-questions/`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };


  const question_list = async (page = 1,page_size,type,skill) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/question-bank/?page=${page}&page_size=${page_size}&type=${type}&skill=${skill}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const skill_question_list = async (page = 1,skillId) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/skill-questions/${skillId}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const test_list = async (page = 1,skillId) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/test-masters/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const skill_list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/skills/?page=${page}&page_size=100`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const get_Test = async (id) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/test-instance/${id}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };


  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `training/question-bank/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const createTest = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `training/test-masters/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  

  const assignExam = async (data) => {
    const client = new REQUEST();
    const res = await client.post(
      `training/${data.test}/test-instance/${data.operator.key}/`,
      {},
      headers,
      (res) => resolveResponse(res, null,"Test Successfully Assign !")
    );
    return res?.data;
  };

  const linkPlanTestMaster = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `training/training/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };
  const addQuestion = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `training/test-master/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Test Updated Successfully!")
    );
    return res?.data;
  };

  const updateTest = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `training/test-instance/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const updateProfile = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.USER}-${ENDPOINTS.PROFILE}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.USER}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "User deleted successfully!")
    );
    return res?.data;
  };

  return {
    question_list,
    skill_question_list,
    skill_list,
    induction_question,
    hr_question,
    result,
    get_Test,
    test_list,
    createTest,
    assignExam,
    addQuestion,
    updateTest,
    create,
    updateProfile,  
    remove,
    linkPlanTestMaster
  };
};

export default useQuestion;
