import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";
import { Place } from "@mui/icons-material";

const useMatrix = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (page = 1,companyId) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/${companyId}/skill-matrix/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return res?.data;
    }
    return [[], 0];
  };

  const skillMatrixList = async (page = 1,companyId) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/all-skill-matrix/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return res?.data;
    }
    return [[], 0];
  };

  const performanceList = async (data) => {
    const date =  data.replace(/-/g, '');
    const client = new REQUEST();
    const res = await client.get(
      `training/date-wise-performance-record/${date}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return res?.data;
    }
    return [[], 0];
  };

  const create = async (payload = {},companyId) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.ASSET}/${companyId}/${ENDPOINTS.PLANTS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Plant created successfully!")
    );
    return res?.data;
  };

  const postPerformance = async (payload) => {
    const client = new REQUEST();
    const res = await client.post(
      `performance-record/${payload.operator}/`,
      {skill:payload.skill,skill_level:payload.skill_level},
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const getPerformanceSkill = async (payload) => {
    const client = new REQUEST();
    const res = await client.get(
      // `training/${payload.operator}/performance-record/`,
      `training/update-skill/${payload.operator}/`,
      {},
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const getPerformanceId = async (payload) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/${payload.operator}/performance-record/`,
      {},
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const performanceRecord = async (payload) => {
    const client = new REQUEST();
    const res = await client.post(
      `training/${payload.operator}/performance-record/`,
      {skill:payload.skill,skill_level:payload.skill_level},
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };


  const putPerformanceRecord = async (skill_id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `training/performance-record/${skill_id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null,"Performance uploaded.")
    );
    return res?.data;
  };


  const update = async (payload) => {
    const newPayload ={
      "name":payload.name,
      "metadata":payload.metadata
    }
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.PLANT}/${payload.id}/`,
      newPayload,
      headers,
      (res) => resolveResponse(res, null, "Plant Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.PLANT}/${id}/`,
      headers,
      (res) => resolveResponse(res, null, "Plant deleted successfully!")
    );
    return res?.data;
  };



  return {
    skillMatrixList,
    list,
    create,
    update,
    remove,
    postPerformance,
    getPerformanceSkill,
    performanceRecord,
    putPerformanceRecord,
    performanceList,
    getPerformanceId
  };
};

export default useMatrix;
