import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography ,Grid} from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import useUser from "../../store/users";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const AddSupervisor = ({ open, onClose, data, setData, onSubmit, loading ,user}) => {
  const [errors, setErrors] = useState(new Map());

  const addSupervisor =[
    {
      title: t('supervisor'),
      placeholder: "Select Supervisor",
      type: SCHEMA_TYPES.SELECT,
      name: "supervisor",
      multi:true,   
      height:'auto',
      keyValues:user
      // keyValues:[{value:'super A',key:"super A"},{value:"super B",key:"super B"},{value:"super C",key:"super C"}]
    }
  ]

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">{t('addSupervisor')}</h3>

      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={addSupervisor}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
      <Button bordered gray onClick={onClose}>
          {t('cancel')}
      </Button>
      <Button onClick={onSubmit} loading={loading}>
          {t('create')}
      </Button>
      </Stack>
    </Modal>
  );
};

export default AddSupervisor;
