import * as React from 'react';
import PropTypes from 'prop-types';
import LinearProgress from '@mui/material/LinearProgress';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { Stack } from '@mui/material';
import { t } from "i18next";

function LinearProgressWithLabel({ value, type }) {
  const getBarColor = () => {
    if (type === "Availability") return "#FF0000";
    if (type === "Performance") return "#FAA036";
    if (type === "Quality") return "#27AE60";
    return "#000000"; // Default fallback color
  };

  return (
    <Box sx={{ display: 'flex',flexDirection:'row', alignItems: 'center',justifyContent:'space-between',px:2 ,gap:1}}>
      <Stack sx={{width:120 }}>
        <Typography className='LinearProgress-title'>
        {type==="Availability"?t('availability'):type==="Performance"?t('performance'):t('quality')}
        </Typography>
      </Stack>
      <Stack sx={{width:180 }}>
        <LinearProgress
          variant="determinate"
          value={value>100?100:value}
          sx={{
            backgroundColor: '#8C8C8C',
            '& .MuiLinearProgress-bar': {
              backgroundColor: getBarColor(),
            },
            height: "12px",
            // marginTop: "1px",
            // width: "170px",
            // marginLeft: "-60px"
          }}
        />
      </Stack>
      <Stack sx={{width:40}}>
        <Typography className='LinearProgress-title' sx={{fontWeight:'bold'}}>
        {`${Math.round(value)}%`}
        </Typography>
      </Stack>
    </Box>
  );
}

LinearProgressWithLabel.propTypes = { 
  value: PropTypes.number.isRequired,
  type: PropTypes.string.isRequired,
};

export default function LinearWithValueLabel({ progress, type }) {
  return (
    <Box sx={{ width: '100%' }}>
      <LinearProgressWithLabel value={progress} type={type} />
    </Box>
  );
}

LinearWithValueLabel.propTypes = {
  progress: PropTypes.number.isRequired,
  type: PropTypes.string.isRequired,
};
