import { resolveResponse } from "../../api/utils";
import REQUEST, { CONTENT_TYPES } from "../../api";
import { ENDPOINTS } from "../../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "../..";
import { useState, useCallback } from "react";

const useScanning = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const apiRequest = new REQUEST();

  const fetchMaterialsRequired = useCallback(
    async (id) => {
      const route = `${ENDPOINTS.ORDERS}/materials-required/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in fetching orders by id");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const setOrderHoldQuantity = useCallback(
    async (id, body = {}) => {
      const route = `${ENDPOINTS.ORDERS}/material-on-hold/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(
          error.message || "Error in setting order on hold quantity"
        );
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const updateOrderState = useCallback(
    async (id, state) => {
      const route = `${ENDPOINTS.ORDERS}/update-state/${id}/`;
      const body = {
        state: state,
      };
      try {
        setLoading(true);
        const response = await apiRequest.put(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in submitting BOM");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );
  const generateLabels = useCallback(
    async (material_code, grn_number, grn_date, unit, qty1, qty2) => {
      const route = `${ENDPOINTS.ORDERS}/label-generator/`;
      const body = {
        material_code: material_code,
        grn_number: grn_number,
        grn_date: grn_date,
        unit: unit,
        qty1: qty1,
        qty2: qty2,
      };
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in submitting BOM");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const getFailureReasons = useCallback(async () => {
    const route = `${ENDPOINTS.ORDERS}/material-failure-reasons/`;
    try {
      setLoading(true);
      const response = await apiRequest.get(route, {}, headers);
      return response.data;
    } catch (error) {
      console.error(error.message || "Error in getting Failure Reasons");
    } finally {
      setLoading(false);
    }
  }, [apiRequest, headers]);

  const submitFailureReason = useCallback(
    async (
      order_id,
      material_code,
      failure_reason_id,
      resolution_date,
      comments = "Fake comment"
    ) => {
      const route = `${ENDPOINTS.ORDERS}/material-failure-entry/${order_id}/`;
      const body = {
        material_code: material_code,
        failure_reason_id: failure_reason_id,
        comments: comments,
        resolution_date: resolution_date,
      };
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in submitting Failure Reason");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const supervisorLogin = useCallback(
    async (username, password) => {
      const route = `${ENDPOINTS.AUTH}/supervisor-authentication/`;
      const body = {
        username: username,
        password: password,
      };
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in supervisor login");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const stockTransfer = useCallback(
    async (
      material_code1,
      material_code2,
      quantity,
      grn_number,
      unit,
      grn_date
    ) => {
      const route = `${ENDPOINTS.ORDERS}/stock-transfer/`;
      const body = {
        material_code1: material_code1,
        material_code2: material_code2,
        quantity: quantity,
        grn_number: grn_number,
        unit: unit,
        grn_date: grn_date,
      };
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in Stock Transfer");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const getKittingLabel = useCallback(
    async (orderId) => {
      const route = `${ENDPOINTS.ORDERS}/kitting-qr/${orderId}/`;
      try {
        setLoading(true);
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in getting Kitting Label");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const printLabels = useCallback(
    async (labelDataArray) => {
      const route = `print-labels/`;
      const body = {
        labelData: labelDataArray,
      };
      try {
        setLoading(true);
        const response = await apiRequest.post_local(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in Printing Labels");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  return {
    loading,
    error,
    fetchMaterialsRequired,
    setOrderHoldQuantity,
    updateOrderState,
    generateLabels,
    getFailureReasons,
    submitFailureReason,
    supervisorLogin,
    stockTransfer,
    getKittingLabel,
    printLabels,
  };
};

export { useScanning };
