import React, { useState } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { useTranslation } from 'react-i18next';
import crossIcon from '../../assets/svg/cross-icon.svg'


const CompleteTest = ({ open, onClose, data, setData, onSubmit, loading, edit }) => {
 
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
        wrapperClasses="px-6"
    >
      {/* <h3 className="opacity-80">{edit=== true?"EDIT":"ADD"} NEW PLANT</h3> */}
      <Stack sx={{ justifyContent: 'center', alignItems: 'center', mt: 4}}>
      <CheckCircleOutlineIcon sx={{width:55,height:55}} color="primary"/>
       <Stack sx={{ justifyContent: 'center', alignItems: 'center', mt: 2,mb:2 }}>
           <Typography sx={{ fontSize: 20, fontWeight: 'bold'}} color={'primary'}>Done !</Typography>
        </Stack>  
      </Stack>
    </Modal>
  );
};

export default CompleteTest;
