import React, { useState } from "react";
import { t } from "i18next";
import Button from "../../../components/buttonn"; // Adjust the import path as needed

const FailureReasonResolutionDateInputModal = ({
  onClose,
  onScan,
  noStepFn,
  nextStepFn,
}) => {
  const [resolutionDate, setResolutionDate] = useState(null);
  const today = new Date().toISOString().split("T")[0];

  const handleDateChange = (event) => {
    setResolutionDate(event.target.value);
  };

  const handleSubmit = () => {
    nextStepFn(resolutionDate);
  };

  return (
    <div className="action_modal__container">
      <span style={{ fontWeight: "600", fontSize: "1.1rem" }}>
        Please input the resolution date
      </span>
      <input
        type="date"
        value={resolutionDate}
        onChange={handleDateChange}
        min={today}
        style={{ margin: "1rem 0", padding: "0.5rem", fontSize: "1rem" }}
      />
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
        }}
      >
        <Button
          onClick={noStepFn}
          white={true}
          width="8rem"
          height="2.5rem"
          bordered={true}
          customStyle={{
            border: "1px solid #3E3E3E",
            backgroundColor: "#FFFFFF",
          }}
        >
          {t('cancel')}
        </Button>
        <Button
          onClick={handleSubmit}
          color="green"
          width="8rem"
          height="2.5rem"
          disabled={resolutionDate === null}
        >
          {t('submit')}
        </Button>
      </div>
    </div>
  );
};

export default FailureReasonResolutionDateInputModal;
