const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    width: drawerWidth,
    // marginLeft:10,
    overflowX: "hidden",
    "& .MuiDrawer-paper": {
      width: drawerWidth,
      overflowX: "hidden",

      backgroundColor: COLORS.BG_GRAY,
    },
  }),
  ...(!open && {
    overflowX: "hidden",
    width: "50px",
    "& .MuiDrawer-paper": {
      overflowX: "hidden",
      width: "50px",
    },
  }),
}));

