import React,{useState,useEffect, useLayoutEffect} from "react";
import { useSelector } from "react-redux";
import Modal from "../../components/modal";
import { Stack, Typography } from "@mui/material";
import CancelIcon from '@mui/icons-material/Cancel';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Button from "../../components/button";
import useMachineGroup from "../../store/group/machine";
import useOperatorGroup from "../../store/group/operator";
import useUserGroup from "../../store/group/user";
import {COLORS, SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { REDUCER_TYPES } from "../../store";
import { setToken } from "../../store/api/utils";
import { useTranslation } from 'react-i18next';
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const Asset = ({ open, onClose, data, setData, onSubmit, loading,edit,tab}) => {

  const { allAssets,groupAsset,updateAssets } = useMachineGroup();
  const {allOperator,groupOp,updateAssets:updateOp} = useOperatorGroup()
  const {allUser,groupUser,updateAssets:updateUser} = useUserGroup()
  const [errors, setErrors] = useState(new Map());
  const [assetList , setAssetList] = useState([])
  const [assetInGroup,setAssetInGroup] = useState([])
  const [finalList,setFinalList] = useState([])
  const groupId = useSelector((state) => state?.SelectedId?.data?.id);
  const groupName = useSelector((state) => state?.SelectedId?.data?.name);

  // "Users", "Operators","Machines"
  useLayoutEffect(() => {
    // fetchAllAssets();
    fetchInGroup()
  }, [groupId!==""]);

  const fetchAllAssets = async (ingrp,all_list) => {
    let temp =all_list
    // const [_list]= tab === "Machines"? await allOperator():await allOperator()
    ingrp.map((item)=>{
      temp.map((item1,index)=>{
        if(item1.id===item.id){
         let idx = temp.indexOf(item1)
         temp.splice(index,1)
        }
      })
    })
    setAssetList(temp)
  };

  const fetchInGroup = async () => {
    if(tab==="Machines"){
      const [all_list] = await allAssets()
      const listt = await groupAsset(groupId)
      fetchAllAssets(listt.assets,all_list)
      if(listt.assets?.length>0){
        setAssetInGroup(listt.assets)  
      }
    }else if(tab==="Operators"){
      const [all_list] = await allOperator()
      const listt = await groupOp(groupId)
      fetchAllAssets(listt.operators,all_list)
      if(listt.operators?.length>0){
        setAssetInGroup(listt.operators)  
      }
    }else if(tab==="Users"){
      const [all_list] = await allUser()
      const listt = await groupUser(groupId)
      fetchAllAssets(listt.users,all_list.results)
      if(listt.users?.length>0){
        setAssetInGroup(listt.users)  
      }
    }
  };



const handleAssest=async(id,val)=>{
  const remove ={
    "remove":[id]
  }
  const add ={
    "add":[id]
  }
  if(tab==="Machines"){
    await updateAssets(val=== "add"?add:remove,groupId)
  }else if(tab==="Operators"){
    await updateOp(val=== "add"?add:remove,groupId)
  }else if(tab==="Users"){
    await updateUser(val=== "add"?add:remove,groupId)
  }

  fetchInGroup()
}

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
        wrapperClasses="px-6"
      
    >
      <h3 className="opacity-80" style={{textTransform:'capitalize'}}>({groupName}) {tab} Management</h3>
      <Stack sx={{flexDirection:'row',justifyContent:'space-between',mb:1,gap:1,height:400}}>
        <Stack sx={{width:'49%',border:1,borderRadius:2,borderColor:COLORS.TEXT_GRAY,p:1,overflow:'scroll',scrollbarWidth:'none'}}>
          <Typography sx={{px:1}}>{tab} in Group</Typography>
          {assetInGroup.map((item,index)=>{
           return( <Stack key={index} sx={{flexDirection:'row',backgroundColor:COLORS.GREEN_OPAQUE,mt:0.5,borderRadius:2,width:'fit-content',px:0.5}}>
              <Typography variant="h6" key={item.name} sx={{textTransform:'capitalize'}}>{index+1}. {item.name || item.fullname || item.username}</Typography>
              <CancelIcon fontSize="small" sx={{color:COLORS.DARK_RED,m:0.2,cursor:'pointer'}} onClick={()=>handleAssest(item.id,"remove")}/>
            </Stack>
           )
          })}
        </Stack>
        <Stack sx={{width:'49%',border:1,borderRadius:2,borderColor:COLORS.TEXT_GRAY,p:1,overflow:'scroll',scrollbarWidth:'none'}}>
          <Typography sx={{px:1}}>All {tab}</Typography>
          {assetList.map((item,index)=>{
           return( <Stack key={index} sx={{flexDirection:'row',backgroundColor:COLORS.GREEN_OPAQUE,mt:0.5,borderRadius:2,width:'fit-content',px:0.5}}>
              <Typography variant="h6" key={item.name} sx={{textTransform:'capitalize'}}>{index+1}. {item.name || item.fullname || item.username}</Typography>
              <AddCircleIcon fontSize="small" sx={{color:COLORS.DARK_GREEN,m:0.2,cursor:'pointer'}} onClick={()=>handleAssest(item.id,"add")}/>
            </Stack>
           )
          })}
        </Stack>
      </Stack>
      
     
      {/* <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={onSubmit} loading={loading}>
          Create
        </Button>
      </Stack> */}
    </Modal>
  );
};

export default Asset;
