import React from "react";
import { resolveResponse } from "../api/utils";
// import REQUEST, { CONTENT_TYPES } from "../api/indexMachine";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useMachineMonitoring = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const companies = async (page = 1,divId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/all-companies/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const plants = async (page = 1,companyId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/company/${companyId}/plants/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const divisions = async (page = 1,plantId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/plant/${plantId}/divisions/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const lines = async (page = 1,divisionId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/division/${divisionId}/lines/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const summary = async (
    page = 1,
    page_size,
    company_id,
    plant_id,
    division_id,
    line_id,
    date,
    shift_id,
    state
    ) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/get-daily-summary/?state=${state}&company_id=${company_id}&plant_id=${plant_id}&division_id=${division_id}&line_id=${line_id}&date=${date}&shift_id=${shift_id}&page=${page}&page_size=${page_size}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const assetSummary = async (
    page,
    asset_id,
    date,
    shift,
    state,
    quality,
    barcode
    ) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/daily-summary/${asset_id}/?date=${barcode?'':date}&barcode=${barcode}&shift=${shift}&state=${state}&quality=${quality}&page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const timelineSummary = async (
    asset_id,
    date,
    shift,
    state,
    quality
    ) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/asset-change-history/${asset_id}/?date=${date}&shift=${shift}&state=${state}&quality=${quality}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const getFilters = async (id) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/get-filters/?asset_id=${id}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `orders/issue-extra-material/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const update = async (failid,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `orders/update-material-failure/${failid}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  return {
    summary,
    assetSummary,
    timelineSummary,
    companies,
    plants,
    divisions,
    lines,
    create,
    update,
    getFilters
  };
};

export default useMachineMonitoring;
