import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import Button from "../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid, Divider, Chip } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import Input from "../../components/input";
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import Confirm from "./Confirm";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { useTranslation } from 'react-i18next';
import useUser from "../../store/users";

const ChangeLanguage = () => {
  const { t, i18n } = useTranslation();
  const navigate = useNavigate()
  const {updateProfile, userProfile,update} = useUser();
 
  const [errors, setErrors] = useState(new Map());
  const [modalOpen, setModalOpen] = useState(false);
  const [data, setData] = useState({
    language: '',
  });

  const handleLanguageChange = async () => {
    let userid = localStorage.getItem('userId')
    localStorage.getItem('draweWidth',190)
    console.log("language----->>>",data.language)
    i18n.changeLanguage(data.language); // Change language on selection
    const payload ={
      "preferred_language":data.language
    }
    await update(userid,payload)
    window.location.reload()

  };

  const USER_SCHEMA = [
    {
      // title: "Name",
      placeholder: t('select_language'),
      type: SCHEMA_TYPES.SELECT,
      name: "language",
      validator: VALIDATORS.REQUIRED,
      keyValues:[{value:'English',key:"EN"},{value:'',key:"HI"},{value:'',key:"MR"}]
    }
  ];

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Typography className="fw-20-500-36">
          {t('changeLanguage')}
        </Typography>
      </div>
      <Stack sx={{ justifyContent: 'center', alignItems: 'center',width:'90%' }}>
        <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', width: '90%' }}>
          <Stack sx={{ width: '50%' }}>
            <Typography sx={{ fontWeight: 500, fontSize: 18 }}>{t('existing')}</Typography>
          </Stack>
          <Stack sx={{ width: '50%', justifyContent: 'center', alignItems: 'center' }}>
            <Typography sx={{ fontWeight: 500, fontSize: 18 }}>{t('select_language')}</Typography>
          </Stack>
        </Stack>
        <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', mt: 1, width: '90%' }}>
          <Typography sx={{ fontWeight: 500, fontSize: 16 }}>{t('sel_lang')}</Typography>
          <Stack sx={{  }}>
            <SchemaExtractor
              data={data}
              errors={errors}
              onChange={handleChange}
              schema={USER_SCHEMA}
            />
          </Stack>   
        </Stack>
        <Stack
        direction="row"
        // alignItems="center"
        width={"90%"}
        justifyContent="flex-end"
        mt={6}
        mb={1}
      // sx={{px:6}}
      >
        {/* <Button bordered gray >
          Cancel
        </Button> */}
        <Button color={'gray'} onClick={() => handleLanguageChange()}>
          <Typography color={COLORS.TEXT_GRAY}>
            {t('confirm')}
          </Typography>
        </Button>
      </Stack>
      </Stack>
     
      {/* <Confirm
        open={modalOpen === "confirm"}
        onClose={() => { setModalOpen(false) }}
      /> */}
    </div>


  );
};

export default ChangeLanguage;
