import React, { useEffect, useState } from "react";
import Modal from "../../components/modal";
import Table from "../../components/table";
import Input from "../../components/input";
import { Stack, Typography, IconButton } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { COLORS, SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import useQuestion from "../../store/questionBank";
import { Delete } from "@mui/icons-material";
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import crossIcon from '../../assets/svg/cross-icon.svg'
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const AddQues = ({ open, onClose, data, setData, onSubmit, loading, tab, skills }) => {

  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
  const [questions, setQuestions] = useState([])
  const [finalQuestions, setFinalQuestions] = useState([])
  const [selectQuestions, setSelectedQuestions] = useState([])

  const { question_list, skill_question_list, induction_question, hr_question, create, update, remove } = useQuestion();

  const [data1, setData1] = useState({
    skill: ''
  })

  const [tabs, setTabs] = useState([])
  const [ques, setQues] = useState({})
  const [hr_q, setHr_q] = useState([])
  const [ind_q, setInd_q] = useState([])
  const [selTab, setSelTab] = useState('')

  useEffect(() => {
    if (data1.skill !== '' || data1.skill !== '') {
      // setData1({...data1,skill:data.skill}) 
      fetchSkillQuestion()
    }
  }, [data1.skill])

  useEffect(() => {
    fetchInductionQuestion()
    fetchHr()
  }, [])

  const fetchSkillQuestion = async () => {
    const [_list, _count] = await skill_question_list(1, data1.skill ? data1.skill : data.skill);
    handleQuestions(_list)
  };

  const fetchInductionQuestion = async () => {
    const [_list, _count] = await induction_question();
    setInd_q(_list)
    // setQuestions(_list)
  };

  const fetchHr = async () => {
    const [_list, _count] = await hr_question();
    setHr_q(_list)
  };

  function handleQuestions(_list) {
    setQuestions([])
    const compulsoryList = _list.filter(item => item.compulsory === true);
    const List = _list.filter(item => item.compulsory === false);
    console.log("compulsoryList", compulsoryList.length, "List", List.length)
    setQuestions(prevUsers => [...prevUsers, ...List])
    setFinalQuestions(prev => [...prev, ...compulsoryList])

  }

  const handleChange = async (e) => {
    const { name, value } = e.target;
    if (name === 'skill' && name !== 'Skill' && value !== 'Skill') {
      let skill = skills.filter((item) => value === item.key)
      let skillName = skill[0].value
      setSelTab(skillName)
      const [_list, _count] = await skill_question_list(1, value);
      const updatedList = _list.map(item => ({
        ...item,
        checked: item.compulsory // Set the initial checkbox state to false
    }));
      setTabs(prevTabs => {
        // Check if the value already exists in the tabs
        if (!prevTabs.includes(skillName)) {
          // Only add the value if it's not a duplicate
          return [...prevTabs, skillName];
        }
        // Return the existing tabs if the value is a duplicate
        return prevTabs;
      });
      setQues(prevQues => {
        // Check if the key already exists
        if (!prevQues.hasOwnProperty(skillName)) {
          // Only add the new key-value pair if it doesn't exist
          return { ...prevQues, [skillName]: updatedList };
        }
        // Return the existing ques if the key is a duplicate
        return prevQues;
      });
    }
    if (name !== 'skill' && name !== 'Skill' && value !== 'Skill') {
      let quesss = value === 'Induction' ? ind_q : hr_q
      const updatedQues = quesss.map(item => ({
        ...item,
        checked: item.compulsory // Set the initial checkbox state to false
    }));
      console.log("value question", value)
      setSelTab(value)
      setQues(prevQues => {
        // Check if the key already exists
        if (!prevQues.hasOwnProperty(value)) {
          // Only add the new key-value pair if it doesn't exist
          return { ...prevQues, [value]: updatedQues };
        }
        // Return the existing ques if the key is a duplicate
        return prevQues;
      });
      setTabs(prevTabs => {
        // Check if the value already exists in the tabs
        if (!prevTabs.includes(value)) {
          // Only add the value if it's not a duplicate
          return [...prevTabs, value];
        }
        // Return the existing tabs if the value is a duplicate
        return prevTabs;
      });
    }
    // setErrors((prev) => {
    //   prev.delete(name);
    //   return prev;
    // });
    setData1((prev) => ({ ...data1, [name]: value }));
    console.log("name or value------>", ques)
  };

  const handleSelect = (isChecked, itemId) => {
    const index = ques[selTab].findIndex(item => item.id === itemId);
    console.log("index of sel",index)
    // setQues({...ques,[selTab].[index].checked:true});
    setQues(prevState => ({
      ...prevState,
      [selTab]: prevState[selTab].map((question, idx) =>
          idx === index ? { ...question, checked: isChecked } : question
      )
  }));
  
    setSelectedQuestions(prevState => {
      if (isChecked) {
        // Add item to the array if checked
        return [...prevState, itemId];
      } else {
        // Remove item from the array if unchecked
        return prevState.filter(id => id !== itemId);
      }
    });
  };


  const SKILL = [
    {
      title: t('questionType'),
      placeholder: t('questionType'),
      type: SCHEMA_TYPES.SELECT,
      name: "tranning",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{ value: 'Induction', key: "Induction" }, { value: "Skill", key: "Skill" }, { value: "HR", key: "HR" }]
    },
    data1.tranning === "Skill" && {
      title: t('skill'),
      placeholder: t('selectSkill'),
      type: SCHEMA_TYPES.SELECT,
      name: 'skill',
      validator: VALIDATORS.REQUIRED,
      // keyValues:[{value:'Level 1',key:1},{value:"Level 2",key:2},{value:"Level 3",key:3}]
      keyValues: skills,
      // value:data.skill
      // values={d?.values}
    }
  ];


  const COLUMNS = [
    {
      id: 122,
      title: "",
      render: (rowData) => <FormGroup key={rowData.id}>
        <FormControlLabel control={<Checkbox
          defaultChecked={rowData.checked}
          onChange={(e) => handleSelect(e.target.checked,rowData.id)} />} />
      </FormGroup>,
    },
    {
      id: 1,
      title: t('question'),
      render: (rowData) => rowData.question_text,
    },
    {
      id: 2,
      title: t('compulsory'),
      render: (rowData) => rowData.compulsory === true ? 'Yes' : 'No',
    },
    {
      id: 3,
      title: t('questionLevel'),
      render: (rowData) => rowData.skill_level || "-",
    },

  ]

  const cleanup = () => {
    setErrors(new Map());
    setData({});
    setStep(0);
    onClose();
  };

  const handleBack = () => {
    if (step === 0) {
      cleanup();
    } else {
      setStep((prev) => prev - 1);
    }
  };

  const handleNext = async () => {
    let filterques = tabs.map((tab) => 
      ques[tab].filter((question) => question.checked === true)
    ).flat();
  // setFinalQuestions(...finalQuestions, ...filterques);
    // console.log("fina questions list",filterques)
    await onSubmit(filterques)
 
    // if (step === 0) {
    //   // const _errors = Validate(SKILL, data);
    //   // setErrors(_errors);
    //   // if (_errors.size) return;
    //   // handleAdd()
    //   await onSubmit(finalQuestions)
    //   // setStep((prev) => prev + 1);
    //   return;
    // }
    // if (step === 1) {
    //   await onSubmit(finalQuestions)
    //   setStep((prev) => prev + 1);
    //   return;
    // }
  };

  const handleAdd = () => {
    setFinalQuestions(prevUniqueItems => {
      // Create a new array that includes items that are not already in uniqueItems
      const updatedUniqueItems = [...prevUniqueItems];
      selectQuestions.forEach(item => {
        // Check if the item already exists in uniqueItems
        if (!updatedUniqueItems.some(existingItem => existingItem.id === item.id)) {
          updatedUniqueItems.push(item);
        }
      });
      return updatedUniqueItems;
    });
  };

  const handleDelete = async (id) => {
    const index = finalQuestions.findIndex(obj => obj.id === id);
    const temp = [...finalQuestions];
    temp.splice(index, 1);
    setFinalQuestions(temp);
  };

  const handleQ = () => {
    console.log("questions", ques[selTab])
    console.log("tab", selTab)
  }

  const handleTab=(val)=>{
    setTabs((prevItems) => prevItems.filter(item => item !== val));
    const { [val]: removed, ...newData } = ques; // Destructure to remove the property
    setQues(newData)
  }

  return (
    <Modal
    open={open}
    onClose={onClose}
    width="min(800px,90vw)"
    hideCloseIcon={crossIcon}
    wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{t('addQuestions')}</h3>
      <Stack sx={{ gap: 1,alignItems:'flex-start'}}>
        <SchemaExtractor
          data={data1}
          errors={errors}
          onChange={handleChange}
          schema={SKILL}
        />
      </Stack>

      <Stack sx={{ flexDirection: 'row', gap: 2, mt: 2,border:1,borderColor:COLORS.LIGHT_GRAY,borderTopLeftRadius:5,borderTopRightRadius:5,width:'fit-content' }}>
        {tabs.map((item,index) => {
          return <Stack sx={{p:0.5,flexDirection:'row',gap:1,borderRight:1,borderColor:COLORS.LIGHT_GRAY}}>
            <Typography onClick={() => { setSelTab(item) }} sx={{ fontSize: 14,color:selTab===item?'black':COLORS.GRAY }}>{item}</Typography>
            <IconButton sx={{ height: 14, width: 14 ,cursor:'pointer'}} onClick={()=>handleTab(item)}>
              <RemoveCircleOutlineIcon color={selTab===item?'black':COLORS.LG_GRAY } fontSize="small" />
            </IconButton>
          </Stack>
        })}
      </Stack>
      <Stack sx={{ }}>
        <Table
          columns={COLUMNS}
          rows={tabs.length>0?ques[selTab]:[]}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
        // serialize
        />
      </Stack>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={handleBack}>
          {t('back')}
        </Button>
        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">
          {step === 1 ? t("create") : t("next")}
          </Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddQues;
