import { IconButton, List, ListItem, ListItemText } from "@mui/material";
import React from "react";

const JsonFormatter = ({ json = {}, icon }) => {
  return Object.values(json || {})?.length ? (
    <List dense disablePadding>
      {Object.entries(json).map(([key, value]) => (
        <ListItem dense disablePadding disableGutters alignItems="center" className="h-45">
          <IconButton className="h-25 w-25 mr-1 fs-10 c-gray">{icon}</IconButton>
          <ListItemText
            className="capitalize"
            primary={key?.split("_")?.join(" ")}
            secondary={value}
          />
        </ListItem>
      ))}
    </List>
  ) : (
    <p className="c-gray fs-12 ml-1">N/A</p>
  );
};

export default JsonFormatter;
