import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useMaterialMove = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const listout = async (page = 1,divId) => {
    const client = new REQUEST();
    const res = await client.get(
      `materials/material-out/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const listreturn = async (page = 1,divId) => {
    const client = new REQUEST();
    const res = await client.get(
      `materials/material-return/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const createOutward = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `materials/material-out/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const createInward = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `materials/material-return/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const update = async (failid,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `orders/update-material-failure/${failid}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  return {
    listout,
    listreturn,
    createOutward,
    createInward,
    update
  };
};

export default useMaterialMove;
