import React, { useState } from "react";
import Modal from "../../components/modal";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { Validate } from "../../helpers/utils";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import Checkbox from '@mui/material/Checkbox';
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const Add = ({ open, onClose, data, setData, onSubmit, loading, edit, plantList, days, setDays, masterShift }) => {
  const USER_SCHEMA = [
    {
      title: t('name'),
      placeholder: t('enterName'),
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: t("plant"),
      placeholder: t("selectPlant"),
      type: SCHEMA_TYPES.SELECT,
      name: "plant",
      validator: VALIDATORS.REQUIRED,
      keyValues: plantList,
    },
    {
      title: t("startTime"),
      // placeholder: "select time",
      type: SCHEMA_TYPES.TIME,
      name: "start_time",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: t("endTime"),
      // placeholder: "select time",
      type: SCHEMA_TYPES.TIME,
      name: "end_time",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: t("type"),
      // placeholder: "Select Plant",
      type: SCHEMA_TYPES.RADIO,
      name: "type",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{ "value": true, "label": t('workingShift') }, { "value": false, "label": t('plannedBreak') }]
    },
    data.type === "false" && ({
      title:t('masterShift'),
      placeholder: t('selectMasterShift'),
      type: SCHEMA_TYPES.SELECT,
      name: "swiftmaster",
      // validator: VALIDATORS.REQUIRED,
      keyValues: [{ key: '', value: 'None' }, ...masterShift]
    }),

  ];
  const [errors, setErrors] = useState(new Map());
  const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleChangedays = (day) => (event) => {
    setDays((prev) => ({
      ...prev,
      [day]: event.target.checked,
    }));
  };

  const handleSubmit = async () => {
    const _errors = Validate(USER_SCHEMA, data);
    setErrors(_errors);
    if (_errors.size) return;
    await onSubmit()
    return;
  }
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit ?t('editShiftSchedule') : t('addShiftSchedule')}</h3>

      <SchemaExtractor
        data={data}
        errors={errors}
        onChange={handleChange}
        schema={USER_SCHEMA}
      />
      <Stack sx={{ mt: 1 }}>
        <Typography>{t('weekDays')}</Typography>
        <Stack sx={{ flexDirection: 'row', gap: 1 }}>
          <Checkbox
            onChange={handleChangedays('monday')}
            checked={days.monday}
            {...label}
            icon={<Typography>{t("mon")}</Typography>}
            checkedIcon={<Typography>{t("mon")}</Typography>}
          />
          <Checkbox
            onChange={handleChangedays('tuesday')}
            checked={days.tuesday}
            {...label}
            icon={<Typography>{t("tue")}</Typography>}
            checkedIcon={<Typography>{t("tue")}</Typography>}
          />
          <Checkbox
            onChange={handleChangedays('wednesday')}
            checked={days.wednesday}
            {...label}
            icon={<Typography>{t("wed")}</Typography>}
            checkedIcon={<Typography>{t("wed")}</Typography>}
          />
          <Checkbox
            onChange={handleChangedays('thursday')}
            checked={days.thursday}
            {...label}
            icon={<Typography>{t("thu")}</Typography>}
            checkedIcon={<Typography>{t("thu")}</Typography>}
          />
          <Checkbox
            onChange={handleChangedays('friday')}
            checked={days.friday}
            {...label}
            icon={<Typography>{t("fri")}</Typography>}
            checkedIcon={<Typography>{t("fri")}</Typography>}
          />
          <Checkbox
            onChange={handleChangedays('saturday')}
            checked={days.saturday}
            {...label}
            icon={<Typography>{t("sat")}</Typography>}
            checkedIcon={<Typography>{t("sat")}</Typography>}
          />
          <Checkbox
            onChange={handleChangedays('sunday')}
            checked={days.sunday}
            {...label}
            icon={<Typography>{t("sun")}</Typography>}
            checkedIcon={<Typography>{t("sun")}</Typography>}
          />
        </Stack>

      </Stack>

      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
          <Typography className="fw-20-500-24">{t('create')}</Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default Add;
