import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import useSkill from "../../store/skill";
import useUser from "../../store/users"
import Button from "../../components/button";
import AddSkill from "./AddSkill";
import AddReport from "./AddReport"
import AddSupervisor from './AddSupervisor'
import { toast } from "react-toastify";
import { IconButton, Stack ,Tab, Tabs, Typography} from "@mui/material";
import { Delete } from "@mui/icons-material";
import Tooltip from '@mui/material/Tooltip';
import EditIcon from '@mui/icons-material/Edit';
import Category from "../../components/UI/Category";
import UploadCsv from "../store/UploadCsv";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import { COLORS } from "../../helpers/constants";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const SkillPage = () => {
  const [tab , setTab] = useState("Users")
  const [users, setUsers] = useState([]);
  const [skill, setSkill] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [csvData, setCsvData] = useState([])
  const [page, setPage] = useState(0);
  const [file, setFile] = useState(null);
  const [data, setData] = useState({
    days_1:'',
    days_2:'',
    days_3:'',
    supervisor_one:'',
    supervisor_two:'',
    supervisor_three:'',
    exam_1:'',
    exam_2:'',
    exam_3:'',
    floor_1:'',
    floor_2:'',
    floor_3:'',
    entry_1:'',
    entry_2:'',
    entry_3:'',

  });
  const { list, create,updateSkill,update,addSupervisor, remove,opratorList,createOperator,removeOperator,updateOperator } = useSkill();
  const [edit , setEdit] = useState(false)
  const [skillId,setSkillId] = useState('')
 
  const { activeUsers } = useUser();
  const [selectSkill, setSelectSkill] = useState()

  useEffect(() => {
    fetchSkill();
  }, [page, tab]);

  useEffect(() => {
    fetchUsers();
  }, []);

  const fetchUsers = async () => {
    setLoading("fetch");
    // const [_list, _count] =  await fetchUser(page + 1)
    const res =  await activeUsers(page + 1)
    // const newOptr = _list.filter(item => item.is_operator);
    // const newUsr = _list.results.filter(item => !item.is_operator && item.status === true);
    // const newUsers = newUsr.map(item => ({
    //   value: item.username,
    //   key: item.id
    // }));
    setUsers(res);
  };

  const fetchSkill = async () => {
    setLoading("fetch");
    const [_list, _count] = await list(page + 1)
    console.log("_list--------------",_list)
    setSkill(_list.results);
    setTotal(_list.count);
    setLoading(false);
  };

const handleAdd = async () => { 
    const payload = {
      "skill_name": data.skill_name,
      "test_performance_criteria": [data.exam_1,data.exam_2,data.exam_3],
      "duration_between_two_tests": [data.days_1,data.days_2,data.days_3 ],
      "floor_performance_criteria": [
                parseInt(data.floor_1),
                parseInt(data.floor_2),
                parseInt(data.floor_3)
              ],
      "no_of_performance_entries": [
                parseInt(data.entry_1),
                parseInt(data.entry_2),
                parseInt(data.entry_3)
              ]
  }

    setLoading("fetch")
  //  let res = await create(payload)
  //  let res = edit === false? await create(payload):await updateSkill(payload,skillId)
   if(edit === false){
   let res = await create(payload)
   assignSupervisor(res.id)
   console.log("add skill respose----",res)
   }
   else{
   let res = await updateSkill(payload,skillId)
   assignSupervisor(skillId)
   }
  };

  async function assignSupervisor (id){
    const payload = {...data}
    const payload2 ={
      "add":payload.supervisor_one
    }
    await addSupervisor(id,payload2)
    setModalOpen(false);
    setData({})
    await fetchSkill();
    setLoading(false);
  }

  const handleDelete = async (id) => {
    setLoading("fetch");
    tab === "Users" ? await remove(id): await removeOperator(id);
    await fetchUsers();
    setLoading(false);
  };

  const handleUpdate = async (id,is_admin,type) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    const payload2 ={
      "status":is_admin===true?false:true
    }
    tab === "Users" ? await update(id,type==="status"?payload2:payload):updateOperator(id,type==="status"?payload2:payload)
    await fetchUsers();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: t('skillName'),
      render: (rowData) => rowData.skill_name,
    },
    // {
    //   id: 2,
    //   title: t("tags"),
    //   render: (rowData) =>' -',
    // },
    // {
    //   id: 3,
    //   title: "Created On",
    //   render: (rowData) => '-',
    // },
    // {
    //   id: 3,
    //   title: "No. of Times Used",
    //   render: (rowData) => '-',
    // },
    {
      id: 112,
      title: t('noOfEntries'),
      render: (rowData) =><Stack sx={{flexDirection:'row',gap:0.3}}>
       {rowData.no_of_performance_entries.map((item,index)=>{
        return<Typography>{item}{index < rowData.no_of_performance_entries.length - 1 && ','}</Typography>
       })} 
      </Stack>
    },
    {
      id: 113,
      title: t('admins'),
      render: (rowData) => <Stack sx={{flexDirection:'row',gap:0.3}}>
      {rowData.supervisor_list.map((item,index)=>{
        return(
          <Typography sx={{fontSize:14}}>{item.username}{index < rowData.supervisor_list.length - 1 && ','}</Typography>
        )
        })}
        </Stack>,
    },
    {
      id: 6,
      title: t('action'),
      render: (rowData) => (<>
        <Stack flexDirection={'row'} sx={{gap:1}}>
        {/* <Tooltip title="add supervisor"> <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => {setEdit(true);setModalOpen("addSuper");setSkillId(rowData.id)}}
        >
        <AddCircleIcon color="primary" fontSize="small" />
        </IconButton>
        </Tooltip> */}
        <Tooltip title="edit skill"> 
          <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => {setEdit(true);setModalOpen("add");setSelectSkill(rowData);
            setData({ ...data,
              skill_name:rowData.skill_name,
              supervisor_one:rowData.supervisor_list.map(item => item.id),
              days_1:rowData.duration_between_two_tests[0],
              days_2:rowData.duration_between_two_tests[1],
              days_3:rowData.duration_between_two_tests[2],
              exam_1:rowData.test_performance_criteria[0],
              exam_2:rowData.test_performance_criteria[1],
              exam_3:rowData.test_performance_criteria[2],
              floor_1:rowData.floor_performance_criteria[0],
              floor_2:rowData.floor_performance_criteria[1],
              floor_3:rowData.floor_performance_criteria[2],
              entry_1:rowData.no_of_performance_entries[0],
              entry_2:rowData.no_of_performance_entries[1],
              entry_3:rowData.no_of_performance_entries[2],
            });
            setSkillId(rowData.id)}}
        >
        <EditIcon color="primary" fontSize="small" />
        </IconButton>
        </Tooltip>

        {/* <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
          <Delete color="error" fontSize="small" />
        </IconButton> */}
      </Stack>
        </>
      ),
    },
  ];

  const handleSubmitCSV = async()=>{
     csvData.map(async(csvitem)=> {
      if(tab === "Users"){
        if(csvitem.email!==""){
          await create(csvitem)
        }else{
          return
        }
      }else{
        if(csvitem.employee_id!==""){
          await createOperator(csvitem)
        }else{
          return
        }
      }
     })
     setModalOpen(false);
     await fetchSkill(page =1)
  }

  return (
    <div className="mx-3 my-2 fw-500">
      <div className="flex items-center justify-between">
        <Typography className="fw-20-500-36">
          {t('skill')}
        </Typography>
        <Stack justifyContent="flex-end" direction="row" sx={{mb:1}}>
        <Button color={'gray'} onClick={() => {setModalOpen("add");setData({})}}>
          <Typography className="fw-20-500-24">+ {t('createSkill')}</Typography>
          </Button>
        </Stack>
      </div>
      <Table
        columns={COLUMNS}
        rows={skill}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
        tableBodyColor={COLORS.WHITE}
      />
      <AddSkill
        selectSkill={selectSkill}
        open={modalOpen === "add"}
        onClose={() => {setModalOpen(false);setEdit(false)}}
        data={data}
        setData={setData}
        onSubmit={handleAdd}
        loading={loading === "add"}
        edit={edit}
        user={users}
      />
      <AddSupervisor
        open={modalOpen === "addSuper"}
        onClose={() => {setModalOpen(false);setEdit(false)}}
        data={data}
        setData={setData}
        onSubmit={assignSupervisor}
        loading={loading === "add"}
        user={users}
      />
      <AddReport
        open={modalOpen === "Report"}
        onClose={() => setModalOpen(false)}
        onSubmit={handleAdd}
      />
      <UploadCsv
        onClose={() => setModalOpen(false)}
        open={modalOpen === "csvUser"}
        onSubmit={handleSubmitCSV}
        file={file}
        setFile={setFile}
        setCsvData={setCsvData}
        tab={tab}
      />
    </div>
  );
};

export default SkillPage;
