import React,{useEffect, useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Grid, Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {COLORS, SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import useUserGroup from "../../store/group/user";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";


const AddTraining = ({ open, onClose, data, setData, onSubmit, loading,edit,users}) => {
  const {list:userGrouplist,groupUser} = useUserGroup()
  const [groups,setGroups] = useState([])

  useEffect(()=>{
    fetchGroups()
  },[])
  const USER_SCHEMA = [
    {
      title: t('trainingName'),
      placeholder: t('enterTrainingName'),
      type: SCHEMA_TYPES.INPUT,
      name: "training_name",
      validator: VALIDATORS.REQUIRED,
      width:'350px'
    },
    {
      title: t('trainingStartDate'),
      placeholder: "Select Start Date",
      type: SCHEMA_TYPES.DATE,
      name: "start_date",
      validator: VALIDATORS.REQUIRED,
       width:'350px'
    },
   
    {
      title: t('trainingType'),
      placeholder: t('enterTrainingType'),
      type: SCHEMA_TYPES.INPUT,
      name: "training_type",
      validator: VALIDATORS.REQUIRED,
       width:'350px'
    },
     
    {
      title: t('trainingEndDate'),
      placeholder: "Select End Date",
      type: SCHEMA_TYPES.DATE,
      name: "end_date",
       width:'350px'
      // validator: VALIDATORS.REQUIRED,
    },
   
    {
      title: t('trainingDescription'),
      placeholder: t('enterTrainingDescription'),
      type: SCHEMA_TYPES.INPUT,
      name: "training_description",
       width:'350px'
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('trainingStartTime'),
      // placeholder: "Select start Time",
      type: SCHEMA_TYPES.TIME,
      name: "start_time",
      validator: VALIDATORS.REQUIRED,
      width:'350px'
    },
    {
      title: t('trainer'),
      placeholder: t('enterTrainerName'),
      type: SCHEMA_TYPES.INPUT,
      name: "trainer",
      width:'350px'
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('trainingEndTime'),
      // placeholder: "Select End Time",
      type: SCHEMA_TYPES.TIME,
      name: "end_time",
       width:'350px'
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('trainingAdmin'),
      // placeholder: "Enter Training admin",
      type: SCHEMA_TYPES.SELECT,
      name: "training_admin",
      validator: VALIDATORS.REQUIRED,
      keyValues:users,
      multi:true,
      height:'auto',
      width:'350px'
    },
    {
      title: t('testRequiredAtEndOfTraining'),
      type: SCHEMA_TYPES.RADIO,
      name: "test_required",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{"value":"yes","label":t('yes')},{"value":"no","label":t('no')}],
      width:'350px'
    },
    {
      title: t('group'),
      placeholder: "Select group",
      type: SCHEMA_TYPES.SELECT,
      name: "group",
      // validator: VALIDATORS.REQUIRED,
      keyValues:groups,
      multi:true,
      height:'auto',
      width:'350px'
    },
    {
      title: t('feedbackAtEndOfTraining'),
      type: SCHEMA_TYPES.RADIO,
      name: "feedback_required",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{"value":"yes","label":t('yes')},{"value":"no","label":t('no')}],
      width:'350px'
    },
    {
      title: t('trainees'),
      placeholder: "Select Trainees",
      type: SCHEMA_TYPES.SELECT,
      name: "trainees",
      // validator: VALIDATORS.REQUIRED,
      keyValues:users,
      multi:true,
      height:'auto',
      width:'350px'
    },
      data.feedback_required=== 'yes' &&({
      title: t('feedbackLink'),
      placeholder: t('enterFeedbackLink'),
      type: SCHEMA_TYPES.INPUT,
      name: "feedback_link",
      validator: VALIDATORS.REQUIRED,
       width:'350px'
      
    })
  
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    console.log(e.target)
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleNext=async ()=>{
    const _errors = Validate(USER_SCHEMA, data);
        setErrors(_errors);
        if (_errors.size) return;
        await onSubmit()
  }

  const fetchGroups=async ()=>{
    const [res] = await userGrouplist()
    console.log("fetchGroups------>>>>",res)
    const newUsers = res.map(item => ({
      value: item.name,
      key: item.id
    }));
    setGroups(prevUsers => [...prevUsers,...newUsers]);
  }

  return (
  <>
      {/* <h3 className="opacity-80">{edit=== true?"EDIT":"ADD"} NEW LINE</h3> */}
      <Grid display={'grid'} sx={{alignContent:'flex-start'}} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}> 
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />   
      </Grid>
      <Stack
      sx={{flexDirection:'row',justifyContent:'space-between',mb:2}}
      >
        <Button  color={'gray'} onClick={onClose} loading={loading}>
          <Typography sx={{px:2,color:COLORS.BLACK}}>
         { t('back')}
          </Typography>
        </Button>
        <Button  color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography sx={{px:2,color:COLORS.BLACK}}>
          {t('create')}
          </Typography>
        </Button>
      </Stack>
      </>
  );
};

export default AddTraining;
