import React, { useState } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography, Grid } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import { COLORS } from "../../helpers/constants";
import Select from "../../components/input/Select";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { toast } from "react-toastify";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const AddSkill = ({ open, onClose, data, setData, onSubmit, loading, tab, edit, selectSkill, user }) => {

  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
  const [level, setLevel] = useState(1)
  const [levels, setLevels] = useState(1)
  const regex = /^[A-Za-z\s]*$/;

  const handleChange = (e) => {
    const { name, value } = e.target;
    // if (name=== 'skill_name') {
    //   if(regex.test(value)===true){
    //     setData((prev) => ({ ...prev, skill_name: value }));
    //   }
      
    // }else{
      setData((prev) => ({ ...prev, [name]: value }));
    // }  
  };

  const handleNext = async () => {
    if (data.skill_name !== '') {
      // const _errors = Validate(USER_SCHEMA, data);
      // setErrors(_errors);
      // if (_errors.size) return;
      await onSubmit()
      setLevel(1)
      setLevels(1)
      // onClose()
      // setStep((prev) => prev + 1);
      return;
    }
  };


  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit === true ? t('editSkill') : t("addingSkill")}</h3>
      <div style={{ display: 'flex', gap: 5 }} className="fw-16 mb-1">
        {levels > 1 && (<Typography sx={{ cursor: 'pointer' }} onClick={() => setLevel(1)}>{t('level')} 1 &gt;</Typography>)}
        {levels > 1 && (<Typography sx={{ cursor: 'pointer' }} onClick={() => setLevel(2)}>{t('level')} 2 &gt;</Typography>)}
        {levels > 2 && (<Typography sx={{ cursor: 'pointer' }} onClick={() => setLevel(3)}>{t('level')} 3</Typography>)}
      </div>
      {/* {edit && (<h3 className="opacity-80">{data.skill_name}</h3>)} */}
      <div style={{ display: 'grid', gap: 10 }}>
        {level === 1 && (
          <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
            <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('enterSkillName')}</Typography>
            <Input name='skill_name'
              value={data.skill_name}
              onChange={handleChange}
              disabled={edit===true}
              width={"500px"} />
               <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('addSupervisor')}</Typography>
                <Select
                  name={'supervisor_one'}
                  width="100%"
                  onChange={handleChange}
                  // error={errors.get(d?.name)}
                  value={data.supervisor_one}
                  keyValues={user}
                  // required={!!d?.validator}
                  multi={true}
                  height={'auto'}
                />
          </Grid>
        )}
        {level === 1 && (
          <>
            <Typography sx={{ textAlign: 'center' }} onClick={()=>console.log('supervisor',data)}>{t('level')} 1</Typography>
            <div style={{ display: 'grid', gap: 10 }}>
              {/* <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: '1fr 2fr 2fr 3fr', xs: '1fr 2fr 2fr 3fr' }}> */}
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{  sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)'  }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('addDays')}</Typography>
                <Input
                  type={'number'}
                  name='days_1'
                  value={data.days_1}
                  width={"500px"}
                  onChange={handleChange}
                />  
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('examPassingCriteria')}</Typography>
                <Input type={'number'} name='exam_1' value={data.exam_1} onChange={handleChange} width={"500px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('floorWorkCriteria')}</Typography>
                <Input type={'number'} name='floor_1' value={data.floor_1} onChange={handleChange} width={"500px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('numberOfEntry')}</Typography>
                <Input type={'number'} name='entry_1' value={data.entry_1} onChange={handleChange} width={"500px"} />
              </Grid>
            </div>
          </>
        )}
        {level === 2 && (
          <>
            <Typography sx={{ textAlign: 'center' }}>{t('level')} 2</Typography>
            <div style={{ display: 'grid', gap: 10 }}>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('addDays')}</Typography>
                <Input
                  type={'number'}
                  name='days_2'
                  value={data.days_2}
                  width={"500px"}
                  onChange={handleChange}
                />
                {/* <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Add Supervisor</Typography>
                <Select
                  name={'supervisor_two'}
                  width="100%"
                  onChange={handleChange}
                  // error={errors.get(d?.name)}
                  // value={data.supervisor_two}
                  keyValues={user}
                  // required={!!d?.validator}
                  multi={true}
                  height={'auto'}
                /> */}
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('examPassingCriteria')}</Typography>
                <Input type={'number'} name='exam_2' value={data.exam_2} onChange={handleChange} width={"500px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('floorWorkCriteria')}</Typography>
                <Input type={'number'} name='floor_2' value={data.floor_2} onChange={handleChange} width={"500px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('numberOfEntry')}</Typography>
                <Input type={'number'} name='entry_2' value={data.entry_2} onChange={handleChange} width={"500px"} />
              </Grid>
            </div>
          </>
        )}
        {level === 3 && (
          <>
            <Typography sx={{ textAlign: 'center' }}>{t('level')} 3</Typography>
            <div style={{ display: 'grid', gap: 10 }}>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)'}}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('addDays')}</Typography>
                <Input
                  type={'number'}
                  name='days_3'
                  value={data.days_3}
                  width={"500px"}
                  onChange={handleChange}
                />
                {/* <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>Add Supervisor</Typography>
                <Select
                  name={'supervisor_three'}
                  width="100%"
                  onChange={handleChange}
                  // error={errors.get(d?.name)}
                  // value={data.supervisor_three}
                  keyValues={user}
                  // required={!!d?.validator}
                  multi={true}
                  height={'auto'}
                /> */}
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('examPassingCriteria')}</Typography>
                <Input type={'number'} name='exam_3' value={data.exam_3} onChange={handleChange} width={"500px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('floorWorkCriteria')}</Typography>
                <Input type={'number'} name='floor_3' value={data.floor_3} onChange={handleChange} width={"500px"} />
              </Grid>
              <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
                <Typography sx={{ fontSize: 16, fontWeight: '400', color: COLORS.TEXT_GRAY }}>{t('numberOfEntry')}</Typography>
                <Input type={'number'} name='entry_3' value={data.entry_3} onChange={handleChange} width={"500px"} />
              </Grid>
            </div>
          </>
        )}
      </div>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
        px={8}
      >
        <Button bordered color={'light_green'} disabled={level === 3} onClick={() => {
          console.log("data skill----->>",regex.test(data.skill_name))
          if(regex.test(data.skill_name) === true && data.skill_name !=="" && data.skill_name !== undefined){
            level <= 3 && setLevel((prev) => prev + 1);
            levels <= 3 && setLevels((prev) => prev + 1)
          }
          else
          {
            toast.warn("Enter Vaild Skill Name",data.skill_name)
          }
        }
        }>
          <Typography color={COLORS.TEXT_GRAY}>{edit===true?t('next'):t('addLevel')}</Typography>
        </Button>
        {edit===true?(
 <Button color={'light_green'}  onClick={handleNext} loading={loading}>
 <Typography color={COLORS.BLACK}>{t('submit')}</Typography>
</Button>
        ):(
          <Button color={'light_green'} disabled={level!=3} onClick={handleNext} loading={loading}>
          <Typography color={COLORS.BLACK}>{t('submit')}</Typography>
        </Button>
        )}
       
      </Stack>
    </Modal>
  );
};

export default AddSkill;
