import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import Table from "../../components/table";
import useQuestion from "../../store/questionBank";
import Button from "../../components/button";
import TestDetail from "./testDetail";
import CompleteTest from "./completeTest"
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete,Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import JsonFormatter from "../../components/UI/JsonFormatter";
import QuizHeader from './quizHeader'
import Question from "./question";
import UploadCsv from "../store/UploadCsv";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";


const Test = () => {
  const params = new URLSearchParams(window.location.search);
  const testId = params.get('key1');
  const location = useLocation()
  const navigate = useNavigate()
  const companyId = location.state
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState("testDetail");
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
   metadata:{}
  });
  const { get_Test,updateTest,result} = useQuestion();
  const [selectedAnswer, setSelectedAnswer] = useState([])
  const [questionNo,setQuestionNo] = useState(0)
  const [ ans, setAns] = useState([])
  const [file, setFile] = useState(null);
  const [csvData, setCsvData] = useState([])
  const [testData,setTestData] = useState()
  const [finalAns,setFinalAns] = useState([])

  useEffect(() => {
    fetchQuestion();
  }, []);

  const fetchQuestion = async () => {
    setLoading("fetch");
    const [_list, _count] = await get_Test(testId);
    // console.log("test data",_list)
    setTestData(_list);
    const testLis = _list.questions_list.map(item => ({
      question: item.id,
      ans: ''
    }));
    setAns(prevUsers => [...prevUsers,...testLis]);

    const Lis = ans.map(item => ({
      [item.question]: 0
    }));
    setFinalAns(prevUsers => [...prevUsers,...Lis]);
    setTotal(_count);
    setLoading(false);
  };

const onStart=async ()=>{
  await updateTest(testId,{'started':true})
}

  const handleAnswerSelection = (e, val) => {
    postAnswer(e,val)
    // console.log("handleAnswerSelection",e,'------',val)
    setAns(prevAns => {
      // Check if the question already exists
      const existingIndex = prevAns.findIndex(obj => obj.question === val);
      if (existingIndex !== -1) {
        // If the question already exists, update its answer
        const updatedAns = [...prevAns];
        updatedAns[existingIndex] = { question: val, ans: e };
        return updatedAns;
      } else {
        // If the question does not exist, add a new entry
        return [...prevAns, { question: val, ans: e }];
      }
    });
    

  };
  
  
  function handleNext (val){
    if(val === 'pre' && questionNo>=1){
      setQuestionNo((prev) => prev - 1)
    } 
    if (val === 'next' && questionNo+1 < testData.questions_list.length){
      setQuestionNo((prev) => prev + 1)
    }
  }

  async function handleSubmit (val){
    localStorage.setItem('draweWidth', 190)
    localStorage.setItem('time',0)
    let payload={
      // answers:{[opts]:quesId},
      completed:true
    }
    await updateTest(testId,payload)
    const res =  await result(testId)
    setModalOpen("timeUps")
    setTimeout(() => {
      onKillTab();
    }, 3000)
  }

async function postAnswer(quesId, opts){
  let timer = localStorage.getItem('time')
  let payload={
    answers:{[opts]:quesId},
    remaining_time_in_seconds:timer
  }
  await updateTest(testId,payload)   
}

function onKillTab(){
  // localStorage.setItem('draweWidth', 180)
  window.opener = null;
  window.open("", "_self");
  window.close();
}

function handleCSV() {
  // Initialize an array to hold the results
  const results = [];

  // Skip the header row and process each row
  csvData.slice(1).forEach((item) => {
    // Extract the question from the first element
    const ques = item[0];
    const ans = item[item.length-1]

    // Use a Set to store unique choices
    const choicesSet = new Set();

    // Add choices to the Set, ignoring empty strings
    item.slice(1).forEach(choice => {
      if (choice !== '') {
        choicesSet.add(choice);
      }
    });

    // Convert the Set to an Array
    const choices = Array.from(choicesSet);

    // Create an object for the current question and its choices
    const obj = { question: ques, choices: choices ,answer:ans};

    // Add the object to the results array
    results.push(obj);

    // Log the result (optional, remove if not needed)
    console.log("Processed object", obj);
  });

  // You can now use `results` as needed
  console.log("All results", results);
}

  async function TimesUps(){
    localStorage.setItem('draweWidth', 190)
    try {
      // Mark the test as completed with remaining time as 0
      let payload = { completed: true, remaining_time_in_seconds: 0 };
      await updateTest(testId, payload);
  
      // Fetch the test results after updating
      const res = await result(testId);
      console.log("Upload result", res);
  
      // Open the modal for 'time up'
      setModalOpen("timeUps");
  
      // Kill the tab after 3 seconds
      setTimeout(() => {
        onKillTab();
      }, 3000);
  
      // Optionally, you could also trigger page reload if necessary:
      // window.location.reload();
    } catch (error) {
      console.error("Error in the timer flow:", error);
      toast.error(error)
    }
}
  return (
    <div className="mx-3 my-2"> 
      {/* <div className="flex items-center justify-between">
          <Stack sx={{flexDirection:'row',alignItems:'center'}}>
          <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/>
            <h1 className="mt-4">
              {t('test')}
              <span className="fs-30 ml-1 c-gray">({users.length})</span>
            </h1>
          </Stack>
        <Button onClick={() => setModalOpen("csvUser")}>upload test</Button>
      </div> */}
     {modalOpen === "start" &&(
       <Stack sx={{flexDirection:'row',justifyContent:'space-between',p:1}}>
       <Stack sx={{width:'78%'}}>
         <QuizHeader
         finishTest={TimesUps}
         activeQuestion={questionNo+1} 
         totalQuestions={testData.questions_list.length}
         setModalOpen={setModalOpen}
         testData={testData}
         />
         <Question
           question={testData.questions_list[questionNo]}
           choices={testData.questions_list[questionNo]?.question_options}
           type={'MAQs'}
           handleAnswerSelection={handleAnswerSelection}
           ans={ans}
           questionNo={questionNo}
         />
         <Stack sx={{flexDirection:'row',justifyContent:'space-between',mt:3}}>
           <Button color={'light_green'}  onClick={()=>handleNext('pre')} >Previous</Button>  
           {questionNo === testData.questions_list.length- 1 ?(
            <Button color={'light_green'} onClick={()=>handleSubmit()}>
             <Typography>{t('submit')}</Typography> 
              </Button>
           ):(
            <Button color={'light_green'} onClick={()=>handleNext('next')}>
              <Typography>{t('next')}</Typography>
            </Button>
           )}
         </Stack>
       </Stack>
       <Stack sx={{width:'18%',boxShadow: 3 ,p:1,py:2,borderRadius:2,justifyContent:'space-between'}}>      
       <Grid display={'grid'} gap={1} gridTemplateColumns={{ sm: 'repeat(5, 1fr)', xs: 'repeat(5, 1fr)' }}>
       {ans.map((item,index)=>{
         return(<Stack key={index} sx={{backgroundColor:item.ans?'#86D56A':'#3E3E3E',justifyContent:'center',alignItems:'center',borderRadius:1,width:22,height:22,cursor:'pointer'}} onClick={()=>setQuestionNo(index)}>
           <Typography style={{fontSize:'12px',fontWeight:'bold'}} sx={{color:item.ans?'black':'white',textAlign:'center'}}>{index+1}</Typography>
         </Stack>)
       })}
       </Grid>
       <Stack sx={{}}>
        <Stack sx={{flexDirection:'row',gap:1}}>
       <Stack sx={{backgroundColor:'#86D56A',justifyContent:'center',alignItems:'center',borderRadius:1,width:22,height:22}}>
       </Stack>
       <Typography sx={{mb:1,fontWeight:'bold'}}>Answered</Typography>
       </Stack>
       <Stack sx={{flexDirection:'row',gap:1}}>
       <Stack sx={{backgroundColor:'#3E3E3E',justifyContent:'center',alignItems:'center',borderRadius:1,width:22,height:22}}>
       </Stack>
       <Typography sx={{fontWeight:'bold'}}>Not Answered</Typography>
       </Stack>
       </Stack>
       </Stack>
       </Stack>
     )}
     {modalOpen === "testDetail" && (
      <TestDetail
        testData={testData}
        setModalOpen={setModalOpen}
        onStart={onStart}
        // onSubmit={handleAdd}
      />
     )}
      <CompleteTest
       open={modalOpen === "timeUps"}
       onClose={() => setModalOpen(false)}
      />
      <UploadCsv
        onClose={() => setModalOpen(false)}
        open={modalOpen === "csvUser"}
        onSubmit={handleCSV}
        file={file}
        setFile={setFile}
        setCsvData={setCsvData}
      />
    </div>
  );
};

export default Test;
