import React from "react";
import MTable from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import TextField from '@mui/material/TextField';
import { Stack, TableFooter, TablePagination, Typography } from "@mui/material";
import Spin from "../spin";
import empty from "../../assets/png/empty.png";
import Input from "../input";
import { COLORS } from "../../helpers/constants";
import { t } from "i18next";

const Table = ({
  rows = [],
  columns = [],
  minWidth = 650,
  tableClasses = "",
  titleCellClasses = "",
  rowClassesMethod = () => {},
  rowClasses = "",
  rowCellClasses = "",
  rowsPerPage = 10,
  paginator = false,
  loading,
  dense,
  hideBorder = false,
  onRowClick = Function.prototype,
  serialize = false,
  page = 0,
  setPage = Function.prototype,
  total,
  headColor,
  headLabelColor,
  tableBodyColor
}) => {
  return (
    <TableContainer>
      <MTable
        sx={{ minWidth: minWidth }}
        className={`${tableClasses}`}
        size={dense && "small"}
      >
        <TableHead
         sx={{backgroundColor:headColor?headColor:COLORS.BG_GRAY}}
         >
          <TableRow>
            {serialize && (
              <TableCell className={`${titleCellClasses} text-nowrap h3`} sx={{color:headLabelColor?headLabelColor:'black'}}>
                {t('sno')}
              </TableCell>
            )}
            {columns.map((col, i) => (
              <TableCell
                style={{ width: col.width && col.width,color:headLabelColor?headLabelColor:'black'}}
                key={i}
                className={`${titleCellClasses} text-nowrap`}
              >
                {col.title}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <Stack sx={{p:0.3}}></Stack>
        <TableBody >
          {loading ? (
            <TableRow >
              <TableCell sx={{alignContent:'center',alignItems:'center'}} colSpan={12} className="flex h-300 text-center">
              <div style={{display:'flex',justifyContent:'center'}}>
                <Spin big blue />
                </div>
              </TableCell>
            </TableRow>
          ) : rows && rows.length ? (
            rows.map((row, i) => (
              <>
              <TableRow
                key={i}
                sx={{  
                  backgroundColor:tableBodyColor || COLORS.LG_GRAY,
                  [`& .${tableCellClasses.root}`]: {
                    borderBottom: hideBorder ? "none" : "true",
                    mt:10
                  },
                  ...rowClassesMethod(i, row),
                }}
                className={`${rowClasses}`}
                onClick={() => onRowClick(row.id)}
              >
                {serialize && (
                  <TableCell sx={{color:'black'}} className={`${rowCellClasses} fw-500 `} align="left">
                    {i + 1}
                  </TableCell>
                )}
                {columns.map((col, index) => {
                  return (
                    <TableCell
                      key={index}
                      sx={{ width: col.width && col.width,color:'black'}}
                      className={`${rowCellClasses} fw-500`}
                      align="left"
                    >
                      {col.render(row)}
                    </TableCell>
                  );})}
                  
              </TableRow>
              <Stack sx={{p:0.3}}></Stack>
              </>
            ))
          ) : (
            <TableRow>
              <TableCell
                colSpan={12}
                className="text-center h-300 flex items-center opacity-60 w-full"
              >
                <img
                  src={empty}
                  alt="leaf"
                  height={100}
                  className="w-full object-contain"
                />
                <p className="text-center">{t('noRecordFound')} !</p>
              </TableCell>
            </TableRow>
          )}
        </TableBody>
        {paginator && (
          <TableFooter>
            <TableRow>
              <TablePagination
                rowsPerPageOptions={[]}
                count={total || rows.length}
                rowsPerPage={rowsPerPage}
                onPageChange={(e, newValue) => setPage(newValue)}
                page={page}
              />
            </TableRow>
          </TableFooter>
        )}
      </MTable>
    </TableContainer>
  );
};


export default Table;
