import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import "./style.scss";
import { useLocation, useSearchParams } from "react-router-dom";

// Assets
import ChevronRightIcon from "@mui/icons-material/ChevronRight";

const BreadcrumbItem = ({ label, path, isLast }) => {
    return isLast ? (
        <span className="breadcrumb-item current">{label}</span>
    ) : (
        <Link to={path} className="breadcrumb-item">
            {label}
        </Link>
    );
};

const Breadcrumb = () => {
    const location = useLocation();
    const [items, setItems] = useState([]);
    const [searchParams] = useSearchParams();
    useEffect(() => {
        if (location) {
            const path = location.pathname;
            const pathArray = path.split("/");
            const breadcrumbItems = [];

            pathArray.forEach((item, index) => {
                switch (item) {
                    case "material-planner-plan":
                        breadcrumbItems.push({
                            label: "Home",
                            path: "/material-planner",
                        });
                        breadcrumbItems.push({
                            label: "Order ID",
                            path: "",
                        });
                        break;
                    case "plan":
                        breadcrumbItems.push({
                            label: "Plan",
                            path: "/material-planner/plan",
                        });
                        break;
                    default:
                        break;
                }
            });
            // Add params in the last item
            breadcrumbItems.push({
                label: searchParams.get("orderOrderId"),
                path: "",
            });
            setItems(breadcrumbItems);
        }
    }, [location]);

    return (
        <nav aria-label="Breadcrumb">
            <ol className="breadcrumb-container">
                {items.map((item, index) => (
                    <li key={item.path}>
                        <BreadcrumbItem
                            label={item.label}
                            path={item.path}
                            isLast={index === items.length - 1}
                        />
                        {index < items.length - 1 && (
                            <span className="breadcrumb-separator">
                                {/* using angle bracket */}
                                <ChevronRightIcon />
                            </span>
                        )}
                    </li>
                ))}
            </ol>
        </nav>
    );
};

Breadcrumb.propTypes = {
    items: PropTypes.arrayOf(
        PropTypes.shape({
            label: PropTypes.string.isRequired,
            path: PropTypes.string.isRequired,
        })
    ).isRequired,
};

export default Breadcrumb;
