import React, { useRef, useState, useEffect } from "react";
import { t } from "i18next";

// components
import Button from "../../../components/buttonn";
// import QRCode from "react-qr-code";
import TSCPrinter from "../../../components/TSCPrinter";
// Assets
// const modalStateData = {
//   msg1: [
//     "Please break the packet into two parts ",
//     "Make first Packet with 500 Units ",
//     "Make second Packet with 700 Units",
//   ],
//   msg2: [
//     "Print the Bar Code and Paste",
//     "Barcode 1 on First packet",
//     "Barcode 2 on Second packet",
//   ],
// };
const LabelsGeneratedModal = ({
  qty1 = 0,
  qty2 = 0,
  label1 = "",
  label2 = "",
  noStepFn = () => {},
  nextStepFn = () => {},
}) => {
  const parentRef = useRef();
  const handlePrint = () => {
    window.print();
  };
  const [printData, setPrintData] = useState(null);

  useEffect(() => {
    const [materialCode, quantityString, unit, grnNumber, grnDate] =
      label1.split("#");
    const [materialCode2, quantityString2, unit2, grnNumber2, grnDate2] =
      label2.split("#");
    const transformedKittingLabelDataArray = [
      {
        head: "Dhoot Transmission",
        material_code: materialCode,
        quantity: quantityString,
        unit: unit,
        grn_number: grnNumber,
        grn_date: grnDate,
        QR_DATA: label1,
      },
      {
        head: "Dhoot Transmission",
        material_code: materialCode2,
        quantity: quantityString2,
        unit: unit2,
        grn_number: grnNumber2,
        grn_date: grnDate2,
        QR_DATA: label2,
      },
    ];
    console.log(
      "transformedKittingLabelDataArray",
      transformedKittingLabelDataArray
    );
    setPrintData(transformedKittingLabelDataArray);
  }, []);

  return (
    <div className="action_modal__container">
      <div>
        <img src={"/error_outline.svg"} alt="icon" />
      </div>
      <span>Please break the packet into 2 parts: </span>
      <span>
        First one with <strong>{qty1}</strong> units, and second one with{" "}
        <strong>{qty2}</strong> units.{" "}
      </span>
      <span>
        Then, please paste label: <strong>{label1}</strong> on the first packet,
      </span>

      <span>
        and label: <strong>{label2}</strong> on the second packet.{" "}
      </span>

      <TSCPrinter kitting={false} payload={printData} />

      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
        }}
      >
        <Button
          onClick={nextStepFn}
          color="green"
          width="16rem"
          height="2.5rem"
        >
          I have pasted new labels
        </Button>
      </div>
    </div>
  );
};

export default LabelsGeneratedModal;
