import { React, useState } from "react";
import MTable from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import TextField from "@mui/material/TextField";
import { Stack, TableFooter, TablePagination, Typography } from "@mui/material";
import Spin from "../spin";
import empty from "../../assets/png/empty.png";
import Input from "../input";
import { COLORS } from "../../helpers/constants";

const Table = ({
  rows = [],
  columns = [],
  minWidth = 650,
  tableClasses = "",
  titleCellClasses = "",
  rowClassesMethod = () => {},
  rowClasses = "",
  rowCellClasses = "",
  rowsPerPage = 10,
  paginator = false,
  loading,
  dense,
  hideBorder = false,
  onRowClick = Function.prototype,
  onTableHeadCellClick = Function.prototype,
  handleFgCodeFilterInput = Function.prototype,
  handleOrderIdFilterInput = Function.prototype,
  handleOrderTypeFilterInput = Function.prototype,
  handleOrderStatusFilterInput = Function.prototype,
  serialize = false,
  page = 0,
  setPage = Function.prototype,
  total,
}) => {
  const [orderIdFilterInputValue, setOrderIdFilterInputValue] = useState(null); // only for fg code filter input box functionality purpose
  const [fgCodeFilterInputValue, setFgCodeFilterInputValue] = useState(null); // only for fg code filter input box functionality purpose
  const [orderTypeFilterInputValue, setOrderTypeFilterInputValue] =
    useState(null); // only for order type filter input box functionality purpose
  const [orderStatusFilterInputValue, setOrderStatusFilterInputValue] =
    useState(null); // only for order status filter input box functionality purpose
  return (
    <TableContainer>
      <MTable
        sx={{ minWidth: minWidth }}
        className={`${tableClasses}`}
        size={dense && "small"}
      >
        <TableHead sx={{ backgroundColor: COLORS.BG_GRAY }}>
          <TableRow>
            {serialize && (
              <TableCell
                className={`${titleCellClasses} text-nowrap h3`}
                sx={{ color: "black" }}
              >
                S No.
              </TableCell>
            )}
            {columns.map((col, i) => (
              <TableCell
                style={{ width: col.width && col.width, color: "black" }}
                key={i}
                className={`${titleCellClasses} text-nowrap`}
              >
                <div onClick={() => onTableHeadCellClick(col.id)}>
                  {col.title}
                </div>
                {col.id === "order_id" && (
                  <input
                    type="text"
                    placeholder={`filter by order_id`}
                    value={orderIdFilterInputValue}
                    onChange={(e) => {
                      console.log(`Updated value: ${e.target.value}`);
                      setOrderIdFilterInputValue(e.target.value);
                      handleOrderIdFilterInput(e.target.value);
                    }} // Replace with your update handler
                    style={{
                      borderRadius: "0.35vw",
                      border: "1px solid grey",
                      padding: "0.25vw",
                      width: "90%",
                      fontSize:'0.8rem'
                    }} // Adjust styling as needed
                  />
                )}
                {col.id === "fg_code" && (
                  <input
                    type="text"
                    placeholder={`filter by ${col.id}`}
                    value={fgCodeFilterInputValue}
                    onChange={(e) => {
                      console.log(`Updated value: ${e.target.value}`);
                      setFgCodeFilterInputValue(e.target.value);
                      handleFgCodeFilterInput(e.target.value);
                    }} // Replace with your update handler
                    style={{
                      borderRadius: "0.35vw",
                      border: "1px solid grey",
                      padding: "0.25vw",
                      width: "90%",
                    }} // Adjust styling as needed
                  />
                )}
                {col.id === "order_type" && (
                  <select
                    value={orderTypeFilterInputValue}
                    onChange={(e) => {
                      console.log(`Updated value: ${e.target.value}`);
                      setOrderTypeFilterInputValue(e.target.value);
                      handleOrderTypeFilterInput(e.target.value);
                    }}
                    style={{
                      borderRadius: "0.35vw",
                      border: "1px solid grey",
                      padding: "0.25vw",
                      width: "10vw",
                    }} // Adjust styling as needed
                  >
                    <option value="">Select Order Type</option>
                    <option value="ACC">ACC</option>
                    <option value="LPC">LPC</option>
                    <option value="ASSEMBLY">ASSEMBLY</option>
                  </select>
                )}

                {col.id === "status" && (
                  <select
                    value={orderStatusFilterInputValue}
                    onChange={(e) => {
                      console.log(`Updated value: ${e.target.value}`);
                      setOrderStatusFilterInputValue(e.target.value);
                      handleOrderStatusFilterInput(e.target.value);
                    }}
                    style={{
                      borderRadius: "0.35vw",
                      border: "1px solid grey",
                      padding: "0.25vw",
                      width: "8vw",
                    }} // Adjust styling as needed
                  >
                    <option value="">Select Status</option>
                    <option value="PLAN">PLAN</option>
                    <option value="ISSUING">ISSUING</option>
                    <option value="RUNNING">RUNNING</option>
                  </select>
                )}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <Stack sx={{ p: 0.3 }}></Stack>
        <TableBody>
          {loading ? (
            <TableRow>
              <TableCell colSpan={12} className="flex h-300 text-center">
                <Spin big blue />
              </TableCell>
            </TableRow>
          ) : rows && rows.length ? (
            rows.map((row, i) => (
              <>
                <TableRow
                  key={i}
                  sx={{
                    backgroundColor: COLORS.LG_GRAY,
                    [`& .${tableCellClasses.root}`]: {
                      borderBottom: hideBorder ? "none" : "true",
                      mt: 10,
                    },
                    ...rowClassesMethod(i, row),
                  }}
                  className={`${rowClasses}`}
                  onClick={() => onRowClick(row.id)}
                >
                  {serialize && (
                    <TableCell
                      sx={{ color: "black" }}
                      className={`${rowCellClasses} fw-500 `}
                      align="left"
                    >
                      {i + 1}
                    </TableCell>
                  )}
                  {columns.map((col, index) => {
                    return (
                      <TableCell
                        key={index}
                        sx={{ width: col.width && col.width, color: "black" }}
                        className={`${rowCellClasses} fw-500`}
                        align="left"
                      >
                        {col.render(row)}
                      </TableCell>
                    );
                  })}
                </TableRow>
                <Stack sx={{ p: 0.3 }}></Stack>
              </>
            ))
          ) : (
            <TableRow>
              <TableCell
                colSpan={12}
                className="text-center h-300 flex items-center opacity-60 w-full"
              >
                <img
                  src={empty}
                  alt="leaf"
                  height={100}
                  className="w-full object-contain"
                />
                <p className="text-center">No Records found!</p>
              </TableCell>
            </TableRow>
          )}
        </TableBody>
        {paginator && (
          <TableFooter>
            <TableRow>
              <TablePagination
                rowsPerPageOptions={[]}
                count={total || rows.length}
                rowsPerPage={rowsPerPage}
                onPageChange={(e, newValue) => setPage(newValue)}
                page={page}
              />
            </TableRow>
          </TableFooter>
        )}
      </MTable>
    </TableContainer>
  );
};

export default Table;
