import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useAccCircuit from "../../store/AccCircuit";
import Button from "../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete, Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import JobDetail from "./JobDetail";
import StartJob from "./startJob"
import JsonFormatter from "../../components/UI/JsonFormatter";
import Input from "../../components/input";

const AccCircuitMaster = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const plantId = location.state
  const [users, setUsers] = useState([{'jobid':"JOB_123",'client':"unx@32","operations":5,"qty":200}]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });
  const { list, create, update, remove } = useAccCircuit();

  useEffect(() => {
   fetchList()
  }, [page]);

  const fetchList = async () => {
    setLoading("fetch");
    const [_list, _count] = await list(page + 1, plantId);
    setUsers(_list);
    setTotal(_count);
    setLoading(false);
  };

  const handleAdd = async () => {
    const payload = { ...data }
    setLoading("fetch");
    edit === true ? await update(payload) : await create(payload, plantId);
    setData({});
    setModalOpen(false);
    await fetchList();
    setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchList();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    await update(id, payload);
    await fetchList();
    setLoading(false);
  };

  const LABELS = [
    {
      id: 1,
      title: "Circuit Name",
      render: (rowData) => rowData?.name
    },
    {
      id: 2,
      title: "Circuit Code",
      render: (rowData) => rowData?.circuit_code
    },
    {
      id: 3,
      title: "Description",
      render: (rowData) => rowData?.circuit_description
    },
    {
      id: 5,
      title: "Materials Req",
      render: (rowData) => rowData?.qty
    },
   
  ];

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">ACC Circuit Master</h1>
        {/* <Button>add</Button> */}
        {/* <Stack sx={{ flexDirection: 'row', gap: 2 }}>
          <Stack sx={{ backgroundColor: COLORS.LG_GREEN, width: 240, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>Today<br></br> Jobs</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>38</Typography>
          </Stack>
          <Stack sx={{ backgroundColor: COLORS.LG_PINK, width: 240, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>Remaining <br></br>Jobs</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>38</Typography>
          </Stack>
        </Stack> */}
       
      </div>
      {/* <Stack sx={{mb:1}}>
      <Input placeholder='Search By material code'/>
      </Stack> */}
      <Table
      // headColor={COLORS.TEXT_GRAY}
      // headLabelColor={COLORS.WHITE}
        columns={LABELS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <JobDetail
        open={modalOpen === "detail"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
      <StartJob
        open={modalOpen === "start"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
      {/* <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={2}
        mb={1}
        // sx={{px:6}}
      >
        <Button bordered gray >
          Cancel
        </Button>
        <Button color={'gray'} >
          <Typography color={COLORS.TEXT_GRAY}>
          Next
          </Typography>  
        </Button>
      </Stack> */}
    </div>
  );
};

export default AccCircuitMaster;
