import React,{useState} from "react";
import Modal from "../../components/modal";
import { Stack,Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import JsonCreator from "../../components/UI/JsonCreator";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const AddPlant = ({ open, onClose, data, setData, onSubmit, loading,edit}) => {
  const USER_SCHEMA = [
    {
      title: "Plant Name",
      placeholder: "Enter Plant Name",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
   
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
    hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit=== true?"EDIT":"ADD"} NEW PLANT</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
          <JsonCreator
          setItemss={data.metadata}
          setJson={(value) =>
            setData((prev) => ({ ...prev, metadata: value }))
          }
          title="Details"
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={onSubmit} loading={loading}>
         <Typography className="fw-20-500-24">{t('create')}</Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddPlant;
