import React, { useRef, useState, useEffect } from "react";
import { t } from "i18next";
import Button from "../../../components/buttonn"; // Adjust the import path as needed

const FailureReasonMaterialSelectionModal = ({
  onClose,
  onScan,
  noStepFn,
  nextStepFn,
  materialCodes,
}) => {
  console.log("materialCodes", materialCodes);
  const [selectedCode, setSelectedCode] = useState(null);
  const handleRadioChange = (code) => {
    setSelectedCode(code);
  };

  return (
    <div className="action_modal__container">
      <span style={{ fontWeight: "600", fontSize: "1.1rem" }}>
        Please select the reference material
      </span>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignContent: "start",
          width: "100%",
          gap: "1vw",
        }}
      >
        {materialCodes &&
          materialCodes.map((code, index) => (
            <div
              key={index}
              style={{ display: "flex", gap: "1vw", alignItems: "baseline" }}
            >
              <input
                type="radio"
                id={`material-${index}`}
                name="materialCode"
                value={code}
                checked={selectedCode === code}
                onChange={() => handleRadioChange(code)}
              />
              <label htmlFor={`material-${index}`}>{code}</label>
            </div>
          ))}
        <div></div>
      </div>
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
        }}
      >
        <Button
          onClick={noStepFn}
          white={true}
          width="8rem"
          height="2.5rem"
          bordered={true}
          customStyle={{
            border: "1px solid #3E3E3E",
            backgroundColor: "#FFFFFF",
          }}
        >
          {t('cancel')}
        </Button>
        <Button
          onClick={() => {
            nextStepFn(selectedCode);
          }}
          color="green"
          width="8rem"
          height="2.5rem"
          disabled={selectedCode===null}
        >
          {t('submit')}
        </Button>
      </div>
    </div>
  );
};

export default FailureReasonMaterialSelectionModal;
