import React, { useRef, useState, useEffect } from "react";
import Button from "../../../components/buttonn"; // Adjust the import path as needed
// import QRCode from "react-qr-code";
import { useScanning } from "../../../store/materialPlanner";
import TSCPrinter from "../../../components/TSCPrinter";
import { t } from "i18next";

const NewStockTransferLabelDisplayModal = ({
  onClose,
  onScan,
  noStepFn,
  nextStepFn,
  newLabel
}) => {
  const printRef = useRef();
  const handlePrint = () => {
    window.print();
  };
  const [printData, setPrintData] = useState(null);
  useEffect(() => {
      const [materialCode, quantityString, unit, grnNumber, grnDate] =
        newLabel.split("#");
      const transformedKittingLabelDataArray = [
        {
          head: "Dhoot Transmission",
          material_code: materialCode,
          quantity: quantityString,
          unit: unit,
          grn_number: grnNumber,
          grn_date: grnDate,
          QR_DATA: newLabel,
        }
      ];
      console.log(
        "transformedKittingLabelDataArray",
        transformedKittingLabelDataArray
      );
      setPrintData(transformedKittingLabelDataArray);
    }, []);
  return (
    <div className="action_modal__container">
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "1rem",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <p>Stock Transfer Success!</p>
        <p>Please paste the following new label on the scanned packet.</p>
        <p>
          <strong>{newLabel}</strong>
        </p>
        <TSCPrinter kitting={false} payload={printData} />
        <Button onClick={nextStepFn} color="blue" width="8rem" height="2.5rem">
          OK
        </Button>
      </div>
    </div>
  );
};

export default NewStockTransferLabelDisplayModal;
