import React, { useState } from 'react';

import './AddIssue.css';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: '#D9D9D9',
    color: '#3E3E3E',
    fontSize: 'clamp(16px, 1.5vw, 20px)',
    fontWeight: 'bold',
    padding: '20px 16px',
    borderBottom: '1px',
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 'clamp(14px, 1.2vw, 20px)',
    padding: '16px',
    borderBottom: '1px solid #D9D9D9',
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:last-child td, &:last-child th': {
    borderBottom: '1px',

  },
  '& td, & th': {
    border: 'none',
    height: '60px'
  },
}));

const modalStyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 600,
  maxWidth: '90%',
  bgcolor: 'background.paper',
  boxShadow: 24,
  p: 4,
  borderRadius: '8px',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  maxHeight: '90vh',
  overflowY: 'auto',
};

const AddIssue = () => {
  const [open, setOpen] = useState(false);
  const [reasonName, setReasonName] = useState('');
  const [required, setRequired] = useState('Yes');
  const [rows, setRows] = useState([]);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleSubmit = () => {
    if (!reasonName) {
      setOpen(false);
      return;
    }

    const newRow = {
      reasonName,
      resolutionDate: required,
    };

    setRows([...rows, newRow]);
    setReasonName('');
    setRequired('Yes');
    setOpen(false);
  };

  return (
    <div className="mx-3 my-2">
     
      <div className="add-issue">
        <h1>Issues</h1>
        <Button
          onClick={handleOpen}
          sx={{
            backgroundColor: 'rgb(217, 217, 217)',
            color: '#3E3E3E',
            fontSize: '20px',
            fontWeight: '500',
            border: 'none',
            padding: '10px 20px',
            '&:hover': {
              backgroundColor: 'rgb(200, 200, 200)',
            },
          }}
        >
          + Add Issue
        </Button>
      </div>

      <TableContainer 
        component={Paper} 
        sx={{ 
          width: 'auto',
        //   marginLeft:'20px',
          marginTop:'10px',
          overflowX: 'auto',
          borderRadius: 0,
          
          borderBottom: '1px solid #D9D9D9',
          boxShadow: 'none'

        }}
      >
        <Table aria-label="customized table"
         sx={{
          border: 'none', // Removes default border of the table
          '& .MuiTableCell-root': {
            border: 'none', // Removes borders from individual table cells
          }
        }}  >
          <TableHead>
            <TableRow>
              <StyledTableCell sx={{ width: '10%' }}>S No</StyledTableCell>
              <StyledTableCell sx={{ width: '45%' }}>Reason Name</StyledTableCell>
              <StyledTableCell sx={{ width: '45%' }}>Resolution Date Required</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <StyledTableRow key={row.reasonName}>
                <StyledTableCell>{index + 1}</StyledTableCell>
                <StyledTableCell>{row.reasonName}</StyledTableCell>
                <StyledTableCell>{row.resolutionDate}</StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="add-issue-modal"
      >
        <Box sx={modalStyle}>
          <div style={{ 
            border:'none',
            display: 'flex', 
            justifyContent: 'space-between', 
            width: '100%',
            marginBottom: '20px'
          }}>
            <h2 style={{ 
              fontSize: '24px',
              margin: 0 
            }}>
              Add Issue
            </h2>
            <Button 
              onClick={handleClose}
              sx={{ 
                minWidth: 'auto', 
                marginTop: '-15px',
                padding: 0,
                '&:hover': { backgroundColor: 'transparent' } 
              }}
            >
              <svg xmlns="http://www.w3.org/2000/svg" width="45" height="45"  fill="none">
                <path fill="#F84343" d="M24 4C12.94 4 4 12.94 4 24s8.94 20 20 20 20-8.94 20-20S35.06 4 24 4m0 36c-8.82 0-16-7.18-16-16S15.18 8 24 8s16 7.18 16 16-7.18 16-16 16m7.18-26L24 21.18 16.82 14 14 16.82 21.18 24 14 31.18 16.82 34 24 26.82 31.18 34 34 31.18 26.82 24 34 16.82z"/>
              </svg>
            </Button>
          </div>

          <div style={{ 
            display: 'flex', 
            alignItems: 'center',
            gap: '20px',
            width: '100%', 
            marginBottom: '16px' 
          }}>
            <h3 style={{
              fontSize: '20px',
              fontWeight: 500,
              margin: 0,
              fontFamily: 'metropolis',
              minWidth: '120px',
              borderBottom:'none'
            }}>
              Reason Name
            </h3>
            <TextField
              fullWidth
              placeholder="Enter reason name"
              value={reasonName}
              onChange={(e) => setReasonName(e.target.value)}
            />
          </div>

          <div style={{ 
            display: 'flex', 
            alignItems: 'center',
            gap: '20px',
            width: '100%', 
            marginBottom: '16px' 
          }}>
            <h3 style={{
              fontSize: '20px',
              fontWeight: 500,
              margin: 0,
              minWidth: '120px',
              borderBottom:'none'
            }}>
              Required
            </h3>
            <FormControl component="fieldset">
              <RadioGroup
                row
                value={required}
                onChange={(e) => setRequired(e.target.value)}
              >
                <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
                <FormControlLabel value="No" control={<Radio />} label="No" />
              </RadioGroup>
            </FormControl>
          </div>

          <Button
            variant="contained"
            fullWidth
            onClick={handleSubmit}
            sx={{
              mt: 2,
              backgroundColor: 'rgb(134, 213, 106)',
              color: 'rgb(5, 4, 4)',
              fontSize: '20px',
              fontWeight: '500',
              border: 'none',
              height: '40px',
              width: '85px',
              opacity: reasonName === '' ? 0.8 : 1,
              '&:hover': {
                backgroundColor: 'rgb(134, 213, 106)',
              },
            }}
          >
            Submit
          </Button>
        </Box>
      </Modal>
    </div>
  );
};

export default AddIssue;