import { React, useState, useEffect } from "react";
import { t } from "i18next";

// components
import Button from "../../../components/buttonn";
// Assets
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import { useOrder } from "../../../store/materialPlanner";

const PartialFulfilledOrderActionModal = ({
  setLocalUI,
  data = {},
  noStepFn = () => {},
  nextStepFn = () => {},
  isPartialFulfilledOrderActionModalEditBtnEnabled = false,
}) => {
  const {
    fetchOrdersById,
    error: orderError,
    loading: ordersLoading,
  } = useOrder();
  console.log("PartialFulfilledOrderActionModal data", data);
  const [selectedOrderDetails, setSelectedOrderDetails] = useState(null);
  useEffect(() => {
    if (data && data.orderId) {
      fetchOrdersById(data.orderId).then((fetchedData) => {
        console.log("setSelectedOrderDetails data", fetchedData);
        setSelectedOrderDetails(fetchedData);
      });
    }
  }, [data]);

  return (
    <div className="action_modal__container">
      {/* for ASSEMBLY type orders, Run btn is disabled. User has to go to edit page first to be checked for order completion i.e. whether all items are collected or not. When the user is redirected back to the overview page from the scanning page, FulfilledOrderActionModal is opened which has an option to Run the order. This is done as checks are not done while scanning from mobile app. */}
      <div>
        <CheckCircleOutlineIcon sx={{ fontSize: "6rem", color: "#86D56A" }} />
      </div>
      <p>
        Do you want to Edit
        {isPartialFulfilledOrderActionModalEditBtnEnabled && (
          <span> or Run</span>
        )}
      </p>
      <span>
        Order {selectedOrderDetails?.order_id}{" "}
        <strong>{selectedOrderDetails?.name}</strong>{" "}
      </span>
      <br />
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
        }}
      >
        {/* if isPartialFulfilledOrderActionModalEditBtnEnabled is false, then edit
        btn styling will be same as Run btn when
        isPartialFulfilledOrderActionModalEditBtnEnabled is true */}
        <Button
          onClick={noStepFn}
          white={
            isPartialFulfilledOrderActionModalEditBtnEnabled ? true : false
          }
          color={
            isPartialFulfilledOrderActionModalEditBtnEnabled ? "" : "green"
          }
          width="8rem"
          height="2.5rem"
          bordered={
            isPartialFulfilledOrderActionModalEditBtnEnabled ? true : false
          }
          customStyle={
            isPartialFulfilledOrderActionModalEditBtnEnabled
              ? {
                  border: "1px solid #3E3E3E",
                  backgroundColor: "#FFFFFF",
                }
              : {}
          }
        >
          Edit
        </Button>
        {isPartialFulfilledOrderActionModalEditBtnEnabled && (
          <Button
            onClick={nextStepFn}
            color="green"
            width="8rem"
            height="2.5rem"
          >
            Run
          </Button>
        )}
      </div>
    </div>
  );
};

export default PartialFulfilledOrderActionModal;
