import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useCompany from "../../store/company";
import Button from "../../components/button";
import AddCompany from "./AddCompany";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import { Delete ,Checklist} from "@mui/icons-material";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import { COLORS } from "../../helpers/constants";
import Select from "../../components/input/Select";
import Input from "../../components/input";
import Checkbox from '@mui/material/Checkbox';
import useUser from "../../store/users";
import { t } from "i18next";

const Notification = () => {
  const navigate = useNavigate()
  const [users, setUsers] = useState([{"assets":"machine1"}]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    email_notification:'',
    sms_notification:'',
    whatsapp_notification:''


  });
  const { list, create,update, remove } = useCompany();
  const {  getNotification, postNotification} = useUser();

  useEffect(() => {
    fetchCompany();
  }, []);

  const fetchCompany = async () => {
    setLoading("fetch");
    const res = await getNotification(page + 1);
    console.log("notification fetch",res)
    if(res.email_notification){
      setData((prev) => ({ ...prev, email_notification: res.email_notification.toString(),sms_notification:res.sms_notification.toString(),whatsapp_notification:res.whatsapp_notification.toString() }));
      // setUsers(_list);
      // setTotal(_count);
    }
    setLoading(false);
    
  };

  const handleAdd = async () => { 
    const payload = {...data} 
    console.log("payload",payload)
    setLoading("fetch");
    edit ===true? await update(payload) : await create(payload);
    setData({});
    setModalOpen(false);
    await fetchCompany();
    setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    await fetchCompany();
    setLoading(false);
  };

  const SEARCH = [
    {
      id: 'assets',
      title: "Assets"
    },
    {
      id: 'online',
      title: "Online",
    },
    {
      id: 'downtime',
      title: "Downtime"
    }, 
    {
      id: 'offline',
      title: "Offline",
    }
   
  ];
  const COLUMNS = [
    {
      id: 1,
      title: "Assets",
      render: (rowData) => rowData.assets,
    },
    {
      id: 2,
      title: "Online",
      render: (rowData) =>  <Checkbox />
    },
    {
      id: 3,
      title: "Offline",
      render: (rowData) =>  <Checkbox />
    },
    {
      id: 4,
      title: "Downtime",
      render: (rowData) =>  <Checkbox />
    },
    {
      id: 5,
      title: "Action",
      render: (rowData) => (<Button>Confirm</Button>
      ),
    },
  ];

  const FormSchema =[
    {
          title: t('emailNotification'),
          type: SCHEMA_TYPES.RADIO,
          name: "email_notification",
          validator: VALIDATORS.REQUIRED,
          keyValues: [{ "value": true, "label": t('yes') }, { "value": false, "label": t('no') }],
          // width: '350px',
          pl: 0.2
        },
    {
          title: t('smsNotification'),
          type: SCHEMA_TYPES.RADIO,
          name: "sms_notification",
          validator: VALIDATORS.REQUIRED,
          keyValues: [{ "value": true, "label": t('yes') }, { "value": false, "label": t('no')}],
          // width: '350px',
          pl: 0.2
        },
    {
          title: t('whatsappNotification'),
          type: SCHEMA_TYPES.RADIO,
          name: "whatsapp_notification",
          validator: VALIDATORS.REQUIRED,
          keyValues: [{ "value": true, "label":t('yes')}, { "value": false, "label": t('no') }],
          // width: '350px',
          pl: 0.2
        },
  ]

    const [errors, setErrors] = useState(new Map());
  
    const handleChange = (e) => {
      const { name, value } = e.target;
      // setErrors((prev) => {
      //   prev.delete(name);
      //   return prev;
      // });
      setData((prev) => ({ ...prev, [name]: value }));
    };
  
    const handleSubmit=async ()=>{
      setLoading("fetch");
      const payload=   {
        email_notification:data.email_notification==='true'?true:false,
        sms_notification:data.sms_notification==='true'?true:false,
        whatsapp_notification:data.whatsapp_notification==='true'?true:false
    }
     
      let res = await postNotification(payload)
      // setLoading(false);
      await fetchCompany()
    }

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
         <h1 className="mt-4">{t('notificationSettings')}</h1>  
      </div>
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={FormSchema}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        {/* <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button> */}
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
          <Typography className="fw-20-500-24">{t('create')}</Typography>
        </Button>
      </Stack>
     
    </div>
  );
};

export default Notification;
