import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useUserTenant = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (page = 1,tenant_id) => {
    const client = new REQUEST();
    const res = await client.get(
      `${tenant_id}/${ENDPOINTS.USERS}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const opratorList = async (page = 1,tenant_id) => {
    const client = new REQUEST();
    const res = await client.get(
      `${tenant_id}/${ENDPOINTS.OPERATORS}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const create = async (payload,tenant_id) => {
    const client = new REQUEST();
    const res = await client.post(
      `${tenant_id}/${ENDPOINTS.USERS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User created successfully!")
    );
    return res?.data;
  };
  const createOperator = async (payload,tenant_id) => {
    const client = new REQUEST();
    const res = await client.post(
      `${tenant_id}/${ENDPOINTS.OPERATORS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Operator created successfully!")
    );
    return res?.data;
  };
  const update = async (tenant_id,id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${tenant_id}/${ENDPOINTS.USER}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User Updated successfully!")
    );
    return res?.data;
  };
  const updateOperator = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.OPERATOR}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Operator Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.USER}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "User deleted successfully!")
    );
    return res?.data;
  };
  const removeOperator = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.OPERATOR}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "Operator deleted successfully!")
    );
    return res?.data;
  };

  return {
    list,
    create,
    update,
    remove,
    opratorList,
    createOperator,
    updateOperator,
    removeOperator
  };
};

export default useUserTenant;
