import "./style.scss";
import React, { useState, useEffect } from "react";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import Table from "../../../components/table";
// components
import Button from "../../../components/buttonn";
import { useOrder, useScanning } from "../../../store/materialPlanner";
import FailureReasonSuccessModal from "./FailureReasonSuccessModal";
import FailureReasonInputModal from "./FailureReasonInputModal";
import { t } from "i18next";

function AddfailureReasonPage() {
  const COLUMNS = () => [
    {
      id: "reason_name",
      title: t("reasonName"),
      render: (rowData) => <span>{rowData.name}</span>,
    },
    {
      id: "resolution_date_required",
      title: t('resolutionDateRequired'),
      render: (rowData) => (
        <span>{rowData.resolution_date_required ? t("yes") : t("no")}</span>
      ),
    },
  ];
  const {
    postFailureReason,
    error: orderError,
    loading: ordersLoading,
  } = useOrder();
  const { getFailureReasons } = useScanning();
  const [failureReasonsData, setFailureReasonsData] = useState(null);
  const [failureReasonsDataArray, setFailureReasonsDataArray] = useState(null);
  const [isFailureReasonInputModalOpen, setisFailureReasonInputModalOpen] =
    useState(false);
  const [failureReasonText, setFailureReasonText] = useState(null);
  const [isResolutionDateRequired, setIsResolutionDateRequired] =
    useState(null);
  const [isfailureReasonSuccessModalOpen, setIsFailureReasonSuccessModalOpen] =
    useState(false);

  const handleSubmit = () => {
    postFailureReason(failureReasonText, isResolutionDateRequired)
      .then((data) => {
        console.log("postFailureReason data", data);
        if (data && data.message === "OK") {
          setIsFailureReasonSuccessModalOpen(true);
          setisFailureReasonInputModalOpen(false);
        }
      })
      .catch((error) => {
        console.error("postFailureReason error", error);
      });
  };

  useEffect(() => {
    getFailureReasons().then((data) => {
      setFailureReasonsData(data);
      setFailureReasonsDataArray(Object.values(data));
    });
  }, [isfailureReasonSuccessModalOpen]);

  return (
    <div className="mx-3 my-2 mp-instruction-layout">
      {/* Header   */}
      <div className="mp-instruction-header">
        <p className="issues-header">
        <Typography className="fw-20-500-36">{t('failureReasons')}</Typography>
          <div className="add-issue-btn">
            {!isFailureReasonInputModalOpen && (
              <Button
                onClick={() => {
                  setisFailureReasonInputModalOpen(true);
                }}
                color="gray"
                customStyle={{
                  borderRadius: "0.25rem",
                  border: "1px solid #D9D9D9",
                  padding: "0.7vw",
                  fontSize: "1rem",
                  color: "black",
                }}
              >
                {t("addFailureReasons")}
              </Button>
            )}
          </div>
        </p>
      </div>

      {/* Body   */}
      <div className="material-planner-layout-body">
        <Table
          columns={COLUMNS()}
          rows={failureReasonsDataArray}
          titleCellClasses="c-gray fw-700"
          serialize
        />
      </div>

      {/* Failure Reason Input Modal */}
      {isFailureReasonInputModalOpen && (
        <FailureReasonInputModal
          open={isFailureReasonInputModalOpen}
          onClose={() => {
            setisFailureReasonInputModalOpen(false);
          }}
          failureReasonText={failureReasonText}
          setFailureReasonText={setFailureReasonText}
          isResolutionDateRequired={isResolutionDateRequired}
          setIsResolutionDateRequired={setIsResolutionDateRequired}
          handleSubmit={handleSubmit}
        />
      )}
      {/* Failure Reason submit success Modal */}
      {isfailureReasonSuccessModalOpen && (
        <FailureReasonSuccessModal
          open={isfailureReasonSuccessModalOpen}
          onClose={() => {
            setIsFailureReasonSuccessModalOpen(false);
            setFailureReasonText("");
          }}
          failureReasonText={failureReasonText}
          isResolutionDateRequired={isResolutionDateRequired}
          nextStepFn={() => {
            setIsFailureReasonSuccessModalOpen(false);
          }}
        />
      )}
    </div>
  );
}

export default AddfailureReasonPage;
