import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useDowntime = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const matrixList = async (date) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/downtime-matrix/${date}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `settings/downtime-reasons/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const all_downtime_list = async (
    page,
    p_s,
    company_id,
    plant_id,
    division_id,
    line_id,
    date) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/all-downtime-list/${date}/?company_id=${company_id}&plant_id=${plant_id}&division_id=${division_id}&line_id=${line_id}&page=${page}&page_size=${p_s}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const unassi_downtime_list = async ( 
    page,
    p_s,
    company_id,
    plant_id,
    division_id,
    line_id,
    date) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/unassigned-downtime-list/${date}/?company_id=${company_id}&plant_id=${plant_id}&division_id=${division_id}&line_id=${line_id}&page=${page}&page_size=${p_s}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const reason_list = async (packetId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/downtime-reasons/${packetId}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const post_reason = async (packetId,payload = {}) => {
    const client = new REQUEST();
    const res = await client.patch(
      `monitoring/downtime-assign/${packetId}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };
  

  const assetList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `asset/all-assets/?page=1&page_size=100`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `settings/downtime-reasons/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const update = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `settings/downtime-reason/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null,"Downtime has been updated successfully.")
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `settings/downtime-reason/${id}/`,
      headers,
      (res) => resolveResponse(res, null,'Downtime has been successfully deleted.')
    );
    return res?.data;
  };

  return {
    matrixList,
    list,
    assetList,
    create,
    update,
    remove,
    all_downtime_list,
    unassi_downtime_list,
    reason_list,
    post_reason
  };
};

export default useDowntime;
