import React from 'react';
import ApexCharts from 'react-apexcharts';

const LineChart = () => {
  // Data for the line chart
  const options = {
    chart: {
      id: 'line-chart',
      toolbar: {
        show: false // Hides the chart's toolbar
      },
    },
    xaxis: {
      categories: ['1', '2', '3', '4', '5', '6', '7'], // X-axis labels
    },
    stroke: {
      curve: 'smooth', // Smooth line
    },
    title: {
      text: 'Spindle Speed',
      align: 'left',
    },
  };

  const series = [
    {
      name: 'Speed',
      data: [30, 40, 35, 50, 49, 60, 70], // Data points for the line
    },
  ];

  return (
    <div>
      <ApexCharts
        options={options}
        series={series}
        type="line" // Set chart type to line
        height={350} // Set the height of the chart
      />
    </div>
  );
};

export default LineChart;
