import React, { useEffect, useLayoutEffect, useState } from "react";
import MTable from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import PieChart from '../pie'
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import DhootLogo from '../../assets/png/DhootLogo.png'
import vvdnLogo from '../../assets/png/VVDNLogo.png'
import { useTranslation } from 'react-i18next';

const PrintPage = ({ row, skill }) => {
  let color_1 = [COLORS.L4, COLORS.L4, COLORS.L4, COLORS.L1]
  let color_2 = [COLORS.L2, COLORS.L4, COLORS.L4, COLORS.L1]
  let color_3 = [COLORS.L2, COLORS.L3, COLORS.L4, COLORS.L1]
  let color_4 = [COLORS.L2, COLORS.L3, COLORS.L5, COLORS.L1]
  let color_5 = [COLORS.L4, COLORS.L4, COLORS.L4, COLORS.L4]

  useLayoutEffect(() => {
    console.log("PrintPage--skill----", skill)
    console.warn("PrintPage--skill----", row)
  }, [row])

  return (
    <div className="mx-4 my-4">
      <Stack sx={{justifyContent:'center',alignItems:'center'}}>
      <img src={process.env.REACT_APP_BASE_LOGO==='dhoot'?DhootLogo:vvdnLogo} style={{width:'230px',height:'75px',objectFit:'contain'}}/>
      <Stack sx={{borderBottom: '2px solid #000',my:2}}>
      <Typography className="fw-36" sx={{}}>Operator Skill Matrix</Typography>
      </Stack>
      </Stack>
       <Stack sx={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'space-between'}}>
        <Stack>
        <Typography>Operator Name : {row.fullname}</Typography>
        <Typography>Aadhaar Number : {row.aadhar}</Typography>
        </Stack>
        <Stack sx={{flexDirection: 'row', gap: 2, alignItems: 'center', justifyContent: 'center'}}>
        <Stack sx={{ width: 50 }}>
          <PieChart
            chartColors={color_1}
            chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
          />
          <Typography sx={{ fontSize: 12 }} textAlign='center'>Induction</Typography>
        </Stack>
        <Stack sx={{ width: 50 }}>
          <PieChart
            chartColors={color_2}
            chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
          />
          <Typography sx={{ fontSize: 12 }} textAlign='center'>Level 1</Typography>
        </Stack>
        <Stack sx={{ width: 50 }}>
          <PieChart
            chartColors={color_3}
            chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
          />
          <Typography sx={{ fontSize: 12 }} textAlign='center'>Level 2</Typography>
        </Stack>
        <Stack sx={{ width: 50 }}>
          <PieChart
            chartColors={color_4}
            chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
          />
          <Typography sx={{ fontSize: 12 }} textAlign='center'>Level 3</Typography>
        </Stack>
        </Stack>
      </Stack>
      <Stack>    
      <Grid display={'grid'} gap={1} py={2} gridTemplateColumns={{ sm: 'repeat(5, 1fr)', xs: 'repeat(5, 1fr)' }}>
      {skill.map((item)=>{
        return(<Stack sx={{border:2,alignContent:'center',alignItems:'center',borderColor:COLORS.TEXT_GRAY}}>
          <Typography>{item.skill_name}</Typography>
          <Stack sx={{ width: 70 }}>
            <PieChart
              chartColors={row[item.id] === 0 ? color_1 : row[item.id] === 1 ? color_2 : row[item.id] === 2 ? color_3 : row[item.id] === 3 ? color_4 : color_5}
              chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
            />
          </Stack>
        </Stack>)
      })}
        </Grid>
      </Stack>
      <Typography sx={{mt:2}}> Dhoot Transmission Pvt. Ltd. All Rights Reserved. | Powered by: Smart Udyog</Typography>
    </div>
  );
};

export default PrintPage;
