import REQUEST, { CONTENT_TYPES } from "../../api";
import { ENDPOINTS } from "../../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "../..";
import { useState, useCallback } from "react";

const useBOM = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const apiRequest = new REQUEST();

  const fetchBOM = useCallback(
    async (id) => {
      const route = `${ENDPOINTS.ORDERS}/order-bom/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.get(route, {}, headers);        
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in fetching BOM by id");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  return {
    loading,
    error,
    fetchBOM,
  };
};

export { useBOM };
