import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useMachineGroup from "../../store/group/machine";
import useOperatorGroup from "../../store/group/operator";
import useUserGroup from "../../store/group/user";
import Button from "../../components/button";
import Assets from "./Assets";
import AddGroup from "./AddGroup";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Tab, Tabs } from "@mui/material";
import { useDispatch } from "react-redux";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete,Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import JsonFormatter from "../../components/UI/JsonFormatter";
import { setGroupId } from "../../store/group/itemGroup";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const GroupPage = () => {
  const dispatch = useDispatch()
  const location = useLocation()
  const navigate = useNavigate()
  const [tab , setTab] = useState("Users")
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
   name: "",
   metadata:{}
  });
  const { list, create,update, remove } = useMachineGroup();
  const { list:opGrouplist , create:opGroupCreate, update:opGroupUpdate, remove:opGroupRemove} = useOperatorGroup();
  const {list:userGrouplist, create:userGroupCreate, update:userGroupUpdate,remove:userGroupRemove} = useUserGroup()

  useEffect(() => {
    fetchCompany();
  }, [page,tab]);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list, _count] = tab=== "Machines"?await list(page + 1):await userGrouplist(page + 1);
    setUsers(_list);
    setTotal(_count);
    setLoading(false);
  };

  const handleAdd = async () => { 
    const payload = {...data} 
    setLoading("fetch");
    if(edit ===true){
      // await update(payload)
      tab=== "Machines"?await update(payload):tab=== "Operators"?await opGroupUpdate(payload):await userGroupUpdate(payload); 
    }else{
      tab=== "Machines"?await create(payload):tab=== "Operators"?await opGroupCreate(payload):await userGroupCreate(payload);
    }
    setData({
      name: "",
      metadata:{}
    });
    setModalOpen(false);
    await fetchCompany();
    setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    tab === "Machines"?await remove(id):tab=== "Operators"?await opGroupRemove(id):await userGroupRemove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    await fetchCompany();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: t('groupName'),
      render: (rowData) => 
      <Typography color='primary' sx={{cursor:'pointer'}} onClick={()=>{
        dispatch(setGroupId({"id":rowData.id,"name":rowData.name}));setModalOpen("addremove")}}>
          {rowData.name}
      </Typography>,
    },
    {
      id: 3,
      title: t('details'),
      render: (rowData) =>  Object.entries(rowData.metadata).map(([key, value]) => (
        key&&( <Typography key={key} className="fw-12-400">
          <strong>{key}:</strong> {value}
        </Typography>)
      ))
        // <Typography>{item}</Typography>
    ,
    },
    {
      id: 5,
      title: t('action'),
      render: (rowData) => (
        <Stack flexDirection={'row'}>
        {/* {!rowData?.is_admin &&(
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleUpdate(rowData?.id,rowData?.is_admin)}
        >
        <AdminPanelSettingsIcon color={rowData.is_admin?"error":"success"} fontSize="small" />
        </IconButton>
        )} */}
       
        <IconButton
        sx={{ height: 25, width: 25 }}
        onClick={()=>{setEdit(true);setModalOpen("add");setData(rowData)}}
      >
      <EditIcon color="primary" fontSize="small" />
      </IconButton>
      <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
          <Delete color="error" fontSize="small" />
        </IconButton>
      </Stack>
      ),
    },
  ];

  return (
    <div className="mx-3 my-2">
       {/* <Tabs value={tab} onChange={(_e, v) => {setTab(v);setPage(0)}} className="mb-2 fw-500">
          {["Users","Machines"].map((label, i) => (
            <Tab label={label} className="fw-500" value={label} />
          ))}
        </Tabs> */}
      <div className="flex items-center justify-between">
      <Stack sx={{flexDirection:'row',alignItems:'center',mb:1}}>
      {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
        <Typography className="fw-20-500-36">
         {t('usersGroups')}
        </Typography>
        </Stack>
        <Button color={'gray'} onClick={() => setModalOpen("add")}>
        <Typography className="fw-20-500-24">
          {t('addUsersGroup')}
          </Typography>
          </Button>
      </div>
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        tableBodyColor={COLORS.WHITE}
        serialize
        total={total}
      />
      <AddGroup
        open={modalOpen === "add"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
      {modalOpen === "addremove"&&(
      <Assets
        open={modalOpen === "addremove"}
        onClose={() =>{ setModalOpen(false)}}
        data={data}
        setData={setData}
        onSubmit={handleAdd}
        tab={tab}
      />
      )}
    </div>
  );
};

export default GroupPage;
