import React, { useRef, useEffect, useState } from "react";
import { t } from "i18next";

// components
import Button from "../../../components/buttonn";
import TSCPrinter from "../../../components/TSCPrinter";
// Assets
// const modalStateData = {
//   msg1: [
//     "Please break the packet into two parts ",
//     "Make first Packet with 500 Units ",
//     "Make second Packet with 700 Units",
//   ],
//   msg2: [
//     "Print the Bar Code and Paste",
//     "Barcode 1 on First packet",
//     "Barcode 2 on Second packet",
//   ],
// };
const KittingQRPrintModal = ({
  orderId = null,
  getKittingLabel = ()=>{},
  noStepFn = () => {},
  nextStepFn = () => {},
}) => {
  const parentRef = useRef();
  const handlePrint = () => {
    window.print();
  };
  const [printData, setPrintData] = useState(null);

  useEffect(() => {
    getKittingLabel(orderId).then((kittingLabeldata) => {
      console.log("getKittingLabel data", kittingLabeldata);
      const transformedKittingLabelDataArray = [
        {
          head: "Dhoot Transmission",
          Fg_Code: kittingLabeldata.fg_code,
          Order_no: kittingLabeldata.order_no.toString(),
          Quantity: kittingLabeldata.quantity.toString(),
          QR_DATA: kittingLabeldata.label, // Mapping label to QR_DATA
        },
      ];
      console.log(
        "transformedKittingLabelDataArray",
        transformedKittingLabelDataArray
      );
      setPrintData(transformedKittingLabelDataArray);
    });
  }, []);

  return (
    <div className="action_modal__container">
      <div>
        <img src={"/error_outline.svg"} alt="icon" />
      </div>
      <div style={{ marginBottom: "5vw" }}>
        <strong>Do you want to print kitting Label for this order?</strong>{" "}
      </div>

      <TSCPrinter kitting={true} payload={printData} />

      <div
        style={{
          gap: "2.5rem",
          display: "flex",
          flexDirection: "row",
        }}
      >
        <Button
          onClick={noStepFn}
          white={true}
          width="8rem"
          height="2.5rem"
          bordered={true}
          customStyle={{
            border: "1px solid #3E3E3E",
            backgroundColor: "#FFFFFF",
          }}
        >
          {t('no')}
        </Button>
      </div>
    </div>
  );
};

export default KittingQRPrintModal;
