import React,{useEffect, useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";
import crossIcon from '../../assets/svg/cross-icon.svg'

const AddAssign = ({ open, onClose, data, setData, onSubmit, loading ,tab,setSelectedQuestions,skills,users,testList}) => {
  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  

  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(SKILL, data);
      setErrors(_errors);
      if (_errors.size) return;
      await onSubmit()
      // console.log("data------------",data.admins)
      // setStep((prev) => prev + 1);
      return;
    }
  };


  const SKILL = [
    tab==="Outward"&&(
      {
        title: t('workOrderId'),
        placeholder:t('enterWorkOrderId'),
        type: SCHEMA_TYPES.INPUT,
        name: 'work_order_id',
      }
    ),
    {
      title: t('materialQr'),
      placeholder: t('scanQr'),
      type: SCHEMA_TYPES.INPUT,
      name: 'material_qr',
      // validator: VALIDATORS.REQUIRED,
    },
  
    {
      title: t('materialCode'),
      placeholder: t('enterMaterialCode'),
      type: SCHEMA_TYPES.INPUT,
      name: 'material_code',
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('grnNumber'),
      placeholder: t('enterGrnNumber'),
      type: SCHEMA_TYPES.INPUT,
      name: 'material_grn',
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('quantity'),
      placeholder: t('enterMaterialQuantity'),
      type: SCHEMA_TYPES.INPUT,
      name: 'material_quantity',
      // validator: VALIDATORS.REQUIRED,
    },
   
    {
      title: t('location'),
      placeholder: t('selectLocation'),
      type: SCHEMA_TYPES.SELECT,
      name: 'location',
      // validator: VALIDATORS.REQUIRED,
      keyValues:[{ value: 'SH11',key:'SH11'},{ value: 'SH12',key:'SH12'}],
      width:'512px'
    },
   
   ];

  return (
    <Modal
    open={open}
    onClose={onClose}
    width="min(700px,90vw)"
    hideCloseIcon={crossIcon}
    wrapperClasses="px-5"
    >
      <h3 className="opacity-80" style={{width:'fit-content'}}>{tab==='Outward'?t('addOutwardMaterial'):t('addInMaterial')}</h3>

      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={SKILL}
        />
      
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">
          {t('next')}
          </Typography>  
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddAssign;
