
import { Stack, Typography } from '@mui/material'
import Answer from './answer'
import { useTranslation } from 'react-i18next';


const Question = ({
  question,
  code,
  image,
  type,
  choices=[],
  selectedAnswer,
  handleAnswerSelection,
  ans,
  questionNo
}) => {

  return (
    <Stack>
      <Typography className="fw-500-14" sx={{mb:1}}>{question?.question_text}</Typography>
      <Stack>
        {choices.map((choice, index) => (
          <Answer
            choice={choice}
            index={index}
            key={index}
            onChange={(e) => handleAnswerSelection(index+1, question.id)}
            type={type}
            selectedAnswer={selectedAnswer}
            correct={ans[questionNo]?.ans-1===index}
          />
        ))}
      </Stack>
    </Stack>
  )
}

export default Question
