import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useDowntime from "../../store/downtimeMaster";
import Button from "../../components/button";
import Add from "./Add";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { Delete, Checklist } from "@mui/icons-material";
import JsonFormatter from "../../components/UI/JsonFormatter";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import useMachineMonitoring from '../../store/machineMonitoring'
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import Select from "../../components/input/Select";
import Input from "../../components/input";
import { styled } from '@mui/material/styles';
import Switch from '@mui/material/Switch';
import moment from "moment";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { t } from "i18next";

const DowntimePacket = () => {
  const navigate = useNavigate()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [tab, setTab] = useState(false)
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    "company_id": "",
    "plant_id": "",
    "division_id": "",
    "line_id": "",
    "date": new Date().toISOString().split('T')[0],
    "shift_id": ""
  });

  const [filter_date, setFilter_date] = useState(new Date().toISOString().split('T')[0])
  const {matrixList, all_downtime_list,
    unassi_downtime_list,
    reason_list,
    post_reason } = useDowntime();
    const {summary} = useMachineMonitoring()


  const [errors, setErrors] = useState(new Map());

  const [company, setCompany] = useState([{
    value: 'All',
    key: ''
  }
  ])
  const [plant, setPlant] = useState([{
    value: 'All',
    key: ''
  }])
  const [division, setDivision] = useState([{
    value: 'All',
    key: ''
  }])
  const [line, setLine] = useState([{
    value: 'All',
    key: ''
  }])
  const [assets, setAssets] = useState([])
  const [matrixData,setMatrixData] = useState({})

  useEffect(() => {
    fetchMatrix()
  }, []);

  useEffect(() => {
    allSumary();
  }, [ tab,data.company_id, data.plant_id, data.division_id, data.line_id, data.date, data.shift_id]);
  
  useEffect(() => {
    fetchCompany();
  }, [page, tab,data.company_id, data.plant_id, data.division_id, data.line_id, data.date, data.shift_id]);

  async function allSumary() {
    let [res] = await summary(page + 1, 1,data.company_id ||'', data.plant_id ||'', data.division_id ||'', data.line_id ||'', data.date ||'', data.shift_id ||'',data.state ||'');

    console.log("machine res-------",res)
    // if(res.length>0){
  
    const addUniqueItems = (currentItems, newItems) => {
      const existingKeys = new Set(currentItems.map(item => item.key));
      return newItems.filter(item => !existingKeys.has(item.key));
    };
  
    setCompany(prevUsers => [
      ...prevUsers,
      ...addUniqueItems(prevUsers, res?.companies.map(item => ({
        value: item.name,
        key: item.id,
      }))),
    ]);
  
    setPlant(prevUsers => [
      ...prevUsers,
      ...addUniqueItems(prevUsers, res?.plants.map(item => ({
        value: item.name,
        key: item.id,
      }))),
    ]);
  
    setDivision(prevUsers => [
      ...prevUsers,
      ...addUniqueItems(prevUsers, res?.divisions.map(item => ({
        value: item.name,
        key: item.id,
      }))),
    ]);
  
    setLine(prevUsers => [
      ...prevUsers,
      ...addUniqueItems(prevUsers, res?.lines.map(item => ({
        value: item.name,
        key: item.id,
      }))),
    ])
  
    setData(prev => ({ ...prev, company: company[0]?.key }));
  }


  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list] = tab === false ? 
    await unassi_downtime_list(page + 1, 10,data.company_id ||'', data.plant_id ||'', data.division_id ||'', data.line_id ||'', data.date ||'') :
    await all_downtime_list(page + 1, 10,data.company_id ||'', data.plant_id ||'', data.division_id ||'', data.line_id ||'', data.date ||'');
    setUsers(_list.results);
    setTotal(_list.count);
    setLoading(false);

  };
  const fetchMatrix = async () => {
    setLoading("fetch");
    const [_list] = await matrixList(data.date) 
    console.log("matrix downtime-----",_list)
    setMatrixData(_list)
    // setMatrix(_list);
    // setTotal(_list.count);
    setLoading(false);

  };

  const fetchAssets = async (rowData) => {
    setAssets([])
    setData({ ...data, id: rowData.id });
    setEdit(true);
    setModalOpen("add")
    // setLoading("fetch");
    const [_list] = await reason_list(rowData.id);
    console.log("reason_list linst", _list)
    if(_list?.reasons?.length>0){
      const newUsers = _list?.reasons.map(item => ({
        value: item.reason_name,
        key: item.id
      }));
      setAssets(prevUsers => [...prevUsers, ...newUsers]);
    }

  };

  const handleAdd = async () => {
    let packetId = data.id
    const req = {
      reason_id: data.reason,
    }
    setLoading("fetch");
    let res = await post_reason(packetId, req);
    setData({
      "company_id": "",
    "plant_id": "",
    "division_id": "",
    "line_id": "",
    "date": new Date().toISOString().split('T')[0],
    "shift_id": ""
    });
    setModalOpen(false);
    await fetchCompany();
    console.log("creat res----", res)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    // await remove(id);
    // await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    // await update(id,payload);
    await fetchCompany();
    setLoading(false);
  };


  const COLUMNS = [
    {
      id: 1,
      title: t('company'),
      render: (rowData) => <Typography sx={{ cursor: 'pointer', textTransform: 'capitalize' }} >{rowData.company_name}</Typography>,
    },
    {
      id: 2,
      title: t('plant'),
      render: (rowData) => <Typography >{rowData.plant_name}</Typography>
    },
    {
      id: 3,
      title: t("division"),
      render: (rowData) => <Typography>{rowData.division_name}</Typography>,
    },
    {
      id: 4,
      title: t("line"),
      render: (rowData) => <Typography >{rowData.line_name}</Typography>,
    },
    {
      id: 7,
      title: t("station"),
      render: (rowData) => <Typography >{rowData.asset_name}</Typography>,
    },
    // {
    //   id: 8,
    //   title: "Shift",
    //   render: (rowData) => <Typography >{rowData.applicable_to.map((item)=>item.name)}</Typography>,
    // },
    {
      id: 22,
      title: t('startTime'),
      render: (rowData) => <Typography >{rowData.start_time}</Typography>,
    },
    {
      id: 23,
      title: t('endTime'),
      render: (rowData) => <Typography >{rowData.end_time}</Typography>,
    },
    {
      id: 24,
      title: t('duration'),
      render: (rowData) => <Typography >{rowData.duration}</Typography>,
    },
    // {
    //   id: 25,
    //   title: "Reason",
    //   render: (rowData) => <Typography >{rowData.time_limit}</Typography>,
    // },
    tab === false ? ({
      id: 54,
      title: t("action"),
      render: (rowData) => (
        <Stack flexDirection={'row'}>
          <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => { fetchAssets(rowData) }}
          >
            <AddCircleIcon color="primary" fontSize="small" />
          </IconButton>

        </Stack>
      ),
    }) : (
      {
        id: 234,
        title: t("reason"),
        render: (rowData) => <Typography >{rowData.downtime_reason}</Typography>,
      }),
  ];

  const AntSwitch = styled(Switch)(({ theme }) => ({
    width: 28,
    height: 16,
    padding: 0,
    display: 'flex',
    '&:active': {
      '& .MuiSwitch-thumb': {
        width: 15,
      },
      '& .MuiSwitch-switchBase.Mui-checked': {
        transform: 'translateX(9px)',
      },
    },
    '& .MuiSwitch-switchBase': {
      padding: 2,
      '&.Mui-checked': {
        transform: 'translateX(12px)',
        color: '#fff',
        '& + .MuiSwitch-track': {
          opacity: 1,
          backgroundColor: '#FAA036',
          // borderColor: '#0000',
          border: 2,
          ...theme.applyStyles('dark', {
            backgroundColor: '#FAA036',
          }),
        },
      },
    },
    '& .MuiSwitch-thumb': {
      // boxShadow: '0 2px 4px 0 rgb(0 35 11 / 20%)',
      width: 12,
      height: 12,
      borderRadius: 6,
      // borderColor: '2px solid #0000',
      transition: theme.transitions.create(['width'], {
        duration: 200,
      }),
    },
    '& .MuiSwitch-track': {
      borderRadius: 16 / 2,
      opacity: 1,
      backgroundColor: 'rgba(0,0,0,.25)',
      boxSizing: 'border-box',
      ...theme.applyStyles('dark', {
        backgroundColor: 'rgba(255,255,255,.35)',
      }),
    },
  }));

  const Filters = [
    {
      // title: t('operator'),
      placeholder: t('company'),
      type: SCHEMA_TYPES.SELECT,
      name: 'company_id',
      // validator: VALIDATORS.REQUIRED,
      width: '150px',
      keyValues: company
    },
    {
      // title: t('test'),
      placeholder: t("plant"),
      type: SCHEMA_TYPES.SELECT,
      name: 'plant_id',
      // validator: VALIDATORS.REQUIRED,
      width: '150px',
      keyValues: plant,
    },
    {
      // title: t('test'),
      placeholder: t("division"),
      type: SCHEMA_TYPES.SELECT,
      name: 'division_id',
      validator: VALIDATORS.REQUIRED,
      width: '150px',
      keyValues: division,
    },
    {
      // title: t('test'),
      placeholder: t("line"),
      type: SCHEMA_TYPES.SELECT,
      name: 'line_id',
      validator: VALIDATORS.REQUIRED,
      width: '150px',
      keyValues: line,
    },
    {
      // title: t('test'),
      placeholder: t("date"),
      type: SCHEMA_TYPES.DATE,
      name: 'date',
      // validator: VALIDATORS.REQUIRED,
      width: '150px',
      // keyValues:testList,
    },
    // {
    //   // title: t('test'),
    //   placeholder: "Shift",
    //   type: SCHEMA_TYPES.SELECT,
    //   name: 'shift',
    //   // validator: VALIDATORS.REQUIRED,
    //   width:'150px',
    //   // keyValues:testList,
    // }
  ];

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log("name value", name, value)
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value == 0 ? '' : value }));
  };

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Typography className="fw-20-500-36">
          {t('downtime')}
          {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </Typography>
        {/* <Stack sx={{flexDirection:"row",gap:1}}>
        <Button color={'gray'} onClick={() => setModalOpen("add")} loading={loading}>
          <Typography className="fw-20-500-24">
          Add Downtime Reason
          </Typography> 
        </Button>
        </Stack> */}
        <Stack sx={{ flexDirection: 'row', gap: 1 }}>
          <Stack direction="row" spacing={1} sx={{ alignItems: 'center' }}>
            <Typography className="fw-16-400-36">{t('unassigned')}</Typography>
            <AntSwitch defaultChecked={tab} inputProps={{ 'aria-label': 'ant design' }} onChange={(e) => {setPage(0);setTab(e.target.checked)}} />
            <Typography className="fw-16-400-36">{t('all')}</Typography>
          </Stack>
        </Stack>
      </div>
      <Stack sx={{ mb: 1, flexDirection: 'row', gap: 1 }}>
        <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={Filters}
        />
      </Stack>
      <Stack sx={{mb:1,flexDirection:'row',gap:2}}>
        <Stack sx={{width:'20%',justifyContent:'center',alignItems:'center',borderRadius:2,backgroundColor:'#d8d8d8d4',py:2}}>
          <Typography>{t('eventsToday')}</Typography>
          <Typography sx={{fontWeight:'bold',mt:1}}>{matrixData.total}</Typography>
        </Stack>
        <Stack sx={{width:'20%',justifyContent:'center',alignItems:'center',borderRadius:2,backgroundColor:'#d8d8d8d4',py:2}}>
          <Typography>{t('totalDowntime')}</Typography>
          <Typography sx={{fontWeight:'bold',mt:1}}>{((matrixData.total_duration/60)/60).toFixed(2)} hrs</Typography>
        </Stack>
        <Stack sx={{width:'20%',justifyContent:'center',alignItems:'center',borderRadius:2,backgroundColor:'#d8d8d8d4',py:2}}>
          <Typography>{t('activeDowntime')}</Typography>
          <Typography sx={{fontWeight:'bold',mt:1}}>{matrixData.active}</Typography>
        </Stack>
        <Stack sx={{width:'20%',justifyContent:'center',alignItems:'center',borderRadius:2,backgroundColor:'#d8d8d8d4',py:2}}>
          <Typography>{t('unassignedDowntime')}</Typography>
          <Typography sx={{fontWeight:'bold',mt:1}}>{matrixData.unassigned}</Typography>
        </Stack>
       
      </Stack>
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <Add
        open={modalOpen === "add"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
        assetsList={assets}
      />
    </div>
  );
};

export default DowntimePacket;
