import React,{useState} from "react";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack ,Typography} from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import RadioButtonGroup from "../../components/input/radio";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const AddDowntimeReason = ({ open, onClose, data, setData, onSubmit, loading,edit,assetsList,rsn}) => {

  const USER_SCHEMA = [
    {
      // title: "Asset",
      placeholder: t('selectDowntimeReason'),
      type: SCHEMA_TYPES.SELECT,
      name: "reason",
      validator: VALIDATORS.REQUIRED,
      keyValues:rsn,
      // width:'520px'
    },
   
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

    const handleSubmit=async ()=>{
          const _errors = Validate(USER_SCHEMA, data);
          setErrors(_errors);
          if (_errors.size) return;
          await onSubmit()
          return;
    }

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(600px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{t("downtimeReason")}</h3>
      <Typography color={'primary'}>{data.rowData?.barcode || t('packetNameNotFound')}</Typography>
      <Stack sx={{flexDirection:'row',justifyContent:'space-between'}}>
      <Typography>{t('startTime')}: {data.rowData?.start_time}</Typography>
      <Typography>{t('endTime')}: {data.rowData?.end_time}</Typography>
      </Stack>
      <Typography>{t("duration")}: {data.rowData?.duration}</Typography>
      <Stack sx={{py:2}}>
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
        </Stack>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
         <Typography className="fw-20-500-24">{t('submit')}</Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddDowntimeReason;
