import React,{useEffect, useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import useMatrix from "../../store/skillmatrix";
import { toast } from "react-toastify";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const AddUserReport = ({ open, onClose, data, setData, onSubmit,onSubmit2, loading ,setSelectedQuestions,skills,users,testList}) => {

  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
  const [completed,setCompleted] = useState(false)
  const {getPerformanceSkill,getPerformanceId} = useMatrix()

  const fetchUserProfile = async (id)=>{
    let tempId = id
    let payload ={
      "operator":id
    }
   if(id!==undefined){ 
    let res = await getPerformanceSkill(payload)
    let resPer = await getPerformanceId(payload)
    let lastItem = resPer.at(-1)
    console.log("resPer-----lastItem---------",lastItem)
    if(lastItem?.id){
      setCompleted(lastItem.completed)
      if(lastItem.completed===true){
        toast.warn("Performances are already filled. Assign Supervisor for next level.")
        await onClose()
        setData({...data,skill_level:'',skill:'',operator:'',skill_id:''})
      }
      setData({...data,skill_level:lastItem.skill_level,skill:lastItem.skill.skill_name,operator:tempId,skill_id:lastItem.id})    
    }else{
      setData({...data,skill_level:'',skill:'',operator:'',skill_id:''})
      toast.warn('record not found')
    }
   }else{
    setData({...data,skill_level:'',skill:'',operator:'',skill_id:''})
   }
  }
  // const fetchOpList = async (val) => {
  //   const res = await listByAadharFortestPortal(val||'')
  //   return res
  //   console.log("operator----->>>",res)
  //   // const newOptr = _list.results.filter(item => item.is_operator);
  //   // const newUsers = newOptr.map(item => ({
  //   //   value: item.username,
  //   //   key: item.id
  //   // }));
  //   setUsers(res);
  // };
  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
    if(name ==='operator'){
      console.log("operator id ",value)
      fetchUserProfile(value)
    }
  };
  
  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(SKILL, data);
      setErrors(_errors);
      if (_errors.size) return;
      // await onSubmit()
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 1) {
      const _errors = Validate(SKILL, data);
      setErrors(_errors);
      if (_errors.size) return;
      await onSubmit2()
      setCompleted(false)
      setStep(0)
      return;
    }
  };


  const SKILL = [
    step === 0 &&( {
      title: t('operator'),
      placeholder: t('selectOperator'),
      type: SCHEMA_TYPES.SELECT,
      name: 'operator',
      validator: VALIDATORS.REQUIRED,
      keyValues:users,
    }),
    step === 0 &&( {
      title: t('skill'),
      placeholder: t('selectSkill'),
      type: SCHEMA_TYPES.INPUT,
      name: 'skill',
      disabled:true
      // keyValues:skills,
      // inputType:'number',
      // validator: VALIDATORS.REQUIRED,
    }),
    step === 0 &&({
      title: t('skillLevel'),
      placeholder: t('selectSkillLevel'),
      type: SCHEMA_TYPES.INPUT,
      name: 'skill_level',
      disabled:true
      // validator: VALIDATORS.REQUIRED,
      // keyValues:[{value:'Level 1',key:1},{value:"Level 2",key:2},{value:"Level 3",key:3}]
    }),
    step === 1 &&( {
      title: t('date'),
      type: SCHEMA_TYPES.DATE,
      name: 'plan_date',
      validator: VALIDATORS.REQUIRED,
      // keyValues:users,
    }),
    step === 1 &&( {
      title: t('plan'),
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      name: 'plan',
      inputType:'number',
      validator: VALIDATORS.REQUIRED,
    }),
    step === 1 &&( {
      title: t("actual"),
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      inputType:'number',
      name: 'actual',
      validator: VALIDATORS.REQUIRED,
    }),
    step === 1 &&( {
      title: t('rejected'),
      placeholder: t('rejectedCount'),
      type: SCHEMA_TYPES.INPUT,
      inputType:'number',
      name: 'rejected',
      validator: VALIDATORS.REQUIRED,
    }),
   ];

  return (
    <Modal
    open={open}
    onClose={onClose}
    width="min(700px,90vw)"
    hideCloseIcon={crossIcon}
    wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{t('addPerformance')}</h3>
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={SKILL}
        />     
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>

       {step===0&&( <Button color={'light_green'} onClick={handleNext} loading={loading} disabled={completed}>
          <Typography className="fw-20-500-24">
          {t('next')}
          </Typography>
        </Button>
        )}
       {step===1&&( <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">
          {t('submit')}
          </Typography>
        </Button>
        )}
      </Stack>
    </Modal>
  );
};

export default AddUserReport;
