import React from "react";
import { resolveResponse } from "../../api/utils";
import REQUEST, { CONTENT_TYPES } from "../../api";
import { ENDPOINTS } from "../../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "../..";

const useOperatorGroup = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };
  const allOperator = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.OPERATORS}/`,
      {},
      headers,
      // resolveResponse
    );

    if (res?.data) {
      // console.log("res  ",res.data)
      return [res?.data?.results];
    }
    return [[], 0];
  };

  const groupOp = async (id) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.OPERATOR}-${ENDPOINTS.GROUP}/${id}/`,
      {},
      headers,
      // (res) => resolveResponse(res, null, "Group Updated successfully!")
    );
    return res?.data;
  };

  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.OPERATOR}-${ENDPOINTS.GROUPS}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.OPERATOR}-${ENDPOINTS.GROUPS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Group created successfully!")
    );
    return res?.data;
  };

  const update = async (payload) => {
    const newPayload ={
      "name":payload.name,
      "metadata":payload.metadata
    }
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.OPERATOR}-${ENDPOINTS.GROUP}/${payload.id}/`,
      newPayload,
      headers,
      (res) => resolveResponse(res, null, "Group Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.OPERATOR}-${ENDPOINTS.GROUP}/${id}/`,
      headers,
      (res) => resolveResponse(res, null, "Group deleted successfully!")
    );
    return res?.data;
  };

  const updateAssets = async (payload,id) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.OPERATOR}-${ENDPOINTS.GROUP}/${id}/`,
      payload,
      headers,
      // (res) => resolveResponse(res, null, "Group Updated successfully!")
    );
    return res?.data;
  };

  return {
    list,
    create,
    update,
    remove,
    allOperator,
    groupOp,
    updateAssets
  };
};

export default useOperatorGroup;
