import { useState, useEffect } from "react";
import { toast } from "react-toastify";
import Button from "../../components/button";
import moment from "moment/moment";
const TSCPrinter = (
    {kitting=false,
    // payload=[
    // {Part_No:123,GRN_No:1222,GRN_Date:"27-12-2024",QUANTITY:22,QR_DATA:9876}, testing payload
    // {Part_No:1233,GRN_No:12223,GRN_Date:"28-12-2024",QUANTITY:232,QR_DATA:98736}]
payload}
) => {
    const [isConnected, setIsConnected] = useState(false);
    const [device, setDevice] = useState(null);

    useEffect(() => {
        autoConnectToPrinter();
        console.log("payload", payload);
        
    }, []);

    const autoConnectToPrinter = async () => {
        try {
            // Check for previously authorized devices
            const devices = await navigator.usb.getDevices();
            const filteredDevices = devices.filter(device => device.vendorId === 0x1203);

            if (filteredDevices.length > 0) {
                const usbDevice = filteredDevices[0]; // Use the first matching device
                console.log("Auto-connecting to device:", usbDevice);

                await usbDevice.open();
                if (usbDevice.configuration === null) {
                    await usbDevice.selectConfiguration(1);
                }
                await usbDevice.claimInterface(0);

                setDevice(usbDevice);
                setIsConnected(true);
                console.log("Auto-connected to printer:", usbDevice);
            } else {
                toast.error("No previously printer devices found.")
                console.log("No previously connected devices found.");
            }
        } catch (error) {
            console.error("Error during auto-connect:", error);
            toast.error("Error during auto-connect:", error)
        }
    };

    const connectToPrinter = async () => {
        try {
            const usbDevice = await navigator.usb.requestDevice({
                filters: [{ vendorId: 0x1203 }] // Replace with your printer's vendorId
            });
    
            await usbDevice.open();
    
            if (usbDevice.configuration === null) {
                await usbDevice.selectConfiguration(1); // Select the first configuration
            }
    
            // Debug interfaces to find the right one
            console.log("Available interfaces:", usbDevice.configuration.interfaces);
    
            // Attempt to claim interface 0 (update index if needed)
            await usbDevice.claimInterface(0);
            console.log("claimInterface",)
    
            setDevice(usbDevice);
            setIsConnected(true);
            toast.success("Printer connected")
            console.log("Printer connected manually:", usbDevice);
        } catch (error) {
            console.error("Failed to connect to printer manually:", error);
            toast.error("Failed to connect to printer manually:", error);
        }
    };
    

    const sendCommand = async (command) => {
        if (device) {
            try {
                const encoder = new TextEncoder();
                const data = encoder.encode(command);
                await device.transferOut(1, data); // Endpoint number might vary
                console.log("Command sent:", command);
            } catch (error) {
                toast.error("Error sending command to printer:", error)
                console.error("Error sending command to printer:", error);
            }
        } else {
            console.log("No printer connected.");
        }
    };

    const printSample = () => {
        if(kitting===true){
            const kitting = `
            SPEED 2
            DENSITY 12
            SIZE 3:2
            GAP 0,0
            DIRECTION 1
            CLS
            FONT 0,0,1,0,0,""
            TEXT 110,30,"4",0,1,1,"Dhoot Transmission"
            TEXT 70,100,"2",0,1,1,"FG CODE : ${payload[0].Fg_Code}"
            TEXT 70,150,"2",0,1,1,"ORDER No : ${payload[0].Order_no}"
            TEXT 70,200,"2",0,1,1,"QUANTITY: ${payload[0].Quantity}"
            QRCODE 420,200,M,4,A,0,M2,"${payload[0].QR_DATA}"
            PRINT 1,1
            `;
            sendCommand(kitting);
            console.log("kitting Payload".kitting)
    
        }else{
            payload.map((item)=>{
                const packetBreaking = `
                SPEED 2
                DENSITY 12
                SIZE 3:2
                GAP 0,0
                DIRECTION 1
                CLS
                FONT 0,0,1,0,0,""
                TEXT 110,30,"4",0,1,1,"Dhoot Transmission"
                TEXT 70,100,"2",0,1,1,"Part No : ${item.material_code}"
                TEXT 70,150,"2",0,1,1,"GRN No : ${item.grn_number}"
                TEXT 70,200,"2",0,1,1,"GRN Date : ${moment(item.grn_date).format('ll')}"
                TEXT 70,250,"2",0,1,1,"QUANTITY : ${item.quantity}"
                QRCODE 420,200,M,4,A,0,M2,"${item.QR_DATA}"
                PRINT 1,1
                `;
                sendCommand(packetBreaking);
                console.log("packetBreaking Payload".packetBreaking)
            })  
        }   
    };

    return (
        <div>
            {!isConnected ? (
                <Button color={'light_green'} onClick={connectToPrinter}>Connect to Printer</Button>
            ) : (
                <div>
                    <p>Printer connected: {device?.productName || "TSC Printer"}</p>
                    <Button color={'light_green'} onClick={printSample}>Print Label</Button>
                </div>
            )}
        </div>
    );
};

export default TSCPrinter;
