import { Stack, Typography } from '@mui/material'
import {addLeadingZero,formatTime} from '../../helpers/utils'
import AccessAlarmIcon from '@mui/icons-material/AccessAlarm';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';


const QuizHeader = ({ activeQuestion, totalQuestions ,setModalOpen,testData,finishTest}) => {
  const navigate = useNavigate()
    const [timer , setTimer] = useState(testData?.remaining_time_in_seconds)
    // const [timer , setTimer] = useState(10)
    
    useEffect(() => {
      if(timer>0){
        const countTimer = setTimeout(() => {
          setTimer((prevTimer) => prevTimer - 1)
          localStorage.setItem('time',timer)
        }, 1000)
        return () => clearTimeout(countTimer)
      }    
      if(timer<1){
        finishTest()
        localStorage.setItem('draweWidth',190)
          // window.location.reload();    
      }         
      }, [timer])

  return (
    <Stack sx={{flexDirection:'row',justifyContent:'space-between'}}>
      <Stack sx={{flexDirection:'row',gap:0.5,alignItems:'baseline'}}>
        <Typography sx={{fontWeight:500,fontSize:40}}>{addLeadingZero(activeQuestion)}</Typography>
        <Typography sx={{fontWeight:500,fontSize:20}}>/{addLeadingZero(totalQuestions)}</Typography>
      </Stack>
      <Stack sx={{flexDirection:'row',gap:1,alignItems:'center',mr:2}}>
        <AccessAlarmIcon fontSize='large'/>
        <Typography sx={{fontWeight:500,fontSize:18}}>{formatTime(timer)}</Typography>
      </Stack>
    </Stack>
  )
}

export default QuizHeader
