import { resolveResponse } from "../../api/utils";
import REQUEST, { CONTENT_TYPES } from "../../api";
import { ENDPOINTS } from "../../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "../..";
import { useState, useCallback } from "react";

const useMaterialVerify = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const apiRequest = new REQUEST();

  const verifyMaterial = useCallback(
    async (id, material_code, grn_number, quantity) => {
      const route = `${ENDPOINTS.ORDERS}/material-verifier/${id}/`;
      const body = {
        material_code: material_code,
        grn_number: grn_number,
        quantity: quantity,
      };
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in verifying material");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  return {
    loading,
    error,
    verifyMaterial,
  };
};

export { useMaterialVerify };
