import { resolveResponse } from "../../api/utils";
import REQUEST, { CONTENT_TYPES } from "../../api";
import { ENDPOINTS } from "../../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "../..";
import { useState, useCallback } from "react";

const useOrder = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const apiRequest = new REQUEST();

  const fetchOrders = useCallback(
    async (
      pageNumber = 1,
      isSortedByCode,
      isSortedByOrderType,
      isSortedByStatus,
      isSortedByOrderId,
      isSortedByName,
      isSortedByQuantity,
      sortingOrder,
      orderIdFilterValue,
      fgCodeFilterValue,
      orderTypeFilterValue,
      orderStatusFilterValue
    ) => {
      let route = `${ENDPOINTS.ORDERS}/all/?page=${pageNumber}`;

      // Check if any sorting is applied and add "&sort_by=" to the route if so
      // only if sorting is null, then sorting is not applied
      if (
        isSortedByCode ||
        isSortedByOrderType ||
        isSortedByStatus ||
        isSortedByOrderId ||
        isSortedByName ||
        isSortedByQuantity
      ) {
        // sorting is true (ascending order case)
        route += "&sort_by=";
      } else if (
        isSortedByCode === false ||
        isSortedByOrderType === false ||
        isSortedByStatus === false ||
        isSortedByOrderId === false ||
        isSortedByName === false ||
        isSortedByQuantity === false
      ) {
        // sorting is true (decending order case)
        route += "&sort_by=";
      }
      //Iterate Through sortingOrder Array: Loop through the sortingOrder array to get the column IDs in the desired order.
      //Handle Each Sorting State: Use a switch statement or conditional logic to handle each sorting state based on the column ID.
      sortingOrder.forEach((columnId) => {
        switch (columnId) {
          case "fg_code":
            switch (isSortedByCode) {
              case false:
                console.log("Code is false");
                route += route.endsWith("&sort_by=") ? "-fg_code" : ",-fg_code";
                break;
              case true:
                console.log("Code is true");
                route += route.endsWith("&sort_by=") ? "fg_code" : ",fg_code";
                break;
              default:
                console.log("Code is an unexpected value");
            }
            break;
          case "order_type":
            switch (isSortedByOrderType) {
              case false:
                console.log("Order type is false");
                route += route.endsWith("&sort_by=")
                  ? "-order_type"
                  : ",-order_type";
                break;
              case true:
                console.log("Order type is true");
                route += route.endsWith("&sort_by=")
                  ? "order_type"
                  : ",order_type";
                break;
              default:
                console.log("Order type is an unexpected value");
            }
            break;
          case "status":
            switch (isSortedByStatus) {
              case false:
                console.log("Status is false");
                route += route.endsWith("&sort_by=") ? "-status" : ",-status";
                break;
              case true:
                console.log("Status is true");
                route += route.endsWith("&sort_by=") ? "status" : ",status";
                break;
              default:
                console.log("Status is an unexpected value");
            }
            break;
          case "order_id":
            switch (isSortedByOrderId) {
              case false:
                console.log("Order ID is false");
                route += route.endsWith("&sort_by=")
                  ? "-order_id"
                  : ",-order_id";
                break;
              case true:
                console.log("Order ID is true");
                route += route.endsWith("&sort_by=") ? "order_id" : ",order_id";
                break;
              default:
                console.log("Order ID is an unexpected value");
            }
            break;
          case "name":
            switch (isSortedByName) {
              case false:
                console.log("Name is false");
                route += route.endsWith("&sort_by=") ? "-name" : ",-name";
                break;
              case true:
                console.log("Name is true");
                route += route.endsWith("&sort_by=") ? "name" : ",name";
                break;
              default:
                console.log("Name is an unexpected value");
            }
            break;
          case "quantity":
            switch (isSortedByQuantity) {
              case false:
                console.log("Quantity is false");
                route += route.endsWith("&sort_by=")
                  ? "-quantity"
                  : ",-quantity";
                break;
              case true:
                console.log("Quantity is true");
                route += route.endsWith("&sort_by=") ? "quantity" : ",quantity";
                break;
              default:
                console.log("Quantity is an unexpected value");
            }
            break;
          default:
            console.log("Unknown column ID:", columnId);
        }
      });

      // add filters
      if (orderIdFilterValue) {
        route += "&order_id=";
        route += orderIdFilterValue;
      }
      if (fgCodeFilterValue) {
        route += "&fg_code=";
        route += fgCodeFilterValue;
      }
      if (orderTypeFilterValue) {
        route += "&type=";
        route += orderTypeFilterValue;
      }
      if (orderStatusFilterValue) {
        route += "&status=";
        route += orderStatusFilterValue;
      }

      setLoading(true);
      setError(null);
      try {
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        setError(error.message || "Error in fetchOrders:");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const fetchOrdersMatrixView = useCallback(async () => {
    const route = `${ENDPOINTS.ORDERS}/order-matrix-view/`;
    setLoading(true);
    setError(null);
    try {
      const response = await apiRequest.get(route, {}, headers);
      return response.data;
    } catch (error) {
      setError(error.message || "Error in fetchOrders:");
    } finally {
      setLoading(false);
    }
  }, [apiRequest, headers]);

  const fetchOrdersById = useCallback(
    async (id) => {
      const route = `${ENDPOINTS.ORDERS}/order-details/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in fetching orders by id");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const getOrderHoldQuantity = useCallback(
    async (id) => {
      const route = `${ENDPOINTS.ORDERS}/material-on-hold/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        console.error(
          error.message || "Error in getting order on hold quantity"
        );
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const submitBOM = useCallback(
    async (id, body = {}) => {
      const route = `${ENDPOINTS.ORDERS}/bom-submit/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in submitting BOM");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const postFailureReason = useCallback(
    async (failureReasonText = "", isResolutionDateRequired=true) => {
      const route = `${ENDPOINTS.ORDERS}/material-failure-reasons/`;
      const body = {
        reason_name: failureReasonText,
        resolution_date_required: isResolutionDateRequired,
      };
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in getting Failure Reasons");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  return {
    loading,
    error,
    fetchOrders,
    fetchOrdersMatrixView,
    fetchOrdersById,
    getOrderHoldQuantity,
    submitBOM,
    postFailureReason,
  };
};

export { useOrder };
