import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import PieChart from '../pie'
import { toast } from "react-toastify";
import Button from "../../components/button";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import useUser from "../../store/users";
import useMatrix from "../../store/skillmatrix";
import useSkill from "../../store/skill";
import AddUserReport from './AddUserReport'
import InputDate from "../../components/input/indexDate";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const AddReport = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const companyId = location.state
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState("testDetail");
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    operator:'',
    plan_date:new Date().toISOString().split('T')[0]
  });
  const { putPerformanceRecord,performanceList, createTest, update, remove,performanceRecord,getPerformanceRecord, postPerformance } = useMatrix();
  const { activeOperators ,listByAadharFortestPortal} = useUser();
  const { list: skill_list } = useSkill()
  const [errors, setErrors] = useState(new Map());
  const [skills, setSkills] = useState([])
  const [testList, setTestList] = useState([])
  const [performance_list, setPerformance_list] = useState([])
  const [opList, setOpList] = useState([])
  const [skill_id,setSkill_id] = useState('')

  const [filter_date,setFilter_date] = useState(new Date().toISOString().split('T')[0])

  const COLUMNSS = [
    {
      id: 1,
      title: t('operatorAadhar'),
      render: (rowData) => rowData.aadhar_number,
    },
    {
      id: 13,
      title: t('skill'),
      render: (rowData) => rowData.skill,
    },
    {
      id: 12,
      title: t('skillLevel'),
      render: (rowData) =>rowData.skill_level,
    },
    {
      id: 2,
      title: t("plan"),
      render: (rowData) =>rowData.plan
       
    },
    {
      id: 3,
      title: t('actual'),
      render: (rowData) => rowData.actual
    }
  ]

  useEffect(() => {
    fetchSkill()
    fetchTestList()
    //  fetchUsers()
  }, [])

  useEffect(() => {
    fetchPerformance()
  }, [filter_date])

  const fetchPerformance=async ()=>{
    const res = await performanceList(filter_date)
    console.log("res performance list",res.list)
    setPerformance_list(res.list)
  }

  // const fetchTestList = async (val) => {
  //   const res = await listByAadharFortestPortal(val||'')
  //   // return res
  //   console.log("operator----->>>",res)
  //   // const newOptr = _list.results.filter(item => item.is_operator);
  //   // const newUsers = newOptr.map(item => ({
  //   //   value: item.username,
  //   //   key: item.id
  //   // }));
  //   setUsers(res);
  // };
  const fetchTestList = async () => {
    const res = await activeOperators();
    console.log("operator----->>>",res)
    // const newOptr = _list.results.filter(item => item.is_operator);
    // const newUsers = newOptr.map(item => ({
    //   value: item.username,
    //   key: item.id
    // }));
    setUsers(res);
  };

  const fetchSkill = async () => {
    const [list] = await skill_list(page + 1);
    const newUsers = list.results.map(item => ({
      value: item.skill_name,
      key: item.id
    }));
    setSkills(prevUsers => [...prevUsers,...newUsers]);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const uploadPerformance=async ()=>{
   var formattedDate = data.plan_date.replace(/-/g, '');
    const payload ={
      "new_entry":{[formattedDate]:{"plan":data.plan, "actual":data.actual,"rejected":data.rejected}}
    }
    await putPerformanceRecord(data.skill_id,payload)
    setModalOpen(false)
    setData({...data,   
      rejected:'',
      actual:'',
      plan:'',
      plan_date:'',
      skill_level:'',
      skill:'',
      operator:'',
      plan_date:new Date().toISOString().split('T')[0]
    })
    fetchPerformance()
  }

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Stack sx={{ flexDirection: 'row', alignItems: 'center' }}>
          <Typography className="fw-20-500-36">
            {t('performance')}
          </Typography>
        </Stack>
        <Stack sx={{ flexDirection: 'row',gap:1,mb:1}}>
          <Stack>
          <InputDate
           InputWidth={'150px'} value={filter_date} onChange={(e)=>{
            console.log("date------",e.target.value)
            setFilter_date(e.target.value)}}/>
          </Stack>
          <Button width={'fit-content'} color={'gray'} onClick={() => setModalOpen("addReport")}>
           <Typography className="fw-20-500-24">
           {t('addPerformance')}
           </Typography>
            </Button>
        </Stack>
      </div>
      
      <Table
        columns={COLUMNSS}
        rows={performance_list}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        serialize
        paginator
        total={total}
        tableBodyColor={COLORS.WHITE}
      />

      <AddUserReport
        open={modalOpen === "addReport"}
        onClose={() => { setModalOpen(false) }}
        // onSubmit={handleAdd}
        users={users}
        skills={skills}
        testList={testList}
        data={data}
        setData={setData}
        onSubmit2={uploadPerformance}
      />
    </div>
  );
};

export default AddReport;
