import React from 'react'
import DetailsCard from '../DetailsCard/DetailsCard'
import './ProductDetails.css'
import { useState } from 'react';
// import Table from '@mui/material/Table';
// import TableBody from '@mui/material/TableBody';
// import TableCell from '@mui/material/TableCell';
// import TableContainer from '@mui/material/TableContainer';
// import TableHead from '@mui/material/TableHead';
// import TableRow from '@mui/material/TableRow';
import { Link } from 'react-router-dom';
import PopupCard from '../ReportIssue/ReportIssue'
import Table from '../../../components/table';
import { COLORS } from '../../../helpers/constants';
import { Typography } from '@mui/material';

const ProductDetails = () => {
  const names = [
    { label: 'Number of Parts', value: '12', variant: 'green' },
    { label: 'Machining', value: '2', variant: 'pink' },
    { label: 'Operator', value: '2', variant: 'blue' },
    { label: 'Rework', value: '2', variant: 'purple' },
  ];

  const parts = [
    { id: 1, name: 'LED 1', code: 'QTYPW112', qty: 1, vendor: 'Sameera Enterprises' },
    { id: 2, name: 'Wire 6', code: 'QTI99012', qty: 1, vendor: 'Mayfair Enterprises' },
    { id: 3, name: 'Wire12', code: 'QTI9911', qty: 1, vendor: 'Axar Industries' },
    { id: 4, name: 'Wire11', code: 'QTI99726', qty: 1, vendor: 'Axar Industries' },
    { id: 5, name: 'Wire9', code: 'QTI90011', qty: 1, vendor: 'Axar Industries' },
    { id: 6, name: 'Wire3', code: 'QTI91423', qty: 1, vendor: 'Axar Industries' },
  
  ]

  const [activeIndex, setActiveIndex] = useState(0);
  const [total , setTotal] = useState(10)
  const [page , setPage] = useState(1)

  const COLUMNS = [
    {
      id: 1,
      title: 'Part Name',
      render: (rowData) => rowData.name,
    },
    {
      id: 2,
      title: 'Part Code',
      render: (rowData) =>  rowData.code,
    },
    {
      id: 3,
      title: "Oty",
      render: (rowData) => rowData.qty,
    },
    {
      id: 3,
      title: "Vendor",
      render: (rowData) => rowData.vendor,
    },
    {
      id: 112,
      title: 'Issue',
      render: (rowData) =><Typography color={'#1535DB'}>Report Issue</Typography>
    }
  ];

  const Machine = [
    {
      id: 1,
      title: 'Machine Name',
      render: (rowData) => rowData.name,
    },
    {
      id: 2,
      title: 'Machine Code',
      render: (rowData) => rowData.code,
    },
    {
      id: 3,
      title: "Finish Time",
      render: (rowData) => rowData.qty,
    },
    {
      id: 3,
      title: "Process",
      render: (rowData) => 'Crimping',
    },
    {
      id: 112,
      title: 'Issue',
      render: (rowData) => <Typography color={'#1535DB'}>Report Issue</Typography>
    }
  ];

  const Operator = [
    {
      id: 1,
      title: 'Operator Name',
      render: (rowData) => rowData.name,
    },
    {
      id: 2,
      title: 'Machine Code',
      render: (rowData) => rowData.code,
    },
    {
      id: 3,
      title: "Finish Time",
      render: (rowData) => '-',
    },
    {
      id: 3,
      title: "Process",
      render: (rowData) => 'Wirecutting',
    },
    {
      id: 112,
      title: 'Issue',
      render: (rowData) => <Typography color={'#1535DB'}>Report Issue</Typography>
    }
  ];

  return (
    <div className="mx-3 my-2">
       <Typography className="fw-20-500-36">
        Details
      </Typography>
      <DetailsCard />
      
      <div className='details-cards-container'>
        {names.map((metric, index) => (
          <CardComponent
            key={metric.label}
            label={metric.label}
            value={metric.value}
            variant={metric.variant}
            isActive={activeIndex === index}
            onClick={() => setActiveIndex(index)}
          />
        ))}

      </div>
      <div className="table mt-2" >
      <Table
        columns={activeIndex == 0?COLUMNS:activeIndex == 1?Machine:activeIndex == 2?Operator:Machine}
        rows={parts}
        rowsPerPage={10}
        // loading={loading === "fetch"}
        titleCellClasses="fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
        headColor={activeIndex == 0?COLORS.LG_GREEN:activeIndex == 1? COLORS.LG_PINK:activeIndex == 2?COLORS.LG_BG_BLUE:COLORS.LG_PURPLE}
        tableBodyColor={COLORS.LG_GRAY}
      />
        {/* {activeIndex == 0 && <LogTable />}
        {activeIndex == 1 && <MachiningTable  />}
        {activeIndex == 2 && <OperatorTable '#C4D7FC'/>} */}
        {/* {activeIndex == 3 && <LogTable />} */}
      </div>
    </div>

  )
}

export default ProductDetails

function CardComponent({
  label,
  value,
  variant,
  isActive,
  onClick
}) {
  return (
    <div
      className={`metric-card ${variant} ${isActive ? 'active' : ''}`}
      onClick={onClick}
    >
      <div style={{ display: 'flex', justifyContent: 'space-between', width: '100%' }}>
        <p className="metric-label">{label}</p>
        <p className="metric-value">{value}</p>
      </div>
    </div>
  );
};

