import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import useTenants from "../../store/tenants";
import Button from "../../components/button";
import AddTenant from "./AddTenant";
import { toast } from "react-toastify";
import { IconButton, Stack,Typography } from "@mui/material";
import { Delete } from "@mui/icons-material";
import EditIcon from '@mui/icons-material/Edit';
import { current } from "@reduxjs/toolkit";
import { useNavigate } from "react-router-dom";
import { t } from "i18next";

const TenantsMainPage = () => {
  const navigate = useNavigate()
  const [tenants, setTenants] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    // id: "",
    secret_key: "",
  });
  const [edit ,setEdit] = useState(false)
  const { list, create, remove,update } = useTenants();

  useEffect(() => {
    fetchTenants();
  }, [page]);

  const fetchTenants = async () => {
    setLoading("fetch");
    const [_list, _count] = await list(page + 1);
    setTenants(_list);
    setTotal(_count);
    setLoading(false);
  };

  const handleSubmit = async () => {
    let message;
    if (!data?.name) {
      message = "Please enter the tenant name!";
    }
    // if (!data?.id) {
    //   message = "Please enter the tenant id!";
    // }
    if (!data?.secret_key) {
      message = "Please enter the secret key!";
    }
    if (message) {
      toast.error(message);
      return;
    }
    setLoading("add");
    let payload ={
      name:data.name,
      secret_key:data.secret_key
    }
    const res = edit === false?await create(data): await update(data.id,payload)
    if (res?.id) {
      setData({});
      setModalOpen(false);
      fetchTenants();
    }
    setLoading(false);
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchTenants();
    setLoading(false);
  };

  const COLUMNS = [
    // {
    //   id: 2,
    //   title: "ID",
    //   render: (rowData) => rowData.id,
    // },
    {
      id: 3,
      title: t('tenantName'),
      render: (rowData) => <Typography color='primary' sx={{cursor:'pointer'}} onClick={()=>navigate("/tenant-Users",{state:rowData})}>{rowData.name}</Typography>,
    },
    {
      id: 4,
      title: t('secretCode'),
      render: (rowData) => rowData.secret_key,
    },
    {
      id: 5,
      title: t("action"),
      render: (rowData) => (
        <Stack flexDirection={'row'}>
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
          <Delete color="error" fontSize="small" />
        </IconButton>
        <IconButton
        sx={{ height: 25, width: 25 }}
        onClick={()=>{setModalOpen("edit");setData(rowData);setEdit(true)}}
      >
      <EditIcon color="primary" fontSize="small" />
      </IconButton>
      </Stack>
      ),
    },
  ];

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
         <Typography className="fw-20-500-36">
          {t('tenants')}
        </Typography>
        <Button color={'gray'} onClick={() => {setModalOpen(true)}}>
          <Typography className="fw-20-500-24">{t('createTenant')}</Typography>
          </Button>
      </div>
      <Table
        columns={COLUMNS}
        rows={tenants}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddTenant
        open={modalOpen}
        onClose={() => {setModalOpen(false);setEdit(false)}}
        data={data}
        setData={(e) =>
          setData((prev) => ({ ...prev, [e.target.name]: e.target.value }))
        }
        onSubmit={handleSubmit}
        loading={loading === "add"}
        edit={edit}
      />
    </div>
  );
};

export default TenantsMainPage;
