import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useSkill = () => {

  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const alllist = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/skills/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `training/skills/?page=${page}&page_size=${10}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const opratorList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.OPERATORS}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data?.results.length>0) {
      console.log("res  ",res.data)
      return [res?.data.results,res.data.count];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `training/skills/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Skill created successfully!")
    );
    return res?.data;
  };

  const updateSkill = async (payload = {},id) => {
    const client = new REQUEST();
    const res = await client.put(
      `training/skill/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Skill created successfully!")
    );
    return res?.data;
  };

  const updateProfile = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.USER}-${ENDPOINTS.PROFILE}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User Updated successfully!")
    );
    return res?.data;
  };

  const createOperator = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.OPERATORS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User created successfully!")
    );
    return res?.data;
  };

  const addSupervisor = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `training/skill/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Supervisor Updated successfully!")
    );
    return res?.data;
  };

  const update = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.USER}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User Updated successfully!")
    );
    return res?.data;
  };


  const updateOperator = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.OPERATOR}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Operator Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `training/skills/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "Skill deleted successfully!")
    );
    return res?.data;
  };
  const removeOperator = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.OPERATOR}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "Operator deleted successfully!")
    );
    return res?.data;
  };

  return {
    list,
    alllist,
    create,
    updateProfile,
    update,
    addSupervisor,
    remove,
    opratorList,
    createOperator,
    updateOperator,
    removeOperator,
    updateSkill
  };
};

export default useSkill;
