import React,{useState} from "react";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack ,Typography} from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import RadioButtonGroup from "../../components/input/radio";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const Add = ({ open, onClose, data, setData, onSubmit, loading,edit,assetsList}) => {


  console.log("downtimeedit data",data)

  // const USER_SCHEMA = [
  //   {
  //     title: t('name'),
  //     placeholder: t('enterName'),
  //     type: SCHEMA_TYPES.INPUT,
  //     name: "name",
  //     validator: VALIDATORS.REQUIRED,
  //   },
  //   {
  //     title: t("asset"),
  //     placeholder: "Select Assests",
  //     type: SCHEMA_TYPES.SELECT,
  //     name: "assets",
  //     multi:true,
  //     validator: VALIDATORS.REQUIRED,
  //     // keyValues:assetsList
  //     keyValues:[{ key: 'All', value: 'All' },...assetsList],

  //   },
  //   {
  //     title:t('type'),
  //     type: SCHEMA_TYPES.RADIO,
  //     name:"type",
  //     // validator: VALIDATORS.REQUIRED,
  //     keyValues:[{"value":'false',"label":t('productive')},{"value":'true',"label":t('nonProductive')}]
  //   },
  //   data.type === 'false'&&({
  //     title: "Threshold (seconds)",
  //     placeholder: "Enter Threshold in number",
  //     type: SCHEMA_TYPES.INPUT,
  //     // inputType:'number',
  //     name: "timeLimit",
  //     validator: VALIDATORS.REQUIRED,
  //   }),
   
   
  // ];

  const USER_SCHEMA = [
    {
      title: t('name'),
      placeholder: t('enterName'),
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: t("asset"),
      placeholder: "Select Assets",
      type: SCHEMA_TYPES.SELECT,
      name: "assets",
      multi: true,
      validator: VALIDATORS.REQUIRED,
      keyValues: [{ key: 'All', value: 'All' }, ...assetsList],
    },
    {
      title: t('type'),
      type: SCHEMA_TYPES.RADIO,
      name: "type",
      keyValues: [
        { value: 'false', label: t('productive') },
        { value: 'true', label: t('nonProductive') },
      ],
    },
    // Conditionally add threshold field when data.type equals 'false'
    ...(data['type'] === 'false'
      ? [
          {
            title: t('thresholdS'),
            placeholder: t("EnterThreshold"),
            type: SCHEMA_TYPES.INPUT,
            name: "timeLimit",
            validator: VALIDATORS.REQUIRED,
          },
        ]
      : []),
  ];
  
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

    const handleSubmit=async ()=>{
          const _errors = Validate(USER_SCHEMA, data);
          setErrors(_errors);
          if (_errors.size) return;
          await onSubmit()
          return;
    }
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit=== true?t('editDowntimeReason'):t('addDowntimeReason')} </h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
         <Typography className="fw-20-500-24">{t('create')}</Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default Add;
