import React, { useState } from "react";
import Modal from "../../components/modal";
import { useSelector } from "react-redux";
import Input from "../../components/input";
import { Grid, Stack, Typography,Box } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { COLORS, SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import { addLeadingZero, formatTime, convertSeconds } from '../../helpers/utils'
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import { useTranslation } from 'react-i18next';


const TestDetail = ({ open, onClose, data, setData, onStart, loading, edit,setModalOpen,testData }) => {
  
  const questions = [
    {'option':'Answer 1','check':true},
    {'option':'Answer 2','check':false},
    {'option':'Answer 3','check':false},
    {'option':'Answer 4','check':false},
  ]

  return (
    <>
    <Box sx={{display: 'flex',gap:2}}>
    <Stack sx={{width:'55%'}}>
      <Stack sx={{ backgroundColor:COLORS.LG_BG_BLUE,borderRadius:2,p:2}}>
        <Typography className="fw-600-16">{testData?.test.test_name ||'sass'}</Typography>
        {/* <Typography>Complete by - 03 Sep 2024</Typography> */}
      </Stack>
      <Stack className="fw-500-14" sx={{backgroundColor:COLORS.LIGHT_GRAY,mt:2,borderRadius:2,p:2}}>
        <Typography className="fw-500-14"><strong>You are going to start the Test, Please read carefully below Instructions :- </strong></Typography>
        <Stack>
          <Typography className="fw-500-14">1.This test is designed to evaluate your understanding and skills - <strong>{testData?.test.skill ||testData?.test.test_type}</strong></Typography>
          <Typography className="fw-500-14">2.Duration: The total time for the test is <strong>{testData?.test.alloted_minutes ||'-'} minutes.</strong></Typography>
          <Typography className="fw-500-14">3.The test will auto-submit once the allocated time ends. <strong>{testData?.test.alloted_minutes ||'-'} minutes</strong></Typography>
          <Typography className="fw-500-14">4.The test consists of multiple-choice questions.</Typography>
          <Typography className="fw-500-14">5.There are <strong>{testData?.test.maximum_questions ||'-'} Questions.</strong></Typography>
          <Typography className="fw-500-14">6.There is no negative marking, so attempt all questions without hesitation.</Typography>
          <Typography className="fw-500-14">7.Each question has only one correct answer. Select the best option from the given choices.</Typography>
          <Typography className="fw-500-14">8.You can navigate between questions and modify your answers before submitting.</Typography>
          <Typography className="fw-500-14">9.The test must be completed in one sitting.</Typography>
          <Typography className="fw-500-14">10.After completing the test, click the Submit button to finalize your responses.</Typography>
        </Stack>
        <Stack sx={{flexDirection:'row',justifyContent:'space-between',mt:2}}>
          <Button color={'gray'}>Back</Button>
          <Button color={'light_green'} onClick={()=>{setModalOpen("start");onStart()}}>Start</Button>
        </Stack>

      </Stack>
    </Stack>
    <Stack sx={{width:'43%'}}>
      <Stack sx={{ backgroundColor:COLORS.LIGHT_GRAY,borderRadius:2,p:2}}>
        <Typography className="fw-600-16">Sample Question :</Typography>
        <Typography sx={{mt:1}} className="fw-600-16">Tick The Correct Options</Typography>
        <Typography sx={{mt:1}} className="fw-500-14">Question 1</Typography>
        <Typography sx={{mt:1,mb:1}} className="fw-500-14">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Typography>
        {questions.map((item,id,index)=>{
          return(
          <FormGroup key={index} sx={{mb:1.4}}>
          <FormControlLabel disabled control={<Checkbox className="fw-500-14" defaultChecked={item.check} />} label={item.option} />
          </FormGroup>
        )})}
      </Stack>
    </Stack>
    </Box>
    </>
  );
};

export default TestDetail;
