import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useLine from "../../store/line";
import Button from "../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete,Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import AddLine from "./AddLine";
import JsonFormatter from "../../components/UI/JsonFormatter";
import { t } from "i18next";


const LinePage = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const divId = location.state
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata:{},
  });
  const { list, create,update, remove } = useLine();

  useEffect(() => {
    fetchCompany();
  }, [page]);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list, _count] = await list(page + 1,divId);
    setUsers(_list.results);
    setTotal(_list.count);
    setLoading(false);
  };

  const handleAdd = async () => { 
    const payload = {...data} 
    setLoading("fetch");
    edit ===true? await update(payload) : await create(payload,divId);
    setData({ name: "",
      metadata:{}});
    setModalOpen(false);
    await fetchCompany();
    setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    await fetchCompany();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: "Line Name",
      render: (rowData) =><Typography color='primary' sx={{cursor:'pointer'}} onClick={()=> navigate('/asset',{state:rowData.id})}>{rowData.name}</Typography>,
    },
    {
      id: 2,
      title: "Division Name",
      render: (rowData) => rowData.division,
    },
    {
      id: 3,
      title: "Details",
      render: (rowData) =>  <JsonFormatter
      icon={<Checklist fontSize="small" />}
      json={rowData?.metadata}
    />,
    },
    {
      id: 5,
      title: "Actions",
      render: (rowData) => (
        <Stack flexDirection={'row'}>
        {/* {!rowData?.is_admin &&(
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleUpdate(rowData?.id,rowData?.is_admin)}
        >
        <AdminPanelSettingsIcon color={rowData.is_admin?"error":"success"} fontSize="small" />
        </IconButton>
        )} */}
       
        <IconButton
        sx={{ height: 25, width: 25 }}
        onClick={()=>{setEdit(true);setModalOpen("add");setData(rowData)}}
      >
      <EditIcon color="primary" fontSize="small" />
      </IconButton>
      <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
          <Delete color="error" fontSize="small" />
        </IconButton>
      </Stack>
      ),
    },
  ];

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
      <Stack sx={{flexDirection:'row',alignItems:'center',gap:1}}>
      <IconButton onClick={()=>navigate(-1)}>
          <ArrowBackIcon/>
        </IconButton>
         <Typography className="fw-20-500-36">
          Lines
          {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </Typography>
        </Stack>
        <Button color={'gray'} onClick={() => setModalOpen("add")} loading={loading}>
          <Typography className="fw-20-500-24">
          Add Line
          </Typography> 
        </Button>
      </div>
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddLine
        open={modalOpen === "add"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
    </div>
  );
};

export default LinePage;
