import React, { useState } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const AddAsset = ({ open, onClose, data, setData, onSubmit, loading, edit }) => {
  const [isEndLine, setIsEndLine] = useState('Yes')
  const [PerformanceCal, setPerformanceCal] = useState('Job/Part based')
  const [cycleTime, setCycleTime] = useState('Yes')

  const USER_SCHEMA = [
    {
      title: "Asset Name",
      placeholder: "Enter Machine Name",
      type: SCHEMA_TYPES.INPUT,
      name: "alias",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Model",
      placeholder: "Enter Machine Model",
      type: SCHEMA_TYPES.INPUT,
      name: "model",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Device ID",
      placeholder: "Enter Machine UUID",
      type: SCHEMA_TYPES.INPUT,
      name: "uuid",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Platform ID",
      placeholder: "Enter Machine Platform ID",
      type: SCHEMA_TYPES.INPUT,
      name: "machineID",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Serial Number",
      placeholder: "Enter Machine serial number",
      type: SCHEMA_TYPES.INPUT,
      name: "snumber",
      // validator: VALIDATORS.REQUIRED,
    },
    // {
    //   title: "Supplier",
    //   placeholder: "Enter supplier",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "supplier",
    //   validator: VALIDATORS.REQUIRED,
    // },
    // {
    //   title: "Purchase Date",
    //   type: SCHEMA_TYPES.DATE,
    //   name: "purchase_date",
    //   validator: VALIDATORS.REQUIRED,
    // },
    // {
    //   title: "Warranty Expiration",
    //   type: SCHEMA_TYPES.DATE,
    //   name: "warranty",
    //   validator: VALIDATORS.REQUIRED,
    // },
    {
      title: "API Endpoint",
      placeholder: "Enter Machine API Endpoint",
      type: SCHEMA_TYPES.INPUT,
      name: "api_endpoint",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Show Parts (per seconds)",
      type: SCHEMA_TYPES.RADIO,
      name: "show_parts",
      // validator: VALIDATORS.REQUIRED,
      keyValues: [{ "value": true, "label": "Yes" }, { "value": false, "label": "No" }],
      // width: '350px',
      pl: 0.2
    },
    {
      title: "Is end of line",
      type: SCHEMA_TYPES.RADIO,
      name: "isEndLine",
      // validator: VALIDATORS.REQUIRED,
      keyValues: [{ "value": true, "label": "Yes" }, { "value": false, "label": "No" }],
      // width: '350px',
      pl: 0.2
    },
    {
      title: "Performance Calculation",
      type: SCHEMA_TYPES.RADIO,
      name: "PerformanceCal",
      // validator: VALIDATORS.REQUIRED,
      keyValues: [{ "value": true, "label": "Job/Part based" }, { "value": false, "label": "Output based" }],
      // width: '350px',
      pl: 0.2
    },
   data.PerformanceCal==='true' &&( {
      title: "Fixed Capacity ",
      type: SCHEMA_TYPES.RADIO,
      name: "fixed_capacity",
      // validator: VALIDATORS.REQUIRED,
      keyValues: [{ "value": true, "label": "Yes" }, { "value": false, "label": "No" }],
      // width: '350px',
      pl: 0.2
    }),
    data.PerformanceCal ==='true' && data.fixed_capacity==='true'&&({
      title: "Ideal Cycle Time (seconds)",
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      name: "cycle_time",
      // validator: VALIDATORS.REQUIRED,
    }),

    data.PerformanceCal==='false'&&({
      title: "Production Output Time (per hour)",
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      name: "productionTime",
      // validator: VALIDATORS.REQUIRED,
    }),

  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit=async ()=>{
        const _errors = Validate(USER_SCHEMA, data);
        setErrors(_errors);
        if (_errors.size) return;
        console.log("---------------data",data)
        await onSubmit()
        return;
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit === true ? "EDIT" : "ADD"} Machine</h3>

      <SchemaExtractor
        data={data}
        errors={errors}
        onChange={handleChange}
        schema={USER_SCHEMA}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
          <Typography className="fw-20-500-24">{t('create')}</Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddAsset;
