import React from "react";
import Modal from "../../../components/modal";
import Button from "../../../components/button";
import crossIcon from "../../../assets/svg/cross-icon.svg";
import { t } from "i18next";
const FailureReasonInputModal = ({
  open,
  onClose,
  failureReasonText='',
  setFailureReasonText=()=>{},
  isResolutionDateRequired=null,
  setIsResolutionDateRequired = () => { },
  handleSubmit=()=>{}
}) => {
  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 py-4"
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      crossIconClasses={`w-2.5vw w-5vw--mob h-2.5vw h-5vw--mob`}
    >
      <div className="failure-submit-success-message">
        <p>
          <strong>{t('addFailureReason')}</strong>
        </p>
        <form action="" method="post" className="failure-reason-form">
          <div className="failureReason-input-text-container">
            <label htmlFor="failureReason">
              <div className="label-text">{t('reasonName')}</div>
              <input
                type="text"
                name="failureReason"
                placeholder={`Write failure reason here`}
                value={failureReasonText}
                onChange={(e) => {
                  console.log(`Updated value: ${e.target.value}`);
                  setFailureReasonText(e.target.value);
                }}
                style={{
                  borderRadius: "0.5vw",
                  border: "1px solid grey",
                  padding: "0.75vw",
                  marginBottom: "1rem",
                  fontSize: "1rem",
                }} // Adjust styling as needed
              />
            </label>
          </div>

          <div className="checkbox-items-container">
            <div className="resolution-date-label" >
              {t('resolutionDateRequired')}?
            </div>
            <div>
              <label className="checkbox-item-container">
                {t('yes')}
                <input
                  type="checkbox"
                  name="isResolutionDateRequiredYes"
                  checked={isResolutionDateRequired === true}
                  onChange={(e) => {
                    console.log(`Yes checkbox updated: ${e.target.checked}`);
                    setIsResolutionDateRequired(
                      e.target.checked
                        ? true
                        : isResolutionDateRequired === false
                        ? null
                        : false
                    );
                  }}
                />
              </label>
            </div>
            <div>
              <label className="checkbox-item-container">
                {t('no')}
                <input
                  type="checkbox"
                  name="isResolutionDateRequiredNo"
                  checked={isResolutionDateRequired === false}
                  onChange={(e) => {
                    console.log(`No checkbox updated: ${e.target.checked}`);
                    setIsResolutionDateRequired(
                      e.target.checked
                        ? false
                        : isResolutionDateRequired === true
                        ? null
                        : true
                    );
                  }}
                />
              </label>
            </div>
          </div>
          <div className="submit-btn-container">
            <Button
              onClick={handleSubmit}
              color="green"
              customStyle={{
                borderRadius: "0.5vw",
                border: "1px solid grey",
                padding: "0.7vw",
                marginBottom: "1rem",
                fontSize: "1rem",
              }}
              disabled={!failureReasonText || isResolutionDateRequired === null}
            >
              {t('submit')}
            </Button>
          </div>
        </form>
      </div>
    </Modal>
  );
};

export default FailureReasonInputModal;
