import React from "react";
import Modal from "../../../components/modal";
import Input from "../../../components/input";
import { Stack } from "@mui/material";
import Button from "../../../components/button";
import SchemaExtractor from "../../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS } from "../../../helpers/constants";
import { Validate } from "../../../helpers/utils";
import crossIcon from "../../../assets/svg/cross-icon.svg";
import { t } from "i18next";
const FailureReasonSuccessModal = ({
  open,
  onClose,
  failureReasonText = "",
  isResolutionDateRequired = null,
  nextStepFn=()=>{},
  setData,
  onSubmit,
  handleAdd2,
  loading,
  tab,
}) => {
  console.log(
    "FailureReasonSuccessModal isResolutionDateRequired",
    isResolutionDateRequired
  );

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 py-4"
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      crossIconClasses={`w-2.5vw w-5vw--mob h-2.5vw h-5vw--mob`}
    >
      <div className="failure-submit-success-message">
        <p>
          <strong>{t('success')}!</strong>
        </p>
        <p>
          Failure Reason <strong>{failureReasonText}</strong> with Resolution
          Date required{" "}
          <strong>{isResolutionDateRequired ? "true" : "false"}</strong> is
          submitted successfully.
        </p>
        <Button
          onClick={nextStepFn}
          color="green"
          customStyle={{
            borderRadius: "0.5vw",
            border: "1px solid grey",
            padding: "0.7vw",
            marginBottom: "1rem",
            marginTop: "1rem",
            fontSize: "1rem",
          }}
        >
          {t('ok')}
        </Button>
      </div>
    </Modal>
  );
};

export default FailureReasonSuccessModal;
