import React, { useEffect, useState ,useRef} from "react";
import Modal from "../../../components/modal";
import Input from "../../../components/input";
import { Stack, Typography } from "@mui/material";
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox'
import Button from "../../../components/button";
import SchemaExtractor from "../../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../../helpers/constants'
import { Validate } from "../../../helpers/utils";
import JsonCreator from "../../../components/UI/JsonCreator";
import qr_scan from '../../../assets/svg/qr_scan.svg'
import crossIcon from '../../../assets/svg/cross-icon.svg'
import warning_amber from '../../../assets/svg/warning_amber.svg'
import { COLORS } from "../../../helpers/constants";
import useDowntime from "../../../store/downtimeMaster"
import { t } from "i18next";

const Downtime = ({ open, onClose, packetData }) => {
  const fetchCalled = useRef(false); 
  const [time, setTime] = useState({ hours: "00", minutes: "00", seconds: "00" });

  const [loading, setLoading] = useState();
  const [errors, setErrors] = useState(new Map());
  const [start, setStart] = useState(false)
  const [data, setData] = useState({})
  const [downtimeReasons, setDowntimeReasons] = useState([])
  const [packetId , setPacketId] = useState('')

  const { matrixList, all_downtime_list,
    unassi_downtime_list,
    reason_list,
    post_reason } = useDowntime();

  // useEffect(() => {
  //   fetchReasons(packetData?.event_packet?.id)

  // }, [packetData?.event_packet?.id])
  useEffect(() => {
    if (packetData?.event_packet?.id && !fetchCalled.current) {
      setPacketId(packetData?.event_packet?.id)
      fetchReasons(packetData.event_packet.id);
      fetchCalled.current = true; // Mark as called to prevent future calls
    }
    if (packetData?.event_packet?.duration) {
      const duration = packetData.event_packet.duration;
      const [hrs, mins, secs] = duration.split(":");
      setTime({ hours: hrs, minutes: mins, seconds: secs });
    }
  }, [packetData]);
  


  const fetchReasons = async (packId) => {
    setDowntimeReasons([])
    const [_list] = await reason_list(packId);
    console.log("reason_list linst", _list)
    if (_list?.reasons?.length > 0) {
      const newUsers = _list?.reasons.map(item => ({
        value: item.reason_name,
        key: item.id
      }));
      setDowntimeReasons(prevUsers => [...prevUsers, ...newUsers]);
    }

  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const USER_SCHEMA = [
    {
      title: t('reason'),
      placeholder: t('selectDowntimeReason'),
      type: SCHEMA_TYPES.SELECT,
      name: "reason",
      // multi:true,
      validator: VALIDATORS.REQUIRED,
      keyValues: downtimeReasons
    },
  ]

  const handleSubmit = async () => {
    const _errors = Validate(USER_SCHEMA, data);
    setErrors(_errors);
    if (_errors.size) return;
    console.log("handleSubmit----------", data.reason)
    await handleAdd()
    return;
  }

  const handleAdd = async () => {
    const req = {
      reason_id: data.reason,
    }
    setLoading("fetch");
    let res = await post_reason(packetId, req);
    await onClose()
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="50vw"
      top="20vh"
      wrapperClasses="px-5"
      hideCloseIcon={crossIcon}
    >
      <Stack sx={{ justifyContent: 'center', alignItems: 'center' }}>
        <img
          src={warning_amber}
          // onClick={onClose}
          style={{ width: "100px", height: "100px" }}
          alt="warning Icon"
        />
        <h1 className="mt-1 fw-24">Downtime Detected !</h1>
      </Stack>
      <Stack sx={{ justifyContent: 'space-between', flexDirection: 'row', alignItems: 'center', mt: 2, px: 10, pb: 2 }}>
  <Typography className="fw-36" sx={{ color: COLORS.LG_RED }}>{time.hours} hrs</Typography>
  <Typography className="fw-36" sx={{ color: COLORS.LG_RED }}>{time.minutes} mins</Typography>
  <Typography className="fw-36" sx={{ color: COLORS.LG_RED }}>{time.seconds} sec</Typography>
</Stack>;
      {/* <Stack sx={{ px: 4 }}>
        <FormGroup sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
          <Typography>Barcode Missing</Typography>
          <FormControlLabel
            control={<Checkbox defaultChecked />}
          />
        </FormGroup>
        <FormGroup sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
          <Typography>Material not available</Typography>
          <FormControlLabel
            control={<Checkbox defaultChecked />}
          />
        </FormGroup>
        <FormGroup sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
          <Typography>Barcode available but material missing or less qty</Typography>
          <FormControlLabel
            control={<Checkbox defaultChecked />}
          />
        </FormGroup>
        <FormGroup sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
          <Typography>Other</Typography>
          <FormControlLabel
            control={<Checkbox defaultChecked />}
          />
        </FormGroup>
      </Stack> */}
      <Stack sx={{ px: 4 }}>
        <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
      </Stack>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={2}
      // mb={2}
      // sx={{px:6}}
      >
        <Button bordered gray >
          Cancel
        </Button>
        <Button color={'light_green'} onClick={handleSubmit}>
          <Typography className="fw-20-500-24">
            Confirm
          </Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default Downtime;
