import React, { useRef, useState, useEffect } from "react";
import { t } from "i18next";
import Button from "../../../components/button"; // Adjust the import path as needed

const SupervisorLoginSuccessModal = ({ onClose, onScan, noStepFn, nextStepFn }) => {
  return (
    <div className="action_modal__container">
      <div
        className="material-planner-layout-footer"
        style={{
          gap: ".5rem",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <p>Thank you for your input.</p>
        <p style={{textAlign:'center'}}>
          Please close this modal and click <strong>Submit</strong> button at the bottom of page
          to issue the materials for this order.
        </p>

        <Button onClick={nextStepFn} color="green" width="8rem" height="2.5rem">
          Close
        </Button>
      </div>
    </div>
  );
};

export default SupervisorLoginSuccessModal;
