import React, { useRef, useState, useEffect } from "react";
import { t } from "i18next";
import Button from "../../../components/buttonn"; // Adjust the import path as needed
import { useScanning } from "../../../store/materialPlanner";

const FailureReasonsModal = ({ onClose, onScan, noStepFn, nextStepFn }) => {
  const [failureReasonsData, setFailureReasonsData] = useState(null);
  const { getFailureReasons } = useScanning();
  const [selectedCheckbox, setSelectedCheckbox] = useState(null);
  const handleCheckboxChange = (key) => {
    setSelectedCheckbox(key);
  };

  useEffect(() => {
    getFailureReasons().then((data) => {
      console.log("getFailureReasons data", data);
      setFailureReasonsData(data);
    });
  }, []);

  return (
    <div className="action_modal__container">
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <form
          style={{
            gap: "1rem",
            display: "flex",
            flexDirection: "column",
            alignItems: "flex-start",
            width: "100%",
          }}
        >
          <p
            style={{
              margin: "0",
              alignSelf: "center",
              fontWeight: "600",
            }}
          >
            Partially Fulfilled Order Detected
          </p>
          <p
            style={{
              margin: "0",
              alignSelf: "center",
              fontSize: "0.85rem",
            }}
          >
            Please select the method to replenish and proceed as per guidelines
          </p>{" "}
          {failureReasonsData &&
            Object.entries(failureReasonsData).map(([key, value]) => (
              <div key={key}>
                {" "}
                <label>
                  {" "}
                  <input
                    type="checkbox"
                    value={key}
                    checked={selectedCheckbox === key}
                    onChange={() => handleCheckboxChange(key)}
                  />{" "}
                  {value.name}{" "}
                </label>{" "}
              </div>
            ))}{" "}
        </form>
        <div
          style={{
            gap: "2.5rem",
            display: "flex",
            flexDirection: "row",
          }}
        >
          <Button
            onClick={noStepFn}
            white={true}
            width="8rem"
            height="2.5rem"
            bordered={true}
            customStyle={{
              border: "1px solid #3E3E3E",
              backgroundColor: "#FFFFFF",
            }}
          >
            {t('cancel')}
          </Button>
          <Button
            onClick={() =>
              nextStepFn(
                selectedCheckbox,
                failureReasonsData[selectedCheckbox].resolution_date_required,
                failureReasonsData[selectedCheckbox].name
              )
            }
            color="green"
            width="8rem"
            height="2.5rem"
            disabled={selectedCheckbox === null}
          >
            {t('submit')}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default FailureReasonsModal;
