import "./style.scss";

import { useNavigate, useSearchParams } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { t } from "i18next";
import Table from "../../../components/tablee";

// components
import Button from "../../../components/buttonn";
import Breadcrumb from "../../../components/breadcrumb";
import { useBOM } from "../../../store/materialPlanner";

function OrderInstruction() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [tableData, setTableData] = useState([]);
  const { fetchBOM, error: orderError, loading: ordersLoading } = useBOM();
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(0);
  const [edit, setEdit] = useState(false);
  const [data, setData] = useState({});

  const handleSubmit = async () => {
    console.log("submit");
    const orderId = "123456";
    navigate(
      `/material-planner-plan/scanning?orderId=${searchParams.get(
        "orderId"
      )}&orderStatus=${searchParams.get(
        "orderStatus"
      )}&orderType=${searchParams.get(
        "orderType"
      )}&orderOrderId=${searchParams.get("orderOrderId")}`
    );
  };

  const COLUMNS = [
    {
      id: 1,
      title: t('materialCode'),
      render: (rowData) => rowData.material_code,
    },
    {
      id: 2,
      title: t('materialName'),
      render: (rowData) => rowData.material_name,
    },
    {
      id: 3,
      title: t('qty'),
      render: (rowData) => rowData.quantity,
    },
  ];
  // fetchBOM(searchParams.get("orderId"));
  useEffect(() => {
    searchParams.get("orderId") &&
      fetchBOM(searchParams.get("orderId")).then((data) =>
        setTableData(data.items)
      );
  }, []);

  return (
    <div className="mx-3 my-2 mp-instruction-layout">
      {/* Header   */}
      <div className="mp-instruction-header">
        <Breadcrumb />

        <p className="instruction-text">
          Please follow the instruction to complete the Material Planning for
          this Order ID
        </p>
      </div>
      {/* Body   */}
      <div className="instruction-layout-body">
        <Table
          edit={edit}
          columns={COLUMNS}
          rows={tableData}
          rowsPerPage={5}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          paginator
          serialize
          total={total}
        />
      </div>
      {/* Footer   */}
      <div className="material-planner-layout-footer">
        <Button
          onClick={() => navigate("/material-planner")}
          white={true}
          width="8rem"
          height="2.5rem"
          bordered={true}
          customStyle={{
            border: "1px solid #3E3E3E",
            backgroundColor: "#FFFFFF",
          }}
        >
          {t('back')}
        </Button>
        <Button
          onClick={handleSubmit}
          color="green"
          width="8rem"
          height="2.5rem"
        >
          {t('plan')}
        </Button>
      </div>
    </div>
  );
}

export default OrderInstruction;
