import React from 'react'
import './openIssues.css'
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { Link } from 'react-router-dom';
import { t } from "i18next";
const parts = [
  { id: 1, name: 'LED 1', code: 'QTYPW112', qty: 1, vendor: 'Sameera Enterprises' },
  { id: 2, name: 'Wire 6', code: 'QTI99012', qty: 1, vendor: 'Mayfair Enterprises' },
  { id: 3, name: 'Wire12', code: 'QTI9911', qty: 1, vendor: 'Axar Industries' },
  { id: 4, name: 'Wire11', code: 'QTI99726', qty: 1, vendor: 'Axar Industries' },
  { id: 5, name: 'Wire9', code: 'QTI90011', qty: 1, vendor: 'Axar Industries' },
  { id: 6, name: 'Wire3', code: 'QTI91423', qty: 1, vendor: 'Axar Industries' },
 
]
const OpenIssues = () => {
  return (
    <div  className="mx-3 my-2">
      <div className='issue-search-bar'>
        <h1>Rework open issues</h1>
            <input type="text" placeholder='Search'/>
        </div>
        <div className='issue-table'>
            <IssueTable/>
        </div>
    </div>
  )
}

export function IssueTable() {
  return (
    <TableContainer  sx={{}}>
      <Table sx={{ borderSpacing: '0 15px',  borderCollapse: 'separate' }} aria-label="parts table">
        <TableHead>
          <TableRow sx={{ backgroundColor: '#E9EFC5',  }}>
            <TableCell sx={{fontSize: '22px', fontWeight: '500' }}>S. No.</TableCell>
            <TableCell sx={{fontSize: '22px' ,fontWeight: '500' }}>QR Code</TableCell>
            <TableCell sx={{fontSize: '22px' , fontWeight: '500' }}>Client</TableCell>
            <TableCell sx={{fontSize: '22px', fontWeight: '500' }}>Date</TableCell>
            <TableCell sx={{fontSize: '22px' ,fontWeight: '500' }}>Tag</TableCell>
            <TableCell sx={{fontSize: '22px' ,fontWeight: '500' }}>Comments</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {parts.map((part) => (
            <TableRow
              key={part.id}
              sx={{
                backgroundColor: "#EEEEEE",
                
              }}
            >
              <TableCell sx={{fontSize: '22px',fontWeight: '500' }}>{part.id}</TableCell>
              <TableCell sx={{fontSize: '22px',fontWeight: '500' }}>{part.name}</TableCell>
              <TableCell sx={{fontSize: '22px',fontWeight: '500' }}>{part.code}</TableCell>
              <TableCell sx={{fontSize: '22px',fontWeight: '500' }}>{part.qty}</TableCell>
              <TableCell sx={{fontSize: '22px',fontWeight: '500' }}>{part.vendor}</TableCell>
              <TableCell sx={{fontSize: '22px',fontWeight: '500' }}>
                <Link
                  to="/"
                  onClick={(e) => {
                    e.preventDefault()
                    console.log(`Reporting issue for part ${part.id}`)
                  }}
                  sx={{ color: '#rgba(21, 53, 219, 1)', textDecoration: 'none' }}
                >
                  View
                </Link>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export default OpenIssues
