import React, { useEffect, useLayoutEffect, useRef } from 'react';
import { Chart, registerables } from 'chart.js';
import 'chartjs-adapter-luxon';
// import 'bootstrap/dist/css/bootstrap.min.css';

Chart.register(...registerables);

const HorizontalStackedBarChart = ({timeLineData}) => {
    // console.log(timeLineData);
    const chartRef = useRef(null);
    const data = {
        labels: [''],
        datasets: timeLineData
    };
    
    // setTimeout(() => {
    //     console.log("timeLineData---",timeLineData)
    // }, 2000);

    const dataDummy = {
        labels: [''],
        datasets: [
            {
                label: "07:42:57 - 07:55:57",
                data: [["07:42", "07:55"]],
                backgroundColor: "#ff683a",
                borderWidth: 1,
            },
            {
                label: "07:55:57 - 10:25:23",
                data: [["07:55", "10:25"]],
                backgroundColor: "#1ea12f",
                borderWidth: 1,
            },
            {
                label: "07:42:57 - 07:42:57",
                data: [["07:42", "07:42"]],
                backgroundColor: "#ffcd3a",
                borderWidth: 1,
            },
            {
                label: "10:25:23 - 12:39:18",
                data: [["10:25", "12:39"]],
                backgroundColor: "#ffcd3a",
                borderWidth: 1,
            },
            {
                label: "12:39:23 - 15:39:18",
                data: [["12:39", "15:39"]],
                backgroundColor: "#159e27",
                borderWidth: 1,
            },
        ],
    };

    useLayoutEffect(() => {
        const ctx = chartRef.current.getContext('2d');
        const config = {
            type: 'bar',
            data,
            options: {
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                    legend: { display: false },
                    tooltip: {
                        callbacks: {
                            label: (context) => {
                                return context.dataset.label;
                            },
                        },
                    },
                },
                indexAxis: 'y',
                scales: {
                    x: {
                        beginAtZero: true,
                        type: 'time',
                        time: {
                            unit: 'minute',
                        },
                        min: '00:00',
                        max: '23:59',
                        ticks: {
                            stepSize: 30,
                            autoSkip: false,
                        },
                    },
                    y: {
                        beginAtZero: false,
                        stacked: true,
                    },
                },
            },
        };

        const myChart = new Chart(ctx, config);

        return () => {
            myChart.destroy();
        };
    }, [timeLineData]);

    return (
        <div style={{ position: 'relative', height: '100px', width: '100%',marginBottom:'15px' }}>
            <canvas ref={chartRef}></canvas>
        </div>
    );
};

export default HorizontalStackedBarChart;
