import { Chip } from "@mui/material";
import React from "react";

const Category = ({ title = "N/A", color }) => {
  return !title ? (
    <p className="c-gray fs-12 ml-1">N/A</p>
  ) : (
    <Chip
      label={title}
      variant="filled"
      sx={{
        backgroundColor: color || "#C1FFB6",
        fontSize: "10px",
        "& .MuiChip-label": {
          fontSize: "13px",
        },
      }}
      size="small"
    />
  );
};

export default Category;
