import React from "react";
import styles from "./capsule.module.scss";

export default function Capsule({ capsuleText }) {
  var capsuleBgColor;
  switch (capsuleText) {
    case "PLAN":
      capsuleBgColor = "#fa7474";
      break;
    case "ISSUING":
      capsuleBgColor = "#ffd6b0";
      break;
    case "RUNNING":
      capsuleBgColor = "#e9efc5";
      break;
    default:
      capsuleBgColor = "#fa7474";
  }

  return (
    <div className={styles.capsule} style={{ backgroundColor: capsuleBgColor }}>
      {capsuleText}
    </div>
  );
}
