import TestPortal from "./pages/testPortal"
import TrainingPage from "./pages/planTraining"
import TestDetail from "./pages/test/testDetail"
import AddReport from "./pages/report"
import Op_ChangePassword from './pages/Profile'
import ChangeLanguage from './pages/ChangeLanguage'
import TestUserPage from "./pages/TestUsers";
import ResultPage from "./pages/ResultPage";
import CardGrid from "./pages/machineMonitoring";
