// import HomePage from "./pages/traceablity/traceablity";
import OpenIssues from "./pages/openIssues/openIssues";
import AddIssue from "./pages/addIssue/AddIssue";
import JobPage from "./pages/Jobs"
import MyJobPage from "./pages/Jobs/MyJobs"
import MaterialScan from "./pages/Jobs/MyJobs/materialScan"
import MachineData from "./pages/Jobs/MyJobs/MachineData"
import Traceablity from './pages/traceablity'
import ProductDetails from "./pages/traceablity/ProductDetails/ProductDetails";

import StoreMaterialMovement from './pages/storeMaterialMovement'
import AccCircuitMaster from './pages/AccCircuit'
import LpcCircuitMaster from './pages/LpcCircuit'
import LpcStageMaster from './pages/LpcStage'
import LocationPage from './pages/Location'
import MaterialMasterPage from './pages/MaterialMaster'
import MaterialPage from './pages/MaterialMapping'
import MachinePage from './pages/machine'
import SubMachinePage from './pages/machineMonitoring/indexChildMachine'
import CompanyPage from './pages/company'
import DowntimePage from './pages/downtime'
import ShiftMaster from './pages/shiftMaster'
import PlantPage from './pages/plant'
import DivisionPage from './pages/division'
import LinePage from "./pages/line";
import AssetPage from "./pages/asset";
import PacketDetailPage from './pages/machineMonitoring/packetDetailPage'
import Notification from './pages/notification'
import DowntimePacket from './pages/packetDowntime'
import TenantsMainPage from './pages/tenants'
import UserTenantPage from "./pages/usersTenant";
import HorizontalStackedBarChart from './pages/machineMonitoring/timeline-bar'

const Routes = () => {
  //   const localMenu = localStorage.getItem('menu')
  //   const menuParse = JSON.parse(localMenu);
  //   const menuSet = new Set(menuParse);
  // console.log("localMenu--------",menuSet)

  const dispatch = useDispatch();
  const [hydrated, setHydrated] = useState(false);
  const state = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const role = state?.user?.data?.role
  const [menuSet, setMenuSet] = useState(new Set());

  useEffect(() => {
    const localMenu = localStorage.getItem('menu');
    if (localMenu) {
      const menuParse = JSON.parse(localMenu);
      setMenuSet(new Set(menuParse)); // Set the menuSet with the value from localStorage
    }
    resetAuth(dispatch, state);
    setHydrated(true);
  }, [dispatch, state]);


  const routes = [
    {
      name: "Material Master",
      path: '/material-master',
      element: <MaterialMasterPage />
    },
    {
      name: "Material Movement",
      path: "/store-material-movement",
      element: <StoreMaterialMovement />
    },
    {
      name: "Pending Items",
      path: '/store-pending-items',
      element: <StorePendingItems />
    },
    {
      name: "Failure Reasons",
      path: "/material-planner/addfailureReasonPage",
      element: <AddfailureReasonPage />
    },
    {
      name: 'Store',
      path: '/material-planner',
      element: <PlannerOverview />
    },
    {
      name: 'test result',
      path: '/test-users',
      element: <TestUserPage />
    },
    {
      name: "performance",
      path: '/report',
      element: <AddReport />
    },
    {
      name: "plan training",
      path: '/plan-training',
      element: <TrainingPage />
    },
    {
      name: "test portal",
      path: '/test-portal',
      element: <TestPortal />
    },
    {
      name: "skill matrix",
      path: '/skill-matrix',
      element: <SkillMatrix />
    },
    {
      name: "test master",
      path: '/test-upload',
      element: <TestUpload />
    },
    {
      name: "skill page",
      path: '/skill-page',
      element: <SkillPage />
    },
    {
      name: "question bank",
      path: '/question-bank',
      element: <QuestionBank />
    },
    {
      name: "groups",
      path: '/group',
      element: <GroupsPage />
    },
    {
      name: "users",
      path: "/users",
      element: <UserMainPage />,
    },
    {
      name: "test portal",
      path: '/test',
      element: <Test />
    },
    {
      name: "test portal",
      path: '/TestDetail',
      element: <TestDetail />
    },
    {
      name: 'test result',
      path: '/result-page',
      element: <ResultPage />
    }
    ,
    { name: 'Machine Monitoring', path: '/machine-monitoring', element: <CardGrid /> },
    { name: 'Machine Monitoring', path: '/child-machine', element: <ChildMachine /> },
    {
      name: 'Machine Monitoring',
      path: '/machines',
      element: <SubMachinePage />
    },
    {
      name: 'Machine Monitoring',
      path: '/packet-detail-page',
      element: <PacketDetailPage />
    },
    {
      name: 'Notification',
      path: '/notification',
      element: <Notification />
    },
    {
      name: 'Tenant Master',
      path: '/tenant-master',
      element: <TenantsMainPage />
    },
    {
      name: 'Tenant Master',
      path: '/tenant-Users',
      element: <UserTenantPage />
    },

    {
      name: 'Asset Master',
      path: '/company-master',
      element: <CompanyPage />
    },
    {
      name: 'Asset Master',
      path: '/plantpage',
      element: <PlantPage />
    },
    {
      name: 'Asset Master',
      path: '/divisionpage',
      element: <DivisionPage />
    },
    {
      name: 'Asset Master',
      path: '/line',
      element: <LinePage />
    },
    {
      name: 'Asset Master',
      path: '/asset',
      element: <AssetPage />
    },
    {
      name: 'Shift Master',
      path: "/shift-master",
      element: <ShiftMaster />,
    },
    {
      name: 'Downtime Master',
      path: "/downtime-master",
      element: <DowntimePage />,
    },
    {
      name: 'Downtime',
      path: "/downtime-packet",
      element: <DowntimePacket />,
    },
    {
      name:'Planning',
      path: "/planning-material",
      element: <PlanningMaterial />
    },
    {
      name:'Planning',
      path: "/planning-material-plan",
      element: <PlanningMaterialOrderInstruction />,
    },
    {
      name:'Operator Portal',
      path: '/machine-scan',
      element: <MachineScanPage />
    },
    {
      name:'Operator Portal',
      path: '/job',
      element: <JobPage />
    },
    {
      name:'Operator Portal',
      path: '/myjob',
      element: <MyJobPage />
    },  
    {
      name:'Operator Portal',
      path: '/job-material-scan',
      element: <MaterialScan />
    },  
    {
      name:'Operator Portal',
      path: '/machine-data',
      element: <MachineData />
    },  
   
    {
      path: '/error',
      element: <Error />
    },

  ];

  const alwaysAccessibleRoutes = [
    { path: "/HorizontalStackedBarChart", element: <HorizontalStackedBarChart /> },
    { path: "/profile", element: <OgProfile /> },
    { path: '/change-password', element: <Op_ChangePassword /> },
    { path: '/change-language', element: <ChangeLanguage /> },
    { path: "/material-planner-plan", element: <OrderInstruction /> },
    { path: "/material-planner-plan/scanning", element: <ScanningPage /> },
   
 //operator portal
    
    {
      path: "/acc-circuit-master",
      element: <AccCircuitMaster />
    },
    {
      path: "/lpc-circuit-master",
      element: <LpcCircuitMaster />
    },
    {
      path: "/lpc-stage-master",
      element: <LpcStageMaster />
    },

    {
      path: '/material-mapping',
      element: <MaterialPage />
    },
    {
      path: '/locaton-master',
      element: <LocationPage />
    },
   
    {
      path: "/traceablity",
      element: <Traceablity />
    },
    {
      path: "/product-details",
      element: <ProductDetails />
    },
    {
      path: "/open-issues",
      element: <OpenIssues />
    },
    {
      path: "/add-issue",
      element: <AddIssue />
    },
    // {
    //   path: "/machine",
    //   element: <MachinePage />
    // },



  ];

  // const modifiedRoutes = useMemo(() => {
  //   return routes.map(route => {
  //     // Check if route's name exists in the static menuSet
  //     if (!menuSet.has(route.name)) {
  //       return { ...route, element: <Error /> };  // Return Error page if name not found in menu
  //     }
  //     return route;
  //   });
  // }, [routes]);

  const modifiedRoutes = routes.map(route => {
    if (!menuSet.has(route.name)) {
      return { ...route, element: <Error /> }; // Return Error page if name not found in menuSet
    }
    return route;
  });

  // useEffect(() => {
  //   resetAuth(dispatch, state);
  //   setHydrated(true);
  // }, []);

  if (!hydrated) {
    return null;
  }
  return (
    <AllRoutes>
      {modifiedRoutes.map(({ path, element }) => (
        <Route element={element} path={path} key={path} />
      ))}
      {alwaysAccessibleRoutes.map(({ path, element }) => (
        <Route element={element} path={path} key={path} />
      ))}
    </AllRoutes>

  );
};

export default Routes;
