import React,{useState} from "react";
import Modal from "../../components/modal";
import { Stack, Typography ,Grid} from "@mui/material";
import Button from "../../components/button";
import { useTranslation } from 'react-i18next';

const AddReport = ({ open, onClose, data, setData, onSubmit, loading ,tab}) => {

  const matList =[
    {
    "itemName":"item 1",
    "plan":1000
  },
    {
    "itemName":"item 2",
    "plan":800
  },
    {
    "itemName":"item 3",
    "plan":1000
  },
]


  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">ADD REPORT</h3>
      <Grid display={'grid'} sx={{ pt: 1,gap:1 }} gridTemplateColumns={{ sm: 'repeat(4, 1fr)', xs: 'repeat(4, 1fr)' }}>        
        <Typography sx={{fontWeight:'bold'}}>S no</Typography>
        <Typography sx={{fontWeight:'bold'}}>Item Name</Typography>
        <Typography sx={{fontWeight:'bold'}}>Plan</Typography>
        <Typography sx={{fontWeight:'bold'}}>Actual</Typography>
     {matList.map((item,index)=>{
      return(
        <>
        <Typography>{index+1}</Typography>
        <Typography sx={{textTransform:'capitalize'}}>{item.itemName}</Typography>
        <Typography>{item.plan}</Typography>
        <input type="text"/>
      </>
      )
     })}
     </Grid>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={onSubmit} loading={loading}>
          Create
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddReport;
