import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import Button from "../../components/button";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Tooltip from '@mui/material/Tooltip';
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import AddQues from './AddQues'
import AddAssign from './AddAssign'
import AddTest from './AddTest'
import useUser from "../../store/users";
import useQuestion from "../../store/questionBank";
import { Delete } from "@mui/icons-material";
import { COLORS } from "../../helpers/constants";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";


const TestUpload = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const planData = location.state
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState("testDetail");
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
   name: "",
   tranning_type:'',
   metadata:{},
   skill:''
  });
  const { question_list,skill_list,test_list,assignExam, createTest,update, linkPlanTestMaster ,addQuestion} = useQuestion();
  const { list:userList,activeUsers} = useUser();
  const [errors, setErrors] = useState(new Map());
  const [selectQuestions,setSelectedQuestions] = useState([])
  const [skills, setSkills] = useState([])
  const [testList, setTestList] = useState([])
  const [testId, setTestId] = useState('')

  const COLUMNSS=[ 
    {
    id: 1,
    title: t('testName'),
    render: (rowData) =>
       <Typography>{rowData.test_name}</Typography>,
    },
    {
    id: 2,
    title: t('testType'),
    render: (rowData) => <Typography>{rowData.test_type}</Typography>,
    },
    {
    id: 3,
    title: t('skill'),
    render: (rowData) => <Typography>{rowData.skill}</Typography>,
    },
    {
    id: 4,
    title: t('skillLevel'),
    render: (rowData) => <Typography>{rowData.skill_level}</Typography>,
    },
    {
    id: 5,
    title: t('maximumQuestions'),
    render: (rowData) => <Typography>{rowData.maximum_questions}</Typography>,
    },
    {
    id: 6,
    title: t('questionsInTest'),
    render: (rowData) => <Typography>{rowData.questions_master.length}</Typography>,
    },
    {
    id: 7,
    title: t('allotedMinutes'),
    render: (rowData) =><Typography>{rowData.alloted_minutes}</Typography> ,
    },
    {
    id: 8,
    title: t('admins'),
    render: (rowData) => 
      <>
    {/* <IconButton
    sx={{ height: 25, width: 25 }}
    onClick={() => handleDelete(rowData.id)}
  >
    <Delete color="error" fontSize="small" />
  </IconButton> */}
  <Stack sx={{flexDirection:'row',gap:1}}>
  <Stack sx={{flexDirection:'column'}}>
   {rowData.admin.map((item,index)=>{
    return<Typography key={index} sx={{textTransform:'capitalize'}}>
      {item.username}
    </Typography>
   })} 
  </Stack>
  <Tooltip title="add admins">
    <IconButton
    sx={{ height: 25, width: 25 }}
    onClick={() => {setTestId(rowData.id);setModalOpen("addAssign");setData({...data,"admins":rowData.admin.map(item => item.id)})}}
  >
    <AddCircleIcon color="primary" fontSize="small" />
  </IconButton>
  </Tooltip>
  </Stack>
  </>
  
    },
]

  useEffect(()=>{
    console.log("planData----",planData)
     if(planData){
        setModalOpen("createTest")
     }else{
      setModalOpen(false)
     }
     fetchSkill()
     fetchTestList()
     fetchUsers()
  },[])

  const fetchTestList = async () => {
    const [res] = await test_list(page + 1);
    console.log("test master-------",res.count)
    setTotal(res.count)
    setTestList(res.results);
  };

  const fetchUsers = async () => {
    const res =  await activeUsers(page + 1)
    console.log("active users--------",res)
    // const newOptr = _list.results.filter(item => !item.is_operator && item.status === true);
    // const newUsers = newOptr.map(item => ({
    //   value: item.username,
    //   key: item.id
    // }));
    setUsers(res);
  };

  const fetchSkill = async () => {
    setLoading("fetch");
    const [list] = await skill_list(page + 1);
    const newUsers = list.results.map(item => ({
      value: item.skill_name,
      key: item.id
    }));
    setSkills(prevUsers => [...prevUsers,...newUsers]);
    setLoading(false);
  };
  
 const UploadTest=async ()=>{
    const payload ={
        "test_name": data.testname,
        // "test_description": "descr",
        "test_type": data.tranning_type,
        "skill": data.skill,
        "skill_level":parseInt(data.level),
        "maximum_questions": parseInt(data.totalQuestion),
        "alloted_minutes": parseInt(data.time)
    }  

    const payload1 ={
        "test_name": data.testname,
        // "test_description": "descr",
        "test_type": data.tranning_type,
        "maximum_questions": parseInt(data.totalQuestion),
        "passing_criteria": data.passing_criteria,
        "alloted_minutes": parseInt(data.time)
    }  
    const res = await createTest(data.tranning_type!=="Skill Test"?payload1:payload)
    // setModalOpen(false)
    if(res.id){
      setTestId(res.id)
      setModalOpen("addQues")
    }
    
    // await fetchTestList()
  }

  const postQuestion =async (finalQuestions)=>{
    const quesIds = finalQuestions.map(question => question.id)
    const payload ={
      questions:quesIds,
    }
    await addQuestion(testId,payload)  
    if(planData){
      await linkPlanTestMaster(planData.id,{'test_master':testId,'training_admin':planData.training_admin,trainees:planData.trainees})
    }
    setData({})
    setModalOpen(false)
    await fetchTestList()
  }
   
const handleAssign =async()=>{
  const payload ={
    admin:data.admins,
  }
  await addQuestion(testId,payload) 
  setModalOpen(false)
  setData({})
  await fetchTestList()
}

  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    // await fetchCompany();
    setLoading(false);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <div className="mx-3 my-2"> 
      <div className="flex items-center justify-between">
      <Stack sx={{flexDirection:'row',alignItems:'center',mb:1}}>
      {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
        <Typography className="fw-20-500-36">
          {t('testMaster')}
          {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </Typography>
      </Stack>
      <Stack sx={{flexDirection:'row',gap:1}}>
        <Button color={'gray'} onClick={() => setModalOpen("createTest")}>
          <Typography className="fw-20-500-24">
          {t('createTest')}
          </Typography>
          </Button>
        {/* <Button bordered onClick={() => setModalOpen("addQues")}>Add test</Button>
        <Button  onClick={() => setModalOpen("addAssign")}>Assign test</Button> */}
        </Stack>
      </div>
        <Table
          columns={COLUMNSS}
          rows={testList}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          paginator
          serialize
          total={total}
          tableBodyColor={COLORS.WHITE}
         />
        <AddTest
         open={modalOpen === "createTest"}
         onClose={() => {setModalOpen(false)}}
         onSubmit={UploadTest}
         skills={skills}
         data={data}
         setData={setData}
        />
        <AddQues
         open={modalOpen === "addQues"}
         onClose={() => {setModalOpen(false)}}
         onSubmit={postQuestion}
        //  question={users}
         selectQuestions={selectQuestions}
         setSelectedQuestions={setSelectedQuestions}
         skills={skills}
         data={data}
         setData={setData}
        />
        <AddAssign
         open={modalOpen === "addAssign"}
         onClose={() => {setModalOpen(false)}}
         onSubmit={handleAssign}
         users={users}
         testList={testList}
         data={data}
         setData={setData}
        />
    </div>
  );
};

export default TestUpload;
