import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useShift = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `settings/shifts/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const plantList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `asset/all-plants/?page=1&page_size=100`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `settings/shifts/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };
  const update = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `settings/shift/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null,"Shift has been updated successfully.")
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `settings/shift/${id}/`,
      headers,
      (res) => resolveResponse(res, null,'Shift has been successfully deleted.')
    );
    return res?.data;
  };

  return {
    list,
    plantList,
    create,
    update,
    remove
  };
};

export default useShift;
