import React, { useState,useRef,useEffect } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import qr_scan from '../../assets/svg/qr_scan.svg'
import { useNavigate } from "react-router-dom";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import useJob from "../../store/operatorPortal/job";
import { toast } from "react-toastify";

const MachineScanPage = ({ open, onClose }) => {
  const navigate = useNavigate()
  const{taskList} = useJob()
  const [errors, setErrors] = useState(new Map());
  const [start, setStart] = useState(true)
  const [ machineCode, setMachineCode] = useState('')

  const validateHash = (value) => {
    const hashPattern = /^[a-f0-9]{32}$/; // Only lowercase a-f and 0-9, exactly 32 characters
    return hashPattern.test(value);
};

  const handleChange = async (e) => {
    const value = e.target.value;
    setMachineCode(value)
    if (validateHash(value)) {
        console.log("Success: Valid hash");
       await checkMachine(value)
    } else {
        console.log("Error: Invalid format");
    }
};


  async function checkMachine(id){
    let [res] = await taskList(id)
    // console.log("e.target.machineCodevalue",res.items.length)
    if(res?.items?.length>0){
      localStorage.setItem("machineId",id)
      toast.success("Machine Task list Successfully fetch")
      navigate('/job',{state:id})
    }else if(res?.items?.length===0){
      toast.warn("Please Plan Task for this Machine")
    }
  }

  return (
    <>
      <Stack sx={{ alignItems: 'center', mt: 4 }}>
        <Typography className="fw-24">
          Please Scan the machine barcode to proceed
        </Typography>
        <img className="mt-2" src={qr_scan} width='160px' height='160px' style={{ objectFit: 'contain' }} />
        <Typography className="fw-24">
          or
        </Typography>
        <Typography className="fw-24">
          Enter Machine Code
        </Typography>
        <Stack>
        <Input 
          focus={true} 
          value={machineCode}
          onChange={(e)=>handleChange(e)}/>
        </Stack>
      </Stack>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mx={4}
        mb={1}
      >
        <Button bordered gray onClick={()=>setMachineCode('')}>
          Clear All
        </Button>
        <Button color={'light_green'} disabled={true}>
          <Typography color={'black'}>
            Confirm
          </Typography>
        </Button>
      </Stack>
    </>

  );
};

export default MachineScanPage;
