import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack ,Typography} from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const AddUser = ({ open, onClose, data, setData, onSubmit, loading ,tab}) => {
  const USER_SCHEMA = [
    {
      title: "User Name",
      placeholder: "Enter User Name",
      type: SCHEMA_TYPES.INPUT,
      name: "username",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Email",
      placeholder: "Enter Email",
      type: SCHEMA_TYPES.INPUT,
      name: "email", 
    },
    {
      title: "Full Name",
      placeholder: "Enter Full Name",
      type: SCHEMA_TYPES.INPUT,
      name: "fullname"
    },
    // {
    //   title: "Employee Id",
    //   placeholder: "Employee Id",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "employee_id"
    // },
  ];

  const operatorSchema =[
    {
      title: "Employee Id",
      placeholder: "Employee Id",
      type: SCHEMA_TYPES.INPUT,
      name: "employee_id",
      validator: VALIDATORS.REQUIRED,
    },
     {
      title: "Full Name",
      placeholder: "Enter Full Name",
      type: SCHEMA_TYPES.INPUT,
      name: "fullname"
    }
  ]

  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleNext = async () => {
      const _errors = Validate(USER_SCHEMA, data);
      setErrors(_errors);
      if (_errors.size) return;
      onSubmit()
      return;
    }

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">ADD {tab}</h3>  
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={tab === "Users"?USER_SCHEMA:operatorSchema}
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
          <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">{t('create')}</Typography>
        </Button>
       
      </Stack>
    </Modal>
  );
};

export default AddUser;
