import React, { useState } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { COLORS } from "../../helpers/constants";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { useTranslation } from 'react-i18next';
import { t } from "i18next";

const Confirm = ({ open, onClose }) => {
  const [errors, setErrors] = useState(new Map());

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <Stack sx={{ justifyContent: 'center', alignItems: 'center', mt: 4 }}>
        <CheckCircleOutlineIcon sx={{ color: COLORS.LIGHT_GREEN, width: 100, height: 100 }} />
        <Typography textAlign={"center"} className="fw-24" sx={{ py: 4 }}>
          You have successfully changed password.<br></br>
          You will be automatically logged off <br></br>
          Please Login again with new password</Typography>
      </Stack>
    </Modal>
  );
};

export default Confirm;
