import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { useTranslation } from 'react-i18next';

const AddUser = ({ open, onClose, data, setData, onSubmit,handleAdd2, loading ,tab}) => {
  const { t, i18n } = useTranslation();
 
  const USER_SCHEMA = [
    {
      title: t('user_name'),
      placeholder: t('enterUserName'),
      type: SCHEMA_TYPES.INPUT,
      name: "username",
      validator: VALIDATORS.NAME,
    },
    {
      title: t('email'),
      placeholder: t('enterEmail'),
      type: SCHEMA_TYPES.INPUT,
      name: "email",
      validator: VALIDATORS.EMAIL,
    },  
    {
      title: t('full_name'),
      placeholder: t('enterFullName'),
      type: SCHEMA_TYPES.INPUT,
      name: "fullname"
    },
   
  ];

  const operatorSchema =[
    {
      title: t('aadhaarNumber'),
      placeholder: t('aadhaarNumber'),
      type: SCHEMA_TYPES.INPUT,
      name: "aadhar_no",
      inputType:"number", 
      validator: VALIDATORS.REQUIRED && VALIDATORS.AADHAR,
    },
    // {
    //   title: "User Name",
    //   placeholder: "Enter User Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "username",
    // },
     {
      title: t('full_name'),
      placeholder:  t('enterFullName'),
      type: SCHEMA_TYPES.INPUT,
      name: "fullname"
    }
  ]

  const form2 =[
    {
      title: t('phoneNumber'),
      placeholder:t('phoneNumber'),
      type: SCHEMA_TYPES.INPUT,
      name: "phone_number",
      inputType:"number", 
      validator: VALIDATORS.PHONE, 
    },
    {
      title: t('employeeID'),
      placeholder: t('employeeID'),
      type: SCHEMA_TYPES.INPUT,
      name: "employee_id",
      // validator: VALIDATORS.REQUIRED, 
    },
    tab === 0&&({
      title: "Aadhar Number",
      placeholder: "Aadhar Number",
      type: SCHEMA_TYPES.INPUT,
      name: "aadhar_number",
      inputType:"number", 
      validator: VALIDATORS.AADHAR, 
    }),
    {
      title: t('employeeType'),
      placeholder: t('employeeType'),
      type: SCHEMA_TYPES.INPUT,
      name: "employee_type",
      // validator: VALIDATORS.REQUIRED, 
    },
    {
      title: t('date_of_joining'),
      placeholder: t('date_of_joining'),
      type: SCHEMA_TYPES.DATE,
      name: "date_of_joining",
      // validator: VALIDATORS.REQUIRED, 
    },
    // {
    //   title: "Supervisor",
    //   placeholder: "supervisor",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "Supervisor",
    //   // validator: VALIDATORS.REQUIRED, 
    // }
  ]

  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
  
  const handleNext = async () => {
    if (step === 0 ) {
      const _errors = Validate(tab ===0?USER_SCHEMA:operatorSchema, data);
      console.log("_errors",_errors)
      setErrors(_errors);
      if (_errors.size) return;
     let res = await onSubmit()
     console.log("step error----->>>",res)
     if(res!==false){
        setStep((prev) => prev + 1);
     }
      return;
    }
    if (step === 1) {
      const _errors = Validate(form2, data);
      setErrors(_errors);
      if (_errors.size) return;
      await handleAdd2()
      setStep(0);
      return;
    } 
  };
  
  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  
  return (
    <Modal
    open={open}
    onClose={onClose}
    // width="min(700px,90vw)"
    // hideCloseIcon={crossIcon}
    // wrapperClasses="px-6"
    width="50vw"
    top="10vh"
    hideCloseIcon={crossIcon}
    wrapperClasses="px-5 py-4"
    crossIconClasses={`w-2.5vw w-5vw--mob h-2.5vw h-5vw--mob`}
    >
      <h3 className="opacity-80">{tab===0?t('add_user'):t('add_operator')}</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={step ===0?tab === 0?USER_SCHEMA:operatorSchema:form2}
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">
          {t('next')}
          </Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddUser;
